import { Column, Hooks, MetaBase } from 'react-table'
import { Checkbox } from '../../checkbox/Checkbox'

export function useCheckboxes(hooks: Hooks) {
  hooks.visibleColumns.push(insertRowCheckboxes)
}

function insertRowCheckboxes(columns: Column[], meta: MetaBase<object>): Column[] {
  if (meta.instance.disableRowSelection || columns[0].id === 'selection') return columns

  return [
    {
      id: 'selection',
      Header: ({ getToggleAllRowsSelectedProps, toggleAllRowsSelected }) => {
        if (!meta.instance.selectAll) return null

        const { title, ...rest } = getToggleAllRowsSelectedProps()

        return <Checkbox aria-label={title} reducedMotion {...rest} onChange={toggleAllRowsSelected} />
      },

      Cell: ({ row }) => {
        const { title, ...rest } = row.getToggleRowSelectedProps()

        return <Checkbox aria-label={title} reducedMotion {...rest} onChange={row.toggleRowSelected} />
      },
      width: 50,
      minWidth: 50,
      maxWidth: 50,
      disableResizing: true,
      loadingState: false,
      sticky: 'left',
    },
    ...columns,
  ] as Column[]
}

useCheckboxes.pluginName = 'useCheckboxes'
