import { ChangeEvent } from 'react'
import { FieldItems, FieldProps } from 'react-awesome-query-builder'
import { Select } from '../../select'

export function QueryBuilderFieldSelect({ items, setField, selectedKey, readonly, placeholder }: FieldProps) {
  const onChange = (e: ChangeEvent<HTMLSelectElement>) => {
    if (e.target.value === undefined) return
    setField(e.target.value)
  }

  const hasValue = selectedKey != null

  const renderOptions = (fields: FieldItems) =>
    Object.keys(fields).map(fieldKey => {
      const field = fields[fieldKey]
      const { items, path, label, disabled } = field

      if (items) {
        return (
          <optgroup disabled={disabled} key={path} label={label}>
            {renderOptions(items)}
          </optgroup>
        )
      } else {
        return (
          <option disabled={disabled} key={path} value={path}>
            {label}
          </option>
        )
      }
    })

  return (
    <Select placeholder={placeholder} onChange={onChange} value={hasValue ? selectedKey : ''} disabled={readonly}>
      {!hasValue && <option value={''} disabled />}
      {renderOptions(items)}
    </Select>
  )
}
