import { useToggleState } from 'react-stately'
import { AriaSwitchProps } from '@react-types/switch'
import { motion } from 'framer-motion'
import { ReactNode, useRef } from 'react'
import { useFocusRing, useSwitch, VisuallyHidden } from 'react-aria'
import { Box, HStack } from '../layout'
import { styled } from '../theme/default'
import { Label } from '../typography'

const SwitchWrapper = styled(motion.svg, {
  $$fill: '$colors$gray3',
  width: 44,
  height: 24,
  transition: '$fast',
  cursor: 'pointer',

  variants: {
    selected: {
      true: {
        $$fill: '$colors$primary',
      },
      false: {},
    },

    disabled: {
      true: {
        $$fill: '$colors$gray5',

        '&, rect, circle': {
          cursor: 'not-allowed',
        },

        circle: {
          fill: '$colors$gray3',
        },
      },
    },
  },
})

const SwitchBackground = styled(motion.rect, {
  width: 44,
  height: 24,
  fill: '$$fill',
  transition: '$fast',
})

const SwitchCircle = styled(motion.rect, {
  fill: '$white',
  size: 20,
})

type SwitchProps = AriaSwitchProps & {
  label?: ReactNode
  selected?: AriaSwitchProps['isSelected']
  disabled?: AriaSwitchProps['isDisabled']
}

const SwitchLabel = styled('label', HStack, {
  variants: {
    disabled: {
      true: {
        cursor: 'not-allowed',
      },
    },
  },
})

export const Switch = ({ label, selected, disabled, ...props }: SwitchProps) => {
  props.children ??= label
  props.isSelected ??= selected
  props.isDisabled ??= disabled
  const state = useToggleState(props)
  const ref = useRef<HTMLInputElement>(null)
  const { inputProps } = useSwitch(props, state, ref)
  const { isFocusVisible, focusProps } = useFocusRing()

  return (
    <SwitchLabel align="center" space="small">
      <VisuallyHidden>
        <input {...inputProps} {...focusProps} ref={ref} />
      </VisuallyHidden>
      <SwitchWrapper aria-hidden="true" selected={state.isSelected} disabled={inputProps.disabled}>
        <SwitchBackground rx={12} />
        <SwitchCircle
          x={state.isSelected ? 22 : 2}
          y={2}
          rx={12}
          layout
          transition={{ type: 'spring', duration: 0.5 }}
        />
        {isFocusVisible && (
          <Box
            as="rect"
            x={1}
            y={1}
            width={42}
            height={22}
            rx={11}
            fill="none"
            strokeWidth={2}
            stroke="currentColor"
            css={{ color: '$focusStroke' }}
          />
        )}
      </SwitchWrapper>
      <Label as="span" css={{ userSelect: 'none' }} disabled={inputProps.disabled}>
        {label}
      </Label>
    </SwitchLabel>
  )
}
