import { ComponentProps, forwardRef } from 'react'
import { styled } from '../theme/default'
import { fontPxToNum } from './utils'

export const HeadingFactory = styled('h1', {
  margin: 0,
  fontFamily: '$sans',

  capsize: {
    metrics: 'Modern Era',
    lineGap: 0,
  },

  variants: {
    size: {
      groupHeadline: {
        fontWeight: '$regular',
        letterSpacing: '0.6px',
        textTransform: 'uppercase',
        capsize: {
          fontSize: fontPxToNum(1),
        },
      },
      subHeadline: {
        fontWeight: '$medium',
        capsize: {
          fontSize: fontPxToNum(4),
        },
      },
      headline: {
        fontWeight: '$medium',
        capsize: {
          fontSize: fontPxToNum(5),
        },
      },
      title3: {
        fontWeight: '$medium',
        capsize: {
          fontSize: fontPxToNum(6),
        },
      },
      title2: {
        fontWeight: '$medium',
        capsize: {
          fontSize: fontPxToNum(7),
        },
      },
      title1: {
        fontWeight: '$medium',
        capsize: {
          fontSize: fontPxToNum(8),
        },
      },
      largeTitle: {
        fontWeight: '$bold',
        capsize: {
          fontSize: fontPxToNum(9),
        },
      },
      extraLargeTitle: {
        fontWeight: '$bold',
        capsize: {
          fontSize: fontPxToNum(10),
        },
      },
    },
  },

  defaultVariants: {
    size: 'headline',
  },
})

const headingLevels = {
  subHeadline: 'h6',
  headline: 'h5',
  title3: 'h4',
  title2: 'h3',
  title1: 'h2',
  largeTitle: 'h1',
  extraLargeTitle: 'h1',
} as const

type HeadingProps = ComponentProps<typeof HeadingFactory>

/**
 * ### Use this for UI Text
 * It has no margins, line-height, leading or trailing space around the text.
 * If you don't want that then use `Paragraph` instead
 *
 * @note Don't override the `fontSize` in css. Instead use the `size` prop
 * ```
 * <Heading size="caption2">My Text</Heading>
 * ```
 *
 * @advanced
 * If you need to adjust the font-size of the text as an escape hatch, then use the `capsize` utility.
 *
 * @author Evan Hennessy
 */
export const Heading = forwardRef<HTMLHeadingElement, HeadingProps>(({ size = 'headline', ...props }, forwardedRef) => {
  return <HeadingFactory as={headingLevels[size as string]} size={size} ref={forwardedRef} {...props} />
})

Heading.displayName = 'Heading'
