import { AriaListBoxOptions } from '@react-aria/listbox'
import { VariantProps } from '@stitches/react'
import { MutableRefObject, useRef } from 'react'
import { mergeProps, useListBox } from 'react-aria'
import { ComboBoxState, SelectState, TreeState, useListState, ListProps } from 'react-stately'
import { FieldLabelAndDescription, FieldLabelAndDescriptionProps, FieldWrapper } from '../form'
import { styled } from '../theme/default'
import { ListAction, ListActionProps } from './ListAction'
import { ListSection } from './ListSection'
import { Option } from './Option'

const ListBoxScrollContainer = styled('div', {
  maxHeight: 'calc($controlHeightLarge * $$items)',
  width: '100%',
  flexGrow: 1,
  gap: '$1',
  overflow: 'auto',
})

const ListBoxList = styled('ul', {
  $$items: 6,

  padding: '0',
  listStyle: 'none',

  variants: {
    appearance: {
      standalone: {
        border: '1px solid $gray4',
        borderRadius: '$1',
      },
      inline: {
        border: 'none',
        borderRadius: '0',
      },
    },
  },

  defaultVariants: {
    appearance: 'standalone',
  },
})

type ListBoxVariants = VariantProps<typeof ListBoxList>
type ListBoxOptions<DataType> = AriaListBoxOptions<DataType>

export type AriaListBoxProps<DataType> = ListBoxOptions<DataType> &
  FieldLabelAndDescriptionProps &
  ListBoxVariants &
  ListProps<DataType> & {
    /** @private */
    listBoxRef?: MutableRefObject<any>
    /** @private */
    state?: ComboBoxState<any> | TreeState<any> | SelectState<any>
    showHighlight?: boolean
    actionItem?: ListActionProps
  }

/**
 * This component is used in ComboxBox, Select, etc.
 * We don't recommend using this component directly.
 */
export function ListBox<DataType extends object = {}>({
  appearance,
  showHighlight,
  actionItem,
  labelProps,
  ...props
}: AriaListBoxProps<DataType>) {
  const state = useListState<DataType>(props)

  const ref = useRef()
  const { listBoxRef = ref, state: controlledState } = props
  const listState = controlledState || state
  const { listBoxProps, labelProps: ariaLabelProps } = useListBox<DataType>(props, listState, listBoxRef)

  const items = [...listState.collection]

  return (
    <FieldWrapper>
      <FieldLabelAndDescription
        labelProps={mergeProps(ariaLabelProps, labelProps, { size: 'regular' })}
        label={props.label}
      />
      <ListBoxList appearance={appearance} css={{ $$items: !!actionItem ? 7 : 6 }}>
        <ListBoxScrollContainer {...listBoxProps} ref={listBoxRef}>
          {items.map(item => {
            switch (item.type) {
              case 'section':
                return <ListSection key={item.key} section={item} state={listState} {...item.props} />
              case 'item':
              default:
                return (
                  <Option showHighlight={showHighlight} key={item.key} item={item} state={listState} {...item.props} />
                )
            }
          })}
        </ListBoxScrollContainer>
        {actionItem && <ListAction key="__action_item" {...actionItem} />}
      </ListBoxList>
    </FieldWrapper>
  )
}
