import { motion, useAnimation } from 'framer-motion'
import { ComponentProps, ReactNode } from 'react'
import warning from 'tiny-warning'
import { Button, ButtonProps } from '../button'
import { Box, Card, Overlay, OverlayProps, Pane, VStack } from '../layout'
import { styled } from '../theme/default'
import { Heading, Paragraph, Text } from '../typography'

const SheetFactory = styled(Pane, {
  backgroundColor: '$background',
  flexDirection: 'column',
  display: 'flex',
  alignSelf: 'center',
  marginBottom: '$1',
  marginInline: '0',
  boxShadow: '$4',
  zIndex: 'calc($$zIndex + 1)',
  width: '100%',
  maxWidth: 'max(600px, 40vw)',
  height: '100%',

  '@tabletPortrait': {
    // maxWidth: `calc(100% - ${sideOffset} * 2)`,
    alignSelf: 'initial',
    marginBottom: '0',
  },

  variants: {
    side: {
      right: {
        boxShadow: '-10px 0 15px rgb(0 0 0 0.75)',
      },
      left: {
        boxShadow: '10px 0 15px rgb(0 0 0 0.75)',
      },
      bottom: {
        boxShadow: '0 10px 15px rgb(0 0 0 0.75)',
        maxWidth: '100vw',
        maxHeight: 'max(400px, 40vh)',
        marginBottom: '0',
      },
    },
  },
})

export type SheetProps = Omit<OverlayProps, 'onHide' | 'onShow'> & {
  onOpen?: () => void
  onClose?: (close?: () => void) => void
  onConfirm?: (close: () => void) => void
  overlayProps?: ComponentProps<typeof Overlay>
  side?: 'left' | 'right' | 'bottom'
  topOffset?: ComponentProps<typeof Card>['css']['marginTop']
  minHeightContent?: ComponentProps<typeof Card>['css']['minHeight']
  contentContainerProps?: ComponentProps<typeof Box>
  css?: ComponentProps<typeof Card>['css']
  hideHeader?: boolean
  hideClose?: boolean
  showConfirm?: boolean
  loading?: boolean
  disabled?: boolean
  cancelLabel?: string
  confirmLabel?: string
  intent?: ButtonProps['intent']
  confirmIcon?: ReactNode
  width?: number
  /**
   * The title for the dialog
   * For accessibility reasons a title is always required even without a header
   */
  title: string
  /** An optional sub title for the sheet header */
  subtitle?: string
}

const SheetMotion: Record<SheetProps['side'], Record<'open' | 'closed', {}>> = {
  right: {
    open: { x: 0, y: 0, opacity: 1 },
    closed: { x: '100%', y: 0, opacity: 0 },
  },
  left: {
    open: { x: 0, y: 0, opacity: 1 },
    closed: { x: '-100%', y: 0, opacity: 0 },
  },
  bottom: {
    open: { x: 0, y: 0, opacity: 1 },
    closed: { x: 0, y: '100%', opacity: 0 },
  },
}

const overlayPosition: Record<SheetProps['side'], {}> = {
  right: {
    justifyContent: 'flex-end',
    alignItems: 'flex-start',
  },
  left: {
    justifyContent: 'flex-start',
    alignItems: 'flex-start',
  },
  bottom: {
    flexDirection: 'column',
    alignItems: 'flex-end',
    justifyContent: 'flex-end',
  },
}

/**
 * A sheet is a modal that opens from one of the sides.
 *
 * @note If a string is provided as children, it will be wrapped in a Paragraph component
 *
 * @example
 * ```tsx
 * const [shown, setShown] = useState(false)
 *
 * <Sheet
    onClose={() => setOpen(false)}
    side={side}
    title="Sheets always require a title"
        subtitle="An optional subtitle can go here"
        isShown={open}
      >
 * ```
 */
export function Sheet({
  onClose,
  onOpen,
  onConfirm,
  isShown,
  shouldCloseOnClick = true,
  shouldCloseOnEscapePress = true,
  overlayProps,
  preventBodyScroll = true,
  width,
  css,
  side = 'right',
  contentContainerProps,
  children,
  hideHeader = false,
  hideClose = false,
  showConfirm = false,
  cancelLabel = 'Close',
  confirmLabel = 'Confirm',
  loading = true,
  title,
  subtitle,
}: SheetProps) {
  const controls = useAnimation()

  warning(
    typeof title !== 'undefined',
    'The `title` prop is required for the Sheet component. This lets us describe the Sheet with an aria region for accessbility.',
  )

  return (
    <Overlay
      onHide={() => {
        controls.start(SheetMotion[side].closed)
        onClose?.()
      }}
      onShow={() => {
        controls.start(SheetMotion[side].open)
        onOpen?.()
      }}
      shouldCloseOnClick={shouldCloseOnClick}
      shouldCloseOnEscapePress={shouldCloseOnEscapePress}
      isShown={isShown}
      preventBodyScroll={preventBodyScroll}
      containerCSS={{
        display: 'flex',
        ...overlayPosition[side],
      }}
      {...overlayProps}
    >
      {close => (
        <SheetFactory
          as={motion.div}
          initial={SheetMotion[side].closed}
          animate={controls}
          transition={{
            type: 'tween',
            ease: 'easeInOut',
            duration: 0.35,
          }}
          side={side}
          role="dialog"
          style={{ width }}
          css={css as {}}
        >
          {!hideHeader || !hideClose || showConfirm ? (
            <Pane
              css={{
                padding: '$4',
                flexShrink: 0,
                borderBottom: '1px solid $colors$muted',
                display: 'flex',
                alignItems: 'center',
              }}
            >
              {!hideHeader && (
                <VStack css={{ flex: 1 }} space="small">
                  <Heading size="subHeadline">{title}</Heading>
                  {!!subtitle && <Text size="caption2">{subtitle}</Text>}
                </VStack>
              )}
              {!hideClose && (
                <Button onClick={() => onClose?.(close)} appearance="minimal">
                  {cancelLabel}
                </Button>
              )}
              {showConfirm && (
                <Button loading={loading} onClick={() => onConfirm?.(close)} appearance="minimal">
                  {confirmLabel}
                </Button>
              )}
            </Pane>
          ) : null}
          <Box
            {...contentContainerProps}
            css={{ padding: '$5', overflowY: 'scroll', ...(contentContainerProps?.css as {}) }}
          >
            {typeof children === 'function' ? (
              children(close)
            ) : typeof children === 'string' ? (
              <Paragraph>{children}</Paragraph>
            ) : (
              children
            )}
          </Box>
        </SheetFactory>
      )}
    </Overlay>
  )
}
