import { Fragment, ReactNode, useCallback, useState } from 'react'
import { Dialog } from '../dialog/Dialog'
import { ConfirmContext } from './ConfirmContext'
import { ConfirmDialogOptions } from './types'

const DEFAULT_OPTIONS: ConfirmDialogOptions = {
  title: null,
  confirmLabel: 'Ok',
  cancelLabel: 'Cancel',
  dialogProps: {},
  confirmButtonProps: {},
  cancelButtonProps: {},
}

const buildOptions = (defaultOptions: Partial<ConfirmDialogOptions>, options: Partial<ConfirmDialogOptions>) => {
  const dialogProps = {
    ...(defaultOptions.dialogProps || {}),
    ...(options.dialogProps || {}),
  }
  const confirmationButtonProps = {
    ...(defaultOptions.confirmButtonProps || {}),
    ...(options.cancelButtonProps || {}),
  }
  const cancellationButtonProps = {
    ...(defaultOptions.confirmButtonProps || {}),
    ...(options.cancelButtonProps || {}),
  }

  return {
    ...DEFAULT_OPTIONS,
    ...defaultOptions,
    ...options,
    children: options?.children || options?.message,
    dialogProps,
    confirmationButtonProps,
    cancellationButtonProps,
  }
}

export function ConfirmProvider({
  children,
  defaultOptions = {},
}: {
  children: ReactNode
  defaultOptions?: Partial<ConfirmDialogOptions>
}) {
  const [options, setOptions] = useState<ConfirmDialogOptions>({ ...DEFAULT_OPTIONS, ...defaultOptions })
  const [resolveReject, setResolveReject] = useState([])
  const [resolve, reject] = resolveReject

  const confirm = useCallback((options = {}) => {
    return new Promise<void>((resolve, reject) => {
      setOptions(buildOptions(defaultOptions, options))
      setResolveReject([resolve, reject])
    })
  }, [])

  const handleClose = useCallback(() => {
    setResolveReject([])
  }, [])

  const handleCancel = useCallback(() => {
    reject?.()
    handleClose()
  }, [reject, handleClose])

  const handleConfirm = useCallback(() => {
    resolve?.()
    handleClose()
  }, [resolve, handleClose])

  return (
    <Fragment>
      <ConfirmContext.Provider value={confirm}>{children}</ConfirmContext.Provider>
      {resolveReject.length === 2 && (
        <Dialog
          width="350px"
          isShown
          {...options.dialogProps}
          {...options}
          onCancel={handleCancel}
          onConfirm={handleConfirm}
        />
      )}
    </Fragment>
  )
}
