import { merge } from 'lodash-es'
import { ComponentProps, useMemo } from 'react'
import { styled, theme } from '../theme/default'

const IconFactory = styled('svg', {
  fill: 'currentcolor',
  strokeColor: 'currentColor',

  variants: {
    multicolored: { false: { path: { fill: 'currentColor' } } },
  },
})

export type IconProps = Omit<ComponentProps<typeof IconFactory>, 'multicolored'> & {
  /** The size of the icon */
  size?: string

  /** Whether to use the multicolored versio
   * You can also pass custom colors (including theme tokens) as an array
   */
  multicolored?: boolean | (keyof typeof theme['colors'] | (string & {}))[]
}

const DEFAULT_COLORS = ['$gray4', '$gray3', '$gray2']

export function Icon({ name, children, multicolored, css: passthroughCSS, size = '$5', ...props }: IconProps) {
  const defaultColors = props['data-default-colors']
    ? props['data-default-colors'].split(',').map((color, i) => (!color ? DEFAULT_COLORS[i] : color))
    : DEFAULT_COLORS

  const iconColors: {} = useMemo(() => {
    let colorArray: string[]
    if (multicolored === true) {
      colorArray = defaultColors
    } else if (Array.isArray(multicolored)) {
      colorArray = merge(defaultColors, multicolored)
    } else {
      colorArray = null
    }

    if (Array.isArray(colorArray)) {
      return colorArray.reduce(
        (obj, color, index) => ({
          ...obj,
          [`--icon-color-${index + 1}`]: color.includes('$') ? '$colors' + color : color,
        }),
        {},
      )
    }
  }, [multicolored])

  return (
    <IconFactory data-icon={name} css={{ width: size, ...iconColors, ...(passthroughCSS as {}) }} {...props}>
      {name ? <title>{name}</title> : null}
      {children}
    </IconFactory>
  )
}
