import { ComponentProps, ReactChild, ReactNode } from 'react'
import { Button } from '../button'
import {
  AlertCircleBoldIcon,
  AlertTriangleBoldIcon,
  CheckCircleBoldIcon,
  CloseIcon,
  InformationCircleBoldIcon,
} from '../icon'
import { Box, Pane } from '../layout'
import { Intent, styled } from '../theme/default'
import { Text } from '../typography'

const AlertFactory = styled(Pane, {
  $$intentColor: '$colors$info',
  backgroundColor: '$background',
  border: '1px solid $gray4',
  overflow: 'hidden',
  position: 'relative',
  padding: '$2 $2 $2 $4',

  variants: {
    /**
     * Sets an appearance for the alert
     * @since 1.0
     * @author Evan Hennessy
     * @default 'card'
     */
    appearance: {
      minimal: {
        border: 'none',
        backgroundColor: 'transparent',
      },
      toast: {
        backdropFilter: 'blur(10px) saturate(180%)',
        backgroundColor: 'hsl($hsl_background / 0.7)',
        backfaceVisibility: 'hidden',
        transform: 'translate3d(0, 0, 0)',
        border: '1px solid hsl($colors$gray3 / 0.8)',

        '&::before': {
          content: '""',
          width: '$1',
          height: 'calc(100% - $2)',
          position: 'absolute',
          top: '$1',
          left: '$1',
          backgroundColor: '$$intentColor',
          borderRadius: '$5',
        },
      },
      card: {
        borderRadius: '$1',

        '&::before': {
          content: '""',
          width: '$1',
          height: '100%',
          position: 'absolute',
          top: 0,
          left: 0,
          backgroundColor: '$$intentColor',
        },
      },
    },

    /**
     * Sets the intent of the alert.
     * This will change the color and default Icon for the alert
     * @since 1.0
     * @author Evan Hennessy
     * @default 'info'
     */
    intent: {
      info: {
        $$intentColor: '$colors$info',
      },
      success: {
        $$intentColor: '$colors$success',
      },
      danger: {
        $$intentColor: '$colors$danger',
      },
      warning: {
        $$intentColor: '$colors$warning',
      },
    },
  },

  defaultVariants: {
    appearance: 'card',
    intent: 'info',
  },
})

type AlertBaseProps = ComponentProps<typeof AlertFactory> & {
  /**
   * By default the alert icon is set based on intent
   * You can optionally override this by passing in a custom icon
   * @default 'default'
   * @since 1.0
   * @author Evan Hennessy
   */
  icon?: ReactChild | 'default' | 'none'

  /**
   * Title is a required prop
   * @since 1.0
   * @author Evan Hennessy
   */
  title: string
}

type AlertRemoveableProps =
  | {
      isRemoveable?: false
      onRemove?: never
    }
  | {
      /**
       * Sets whether the alert can be removed
       * When true the alert is presented with a close button
       *
       * @note If an alert is removeable you must also set the `onRemove` handler
       * @since 1.0
       * @author Evan Hennessy
       * @default false
       */
      isRemoveable: true

      /**
       * Called when the remove button is pressed
       */
      onRemove: () => void
    }

type AlertProps = AlertBaseProps & AlertRemoveableProps

const ICONS: Record<Intent, ReactNode> = {
  info: InformationCircleBoldIcon,
  warning: AlertTriangleBoldIcon,
  success: CheckCircleBoldIcon,
  danger: AlertCircleBoldIcon,
}

/**
 * An alert is a UI element that is used to bring attention to the user
 *
 * It is composed by `Pane` and leaves all of `Pane`'s options available
 *
 * @example
 * ```
 * <Alert title="My title">Add a description here</Alert>
 * ```
 */
export function Alert({ icon = 'default', children, title, isRemoveable = false, onRemove, ...props }: AlertProps) {
  const { intent = 'info' } = props

  const Icon = (() => {
    if (icon === 'none') return null
    if (icon === 'default') return ICONS[intent as Intent]
    if (typeof icon === 'function') return icon
    return null
  })()

  return (
    <AlertFactory role="alert" {...props}>
      <Box css={{ display: 'flex', width: '100%', alignItems: children ? 'flex-start' : 'center' }}>
        {typeof Icon === 'function' && <Icon css={{ color: `$${intent}`, marginRight: '$2' }} size="$3" />}

        <Box>
          <Text css={{ fontWeight: '$medium' }} size="body">
            {title}
          </Text>
          {children && (
            <Box css={{ display: 'flex', paddingTop: '$1' }}>
              {typeof children === 'string' ? (
                <Text size="caption1" css={{ color: '$gray2' }}>
                  {children}
                </Text>
              ) : (
                children
              )}
            </Box>
          )}
        </Box>

        {isRemoveable && (
          <Box css={{ marginLeft: 'auto', paddingLeft: '$10' }}>
            <Button
              appearance="minimal"
              icon={CloseIcon}
              css={{ color: '$label', path: { strokeWidth: '3px' } }}
              onClick={onRemove}
              size="small"
            />
          </Box>
        )}
      </Box>
    </AlertFactory>
  )
}
