import type * as Polymorphic from '@radix-ui/react-polymorphic'
import { ComponentProps, forwardRef, useRef } from 'react'
import { useLink } from 'react-aria'
import { mergeRefs } from 'react-laag'
import type { Overwrite } from 'utility-types'
import { darkTheme } from '../theme/dark'
import { styled } from '../theme/default'
import { TextFactory } from './Text'

/**
 * Links are used for internal and external anchors.
 *
 * The Link component is also export as `Anchor` to skirt collisions with React Router.
 * @since 1.0
 * @author Evan Hennessy
 */
const LinkFactory = styled(TextFactory, {
  all: 'unset',
  textDecoration: 'none',
  cursor: 'pointer',
  color: '$primary',
  display: 'inline',
  transition: '$fast',

  '&:hover, &:active': {
    filter: 'brightness(80%) contrast(110%)',
  },

  [`.${darkTheme} &`]: {
    '&:hover, &:active': {
      filter: 'brightness(110%) contrast(110%)',
    },
  },

  '&:focus': {
    outline: 'none',
    backgroundColor: '$gray5',
  },

  variants: {
    /**
     * Adds an underline to the link
     * @default false
     * @type boolean
     */
    underline: {
      true: {
        textDecoration: 'underline',
        textDecorationThickness: '1.5px',
        textUnderlineOffset: '2px',
      },
    },

    /** @private */
    isPressed: {
      true: {
        backgroundColor: '$pressedColor',
      },
    },
  },
})

type AriaLinkProps = Parameters<typeof useLink>[0]

type LinkProps = Omit<Overwrite<ComponentProps<typeof LinkFactory>, AriaLinkProps>, 'isDisabled' | 'elementType'> & {
  /** Whether the link is disabled. */
  disabled?: AriaLinkProps['isDisabled']
}

type LinkComponent = Polymorphic.ForwardRefComponent<'a', LinkProps>

export const Link = forwardRef<HTMLAnchorElement, LinkProps>((props, forwardedRef) => {
  const ref = useRef()
  const { linkProps, isPressed } = useLink({ ...props, isDisabled: props.disabled }, ref)

  return <LinkFactory as="a" isPressed={isPressed} {...props} {...linkProps} ref={mergeRefs(ref, forwardedRef)} />
}) as LinkComponent

Link.displayName = 'Link'

export { Link as Anchor }
