import { AriaComboBoxOptions } from '@react-aria/combobox'
import { ComboBoxState } from '@react-stately/combobox'
import { pick } from 'lodash-es'
import { useRef } from 'react'
import { mergeProps, OverlayContainer, useComboBox, useFilter, useOverlayPosition } from 'react-aria'
import { useComboBoxState } from 'react-stately'
import {
  FieldErrorAndHelpText,
  FieldLabelAndDescription,
  FieldLabelAndDescriptionProps,
  FieldWrapper,
} from '../form/FieldElements'
import { SearchIcon } from '../icon'
import { Box } from '../layout'
import { ListBox } from '../listbox'
import { ListActionProps } from '../listbox/ListAction'
import { AriaPopoverElement } from '../popover/AriaPopover'
import { TextInput, TextInputProps } from '../textinput'

type ComboBoxProps<DataType> = Omit<
  AriaComboBoxOptions<DataType>,
  'inputRef' | 'buttonRef' | 'listBoxRef' | 'popoverRef' | 'isDisabled' | 'isReadOnly' | 'isRequired'
> &
  Partial<ComboBoxState<DataType>> &
  FieldLabelAndDescriptionProps &
  Pick<TextInputProps, 'iconBefore' | 'loading' | 'css' | 'size'> & {
    disabled?: AriaComboBoxOptions<DataType>['isDisabled']
    required?: AriaComboBoxOptions<DataType>['isRequired']
    readonly?: AriaComboBoxOptions<DataType>['isReadOnly']
    /** Whether to highlight parts of matched text */
    showHighlight?: boolean
    actionItem?: ListActionProps
  }
export function ComboBox<DataType extends object>({
  showHighlight = true,
  loading,
  actionItem,
  labelProps,
  css,
  ...props
}: ComboBoxProps<DataType>) {
  const { contains } = useFilter({ sensitivity: 'base' })
  const state = useComboBoxState<DataType>({
    menuTrigger: 'focus',
    shouldCloseOnBlur: true,
    ...props,
    isDisabled: props.disabled,
    isReadOnly: props.readonly,
    isRequired: props.required,
    defaultFilter: contains,
    allowsEmptyCollection: !!actionItem,
  })

  const buttonRef = useRef<HTMLButtonElement>(null)
  const inputRef = useRef<HTMLInputElement>(null)
  const listBoxRef = useRef<HTMLDivElement>(null)
  const popoverRef = useRef<HTMLDivElement>(null)

  const {
    inputProps,
    listBoxProps,
    labelProps: ariaLabelProps,
  } = useComboBox<DataType>(
    {
      menuTrigger: 'focus',
      shouldFocusWrap: true,
      ...props,
      inputRef,
      buttonRef,
      listBoxRef,
      popoverRef,
      isDisabled: props.disabled,
      isReadOnly: props.readonly,
      isRequired: props.required,
    },
    state,
  )

  const { overlayProps } = useOverlayPosition({
    targetRef: inputRef,
    overlayRef: popoverRef,
    placement: 'bottom',
    offset: 5,
    isOpen: state.isOpen,
  })

  delete overlayProps.style.zIndex

  return (
    <FieldWrapper css={css}>
      <FieldLabelAndDescription
        label={props.label}
        description={props.description}
        labelProps={mergeProps(ariaLabelProps, labelProps, { size: 'regular' })}
      />
      <Box css={{ display: 'inline-block', position: 'relative', width: '100%' }}>
        <TextInput
          loading={loading}
          iconBefore={SearchIcon}
          placeholder={props.placeholder}
          size={props.size}
          {...(inputProps as any)}
          onBlur={state.close}
          onFocus={state.open}
          onChange={state.setInputValue}
          ref={inputRef}
        />
        {state.isOpen && (
          <OverlayContainer>
            <AriaPopoverElement
              {...overlayProps}
              ref={popoverRef}
              shouldCloseOnBlur
              isDismissable
              isOpen={state.isOpen}
              onClose={state.close}
              css={pick(css ?? {}, ['width', 'minWidth', 'maxWidth'])}
            >
              <ListBox<DataType>
                {...listBoxProps}
                showHighlight={showHighlight}
                appearance="inline"
                listBoxRef={listBoxRef}
                state={state}
                actionItem={actionItem}
                children={null}
              />
            </AriaPopoverElement>
          </OverlayContainer>
        )}
      </Box>
      <FieldErrorAndHelpText errorMessage={props.errorMessage} />
    </FieldWrapper>
  )
}
