import { useMachine } from '@xstate/react'
import { AnimatePresence, AnimateSharedLayout, motion } from 'framer-motion'
import { noop } from 'lodash-es'
import { ComponentProps, useEffect, useMemo } from 'react'
import { DropzoneOptions, useDropzone } from 'react-dropzone'
import { FormattedMessage, useIntl } from 'react-intl'
import type { State } from 'xstate'
import { Button } from '../button'
import { AddSmallIcon, CloseIcon, RemoveSmallIcon, UploadIcon } from '../icon'
import { HStack, VStack } from '../layout'
import { styled } from '../theme/default'
import { Paragraph, Text } from '../typography'
import { formatBytes } from '../utils/formatBytes'
import { FileUploadContext, FileUploadEvent, fileUploadMachine, HoverStatus } from './fileUploadMachine'

export const FileUploadRoot = styled(motion.div, {
  border: '1.5px dashed $gray2',
  background: '$gray6',
  padding: '$6',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  borderRadius: '$1',
  transition: '$normal',
  userSelect: 'none',
  cursor: 'pointer',
  height: '275px',

  '&[disabled]': {
    cursor: 'not-allowed',
    color: '$gray2',
  },

  variants: {
    hasFiles: {
      true: {
        borderRadius: '$2',
        height: '90px',
        borderStyle: 'solid',
      },
    },
    dragState: {
      ACTIVE: {
        borderColor: '$gray1',
        backgroundColor: '$gray5',
      },
      ACCEPT: {
        borderColor: '$gray1',
        backgroundColor: '$gray5',
      },
      REJECT: {
        color: '$danger',
        borderColor: '$danger',
      },
    },
  },
})

type FileUploadProps = Omit<ComponentProps<typeof FileUploadRoot>, 'onChange'> &
  Pick<DropzoneOptions, 'maxSize' | 'noDrag' | 'accept' | 'disabled' | 'onDrop'> & {
    /** Returns an array of files */
    onChange?: (files: File[]) => void
    /** Set some files on component load */
    initialFiles?: File[]
    iconSize?: string
  }

const icons: Record<HoverStatus, (props: any) => JSX.Element> = {
  [HoverStatus.IDLE]: UploadIcon,
  [HoverStatus.ACCEPT]: AddSmallIcon,
  [HoverStatus.ACTIVE]: AddSmallIcon,
  [HoverStatus.REJECT]: RemoveSmallIcon,
}

export function FileUpload({
  maxSize = 52_428_800,
  noDrag = false,
  disabled,
  accept,
  iconSize = 'clamp(20px, 75%, 100px)',
  initialFiles = [],
  onDrop = noop,
  onChange = noop,
}: FileUploadProps) {
  const { formatMessage } = useIntl()
  const sizeLimit = useMemo(() => formatBytes(maxSize, 1), [maxSize])
  const acceptType = useMemo(() => {
    if (Array.isArray(accept)) return
    switch (true) {
      case /image/gi.test(accept):
        return 'image'
      case /pdf/gi.test(accept):
        return 'pdf'
      case /csv/gi.test(accept):
        return 'csv'
      default:
        return undefined
    }
  }, [accept])

  const [state, send] = useMachine(fileUploadMachine, {
    context: { files: initialFiles },
  })

  const { context } = state

  const {
    getRootProps,
    getInputProps,
    inputRef,
    isDragActive,
    isDragAccept,
    isDragReject,
    fileRejections,
    isFileDialogActive,
  } = useDropzone({
    onFileDialogCancel: () => {
      send({ type: 'CANCEL_DIALOG' })
    },
    maxSize,
    noDrag,
    multiple: false,
    preventDropOnDocument: true,
    onDropAccepted: acceptedFiles => {
      onChange(acceptedFiles)
      send({ type: 'ADD_FILE', files: acceptedFiles })
    },
    onDropRejected: rejections => {
      send({
        type: 'REJECT',
        message: formatMessage(
          {
            id: `fileupload.error.${rejections?.[0]?.errors?.[0].code}`,
            defaultMessage: 'Invalid file',
          },
          { sizeLimit, acceptType },
        ),
      })
    },
    onDragLeave: () => send('RESET'),
    onDragOver: () => {
      switch (true) {
        case isDragAccept:
          send('HOVER', { status: HoverStatus.ACCEPT })
          break
        case isDragReject:
          send('HOVER', {
            status: HoverStatus.REJECT,
            message: formatMessage(
              {
                id: `fileupload.error.${fileRejections?.[0]?.errors?.[0].code}`,
                defaultMessage: 'Invalid file',
              },
              { sizeLimit, acceptType },
            ),
          })
          break
        case isDragActive:
          send('HOVER', { status: HoverStatus.ACTIVE })
          break
      }
    },
    onDrop,
    disabled,
    accept,
  })

  const Icon = icons[context.hoverStatus]

  useEffect(() => {
    if (isFileDialogActive) send({ type: 'OPEN_DIALOG' })
  }, [isFileDialogActive])

  return (
    <AnimateSharedLayout type="crossfade">
      <FileUploadRoot
        {...(getRootProps({ dragState: context.hoverStatus, disabled, hasFiles: context.files.length > 0 }) as {})}
      >
        <input {...getInputProps()} />
        <AnimatePresence presenceAffectsLayout exitBeforeEnter>
          {context.files.length > 0 ? (
            context.files.map(file => (
              <HStack css={{ width: '100%', justifyContent: 'space-between' }} key={file.name + file.size}>
                <VStack
                  space="small"
                  as={motion.div as any}
                  initial={{ opacity: 0, y: '50%' }}
                  animate={{ opacity: 1, y: 0 }}
                >
                  <Paragraph
                    css={{
                      fontWeight: '$medium',
                      display: '-webkit-box',
                      WebkitLineClamp: '1',
                      wordBreak: 'break-word',
                      overflow: 'hidden',
                      textOverflow: 'ellipsis',
                      WebkitBoxOrient: 'vertical',
                    }}
                  >
                    {file.name}
                  </Paragraph>
                  <Text size="caption1">{formatBytes(file.size)}</Text>
                </VStack>
                <Button
                  onClick={() => {
                    const fileName = file.name
                    const dataTransfer = new DataTransfer()
                    const files = Array.from(inputRef.current.files)

                    for (const file of files) {
                      file.name !== fileName && dataTransfer.items.add(file)
                    }

                    inputRef.current.files = dataTransfer.files

                    const newFiles = Array.from(dataTransfer.files)
                    onChange(newFiles)

                    send({ type: 'REMOVE_FILE', files: newFiles })
                    __DEV__ && console.log({ contextFiles: context.files, newFiles })
                  }}
                  icon={CloseIcon}
                  appearance="minimal"
                />
              </HStack>
            ))
          ) : (
            <VStack as={motion.div} align="center" justify="center">
              <Icon
                as={motion.svg}
                layout
                layoutId="icon"
                initial={false}
                animate={{ opacity: 1 }}
                exit={{ height: 0, opacity: 0 }}
                size={iconSize}
              />
              <VStack
                align="center"
                as={motion.div}
                layout
                layoutId="text"
                animate={state.matches('idle') || context.message ? { opacity: 1, y: 0 } : { opacity: 0, y: '-100%' }}
                exit={{ opacity: 0, y: '-100%' }}
              >
                <FileUploadDescription state={state} maxSize={maxSize} noDrag={noDrag} />
              </VStack>
            </VStack>
          )}
        </AnimatePresence>
      </FileUploadRoot>
    </AnimateSharedLayout>
  )
}

type FileUploadDescriptionProps = {
  state: State<FileUploadContext, FileUploadEvent>
  maxSize: FileUploadProps['maxSize']
  noDrag: FileUploadProps['noDrag']
}
function FileUploadDescription({ state, maxSize, noDrag }: FileUploadDescriptionProps) {
  const sizeLimit = useMemo(() => formatBytes(maxSize, 1), [maxSize])

  switch (state.value) {
    case 'rejected':
      return (
        <>
          <Text>Invalid file</Text>
          <Text size="caption2">{state.context.message}</Text>
        </>
      )
    case 'idle':
      return (
        <>
          <Text>
            <FormattedMessage id="fileupload.idle" values={{ canDrag: !noDrag }} />
          </Text>
          {maxSize !== Infinity && (
            <Text size="caption2">
              <FormattedMessage id="fileupload.sizeLimit" values={{ sizeLimit }} />
            </Text>
          )}
        </>
      )
    default:
      return null
  }
}
