import { ChangeEvent } from 'react'
import { SelectWidgetProps } from 'react-awesome-query-builder'
import { Select } from '../../select'
import { mapListValues } from '../utils'

export function QueryBuilderValueSelect({ listValues, value, placeholder, setValue, readonly }: SelectWidgetProps) {
  const renderOptions = () =>
    mapListValues(listValues, ({ title, value }) => {
      return (
        <option key={value} value={value}>
          {title}
        </option>
      )
    })

  const onChange = (e: ChangeEvent<HTMLSelectElement>) => setValue(e.target.value)

  const hasValue = value != null
  return (
    <Select
      placeholder={placeholder}
      onChange={onChange}
      value={hasValue ? value : listValues?.[0]?.value}
      disabled={readonly}
    >
      {renderOptions()}
    </Select>
  )
}
