import { ComponentProps, useRef } from 'react'
import { useMenu } from 'react-aria'
import { useTreeState } from 'react-stately'
import { styled } from '../theme/default'
import { _MenuGroup } from './MenuGroup'
import { _MenuItem } from './MenuItem'

const MenuFactory = styled('ul', {
  outline: 'none',
  paddingBlock: '$1',
})

export type MenuProps = ComponentProps<typeof MenuFactory> &
  Parameters<typeof useMenu>[0] &
  Parameters<typeof useTreeState>[0]

export function Menu(props: MenuProps) {
  const state = useTreeState({ ...props, selectionMode: 'none' })

  const ref = useRef<HTMLUListElement>()
  const { menuProps } = useMenu(props, state, ref)

  return (
    <MenuFactory {...menuProps} ref={ref} {...props}>
      {[...state.collection].map(item => {
        switch (item.type) {
          case 'section':
            return (
              <_MenuGroup
                as={item.props?.as}
                key={item.key}
                section={item}
                state={state}
                onAction={item.props?.onAction || props.onAction}
                {...item.props}
              />
            )
          case 'item':
          default:
            return (
              <_MenuItem
                as={item.props?.as}
                key={item.key}
                item={item}
                state={state}
                onAction={item.props?.onAction || props.onAction}
                {...item.props}
              />
            )
        }
      })}
    </MenuFactory>
  )
}
