import { ComponentProps } from 'react'
import { mergeProps } from 'react-aria'
import { TextInput } from '../textinput'
import { useFormikHelper } from './useFormikHelper'

/**
 * A helper for using `TextInput` with formik forms.
 * Automatically sets errors and assigns `onChange` and `values` based on the field name
 *
 * @throws if not used in a Formik Context
 */

export function FormikTextField(props: ComponentProps<typeof TextInput>) {
  const [field, meta, helpers] = useFormikHelper(props)

  props.name === 'lastName' && console.log(meta)

  return (
    <TextInput
      {...mergeProps(field, props)}
      invalid={(meta.touched || meta.submitCount > 0) && !!meta.error}
      errorMessage={(meta.touched || meta.submitCount > 0) && meta.error}
      onChange={(value: string) => {
        props?.onChange?.(value)
        helpers.setValue(value)
      }}
    />
  )
}
