import cardValidator from 'card-validator'
import { useFormikContext } from 'formik'
import { useEffect, useRef } from 'react'
import { mergeProps } from 'react-aria'
import { IMask, useIMask } from 'react-imask'
import { useFormikHelper } from '../formik'
import { TextInput } from '../textinput'
import { CreditCardChildFieldProps, CreditCardField } from './types'

const DEFAULT_PLACEHOLDER = 'MM / YY'
const DEFAULT_NAME = CreditCardField.EXPIRY
const DEFAULT_ARIA_LABEL = 'Expiry date'

export function FormikCreditCardExpiryField({
  name = DEFAULT_NAME,
  placeholder = DEFAULT_PLACEHOLDER,
  ...props
}: CreditCardChildFieldProps<{ month: string; year: string }>) {
  const { registerField, unregisterField } = useFormikContext()

  const [field, meta, helpers] = useFormikHelper({ name, ...props })

  useEffect(() => {
    registerField(name, {
      validate: value =>
        cardValidator.expirationDate(`${value?.month} / ${value?.year}`).isValid ? undefined : `Invalid expiry date`,
    })

    return () => unregisterField(name)
  }, [registerField, unregisterField, name])

  return (
    <BaseCreditCardExpiryField
      aria-label={DEFAULT_ARIA_LABEL}
      {...mergeProps(field, props, { name, placeholder })}
      invalid={(meta.touched || meta.submitCount > 0) && !!meta.error}
      onChange={value => {
        props?.onChange?.(value)
        helpers.setValue(value)
      }}
      errorMessage={(meta.touched || meta.submitCount > 0) && meta.error}
    />
  )
}

export function BaseCreditCardExpiryField({
  value,
  onChange,
  ...props
}: CreditCardChildFieldProps<{ month: string; year: string }>) {
  const currentDate = useRef(new Date())
  const currentYear = Number(currentDate.current.getFullYear().toString().slice(2))

  const {
    ref,
    value: maskedValue,
    setUnmaskedValue,
  } = useIMask({
    mask: 'MM / YY',
    blocks: {
      MM: { mask: IMask.MaskedRange, from: 1, to: 12 },
      YY: { mask: IMask.MaskedRange, from: currentYear, to: currentYear + 19 },
    },
  })

  useEffect(() => {
    const [month, year] = maskedValue.split(' / ')
    onChange?.({ month, year })
  }, [maskedValue])

  useEffect(() => {
    if (!value) return
    setUnmaskedValue(`${value.month} / ${value.year}`)
  }, [value])

  return (
    <TextInput
      {...props}
      ref={ref}
      autoComplete="cc-exp"
      autoCorrect="off"
      spellCheck="false"
      id="cardExpiry"
      type="text"
      inputMode="numeric"
    />
  )
}
