import { assign, createMachine } from 'xstate'

export enum HoverStatus {
  ACCEPT = 'ACCEPT',
  REJECT = 'REJECT',
  ACTIVE = 'ACTIVE',
  IDLE = 'IDLE',
}

type RemoveFileEvent = { type: 'REMOVE_FILE'; files: FileMaybeWithPreview[] }
type RejectFileEvent = { type: 'REJECT'; message: string }

export type FileUploadEvent =
  | RejectFileEvent
  | RemoveFileEvent
  | { type: 'ACCEPT' }
  | { type: 'HOVER'; status: HoverStatus; message?: string }
  | { type: 'RESET' }
  | { type: 'ADD_FILE'; files: FileMaybeWithPreview[] }
  | { type: 'OPEN_DIALOG' }
  | { type: 'CANCEL_DIALOG' }

export type FileMaybeWithPreview = File & { preview?: string }

export interface FileUploadContext {
  files?: FileMaybeWithPreview[]
  hoverStatus: HoverStatus
  message?: string
  input?: HTMLInputElement
}

export const fileUploadMachine = createMachine<FileUploadContext, FileUploadEvent>(
  {
    id: 'fileupload-component',
    initial: 'idle',
    context: {
      files: [],
      hoverStatus: HoverStatus.IDLE,
    },
    version: '1.0',
    states: {
      idle: {
        entry: ['resetMachine'],
        always: {
          target: 'accepted',
          cond: { type: 'hasInitialFiles' },
        },
        on: {
          HOVER: {
            target: 'hovering',
            actions: assign({
              hoverStatus: (_, { status }) => status || HoverStatus.ACTIVE,
              message: (_, { message }) => message,
            }),
          },

          OPEN_DIALOG: {
            target: 'dialogOpen',
          },
        },
      },
      dialogOpen: {
        on: {
          CANCEL_DIALOG: {
            target: 'idle',
          },
          ADD_FILE: {
            target: 'accepted',
            actions: assign({
              files: ({ files: contextFiles }: FileUploadContext, { files }) => [...contextFiles, ...files],
              hoverStatus: () => HoverStatus.IDLE,
              message: () => '',
            }),
          },
        },
      },
      hovering: {
        on: {
          RESET: {
            target: 'idle',
          },

          REJECT: {
            target: 'rejected',
            actions: assign<FileUploadContext, RejectFileEvent>({
              message: (_, { message }) => message,
              hoverStatus: () => HoverStatus.REJECT,
            }),
          },

          ADD_FILE: {
            target: 'accepted',
            cond: { type: 'fileIsAllowed' },
            actions: assign({
              files: ({ files: contextFiles }: FileUploadContext, { files }) => [...contextFiles, ...files],
              hoverStatus: () => HoverStatus.IDLE,
              message: () => '',
            }),
          },
        },
      },
      rejected: {
        on: {
          HOVER: {
            target: 'hovering',
            actions: assign({
              hoverStatus: (_, { status }) => status || HoverStatus.ACTIVE,
              message: (_, { message }) => message,
            }),
          },
        },
        after: {
          5000: { target: 'idle' },
        },
      },
      accepted: {
        on: {
          REMOVE_FILE: {
            target: 'idle',
            actions: assign({
              files: (_, { files }) => {
                console.log({ files })

                return files
              },
            }),
          },
        },
      },
    },
  },
  {
    actions: {
      resetMachine: assign({ hoverStatus: HoverStatus.IDLE, message: '' }),
    },
    guards: {
      hasInitialFiles: (context: FileUploadContext) => context.files.length > 0,
      fileIsAllowed: (context: FileUploadContext) => context.hoverStatus === HoverStatus.ACCEPT,
    },
  },
)
