import { createContext, FC, memo, ReactNode, useContext } from 'react'
export const StackingContext = createContext(5)

export type StackProps = {
  value: number
  children: (value: number) => ReactNode
}

export const StackingProvider: FC<StackProps> = memo(function Stack({ children, value = 5 }) {
  const previousValue = useContext(StackingContext)
  const currentValue = Math.max(value, previousValue)
  const nextValue = currentValue + 1

  return <StackingContext.Provider value={nextValue}>{children(currentValue)}</StackingContext.Provider>
})
