import { AnimatePresence, motion } from 'framer-motion'
import { cloneElement, ComponentProps, ReactElement, ReactNode } from 'react'
import { mergeProps } from 'react-aria'
import { Arrow, Placement, useHover, useLayer } from 'react-laag'
import { Box, Card, StackingProvider } from '../layout'
import { StackingOrder, theme } from '../theme/default'

export type TooltipProps = ComponentProps<typeof Card> & {
  /** The inside of the toolip. Can be a string or any React Node */
  content: ReactNode
  /** The placement of the tooltip relative to the target */
  placement?: Placement
  /** How long the tooltip should wait before showing when the target is hovered on */
  showDelay?: number
  /** How long the tooltip should stay shown stay after the target loses focus */
  hideDelay?: number
  /** The distance of the tooltip from the target */
  triggerOffset?: number
  /** The minimum distance to maintain from the container. (Usually the window) */
  containerOffset?: number
  /** Force the tooltip to be shown. Tooltip becomes controlled */
  isShown?: boolean
  /** Whether the tooltip should have an arrow */
  hasArrow?: boolean
}

export function Tooltip({
  children,
  content,
  showDelay = 100,
  hideDelay = 300,
  placement = 'top-center',
  hasArrow = false,
  triggerOffset = hasArrow ? 15 : 5,
  containerOffset = 15,
  isShown = false,
  ...props
}: TooltipProps) {
  const [isOver, hoverProps] = useHover({ delayEnter: showDelay, delayLeave: hideDelay })

  const { renderLayer, triggerProps, layerProps, layerSide, arrowProps } = useLayer({
    isOpen: isShown || isOver,
    container: document?.querySelector('[data-overlay-container="true"]') as HTMLDivElement,
    overflowContainer: true, // keep the menu positioned inside the container
    auto: true, // automatically find the best placement
    placement, // we prefer to place the menu "bottom-center"
    triggerOffset, // keep some distance to the trigger
    containerOffset, // give the menu some room to breath relative to the container
  })

  return (
    <>
      <Box as="span" css={{ display: 'inline', width: 'fit-content', height: 'fit-content' }}>
        {cloneElement(children as ReactElement, { ...triggerProps, ...hoverProps })}
      </Box>
      <StackingProvider value={StackingOrder.POSITIONER}>
        {zIndex =>
          renderLayer(
            <AnimatePresence>
              {isOver && (
                <Card
                  {...mergeProps(props, layerProps)}
                  role="dialog"
                  as={motion.div as any}
                  initial={{
                    opacity: 0,
                    scale: 0.99,
                    y: layerSide === 'top' ? triggerOffset : -triggerOffset,
                  }}
                  animate={{ opacity: 1, y: 0, scale: 1 }}
                  exit={{
                    opacity: 0,
                    scale: 0.99,
                    y: layerSide === 'top' ? triggerOffset : -triggerOffset,
                  }}
                  transition={{
                    type: 'spring',
                    stiffness: 800,
                    damping: 25,
                    opacity: { type: 'tween', duration: 0.1 },
                  }}
                  css={{
                    backgroundColor: '$tooltip',
                    color: '$white',
                    backdropFilter: 'blur(20px)',
                    borderRadius: '$1',
                    border: '1px solid $colors$gray2',
                    padding: '3px 10px',
                    paddingBottom: '4px',
                    zIndex,
                  }}
                >
                  {hasArrow && (
                    <Arrow
                      {...arrowProps}
                      backgroundColor={`var(${theme.colors.tooltip.variable})`}
                      borderColor={`var(${theme.colors.gray2.variable})`}
                      borderWidth={1}
                      size={8}
                    />
                  )}
                  {typeof content === 'string' ? (
                    <Box as="span" css={{ fontSize: '$caption2', marginTop: 2 }}>
                      {content}
                    </Box>
                  ) : (
                    content
                  )}
                </Card>
              )}
            </AnimatePresence>,
          )
        }
      </StackingProvider>
    </>
  )
}
