import { Node } from '@react-types/shared'
import { Key, ReactNode, useRef } from 'react'
import { useMenuItem } from 'react-aria'
import { TreeState } from 'react-stately'
import type { DefaultItemProps } from '../collections/types'
import { Item } from '../collections'
import { Intent, styled } from '../theme/default'
import { Text } from '../typography'

const MenuItemFactory = styled('li', {
  appearance: 'none',
  WebkitAppearance: 'none',
  textDecoration: 'none',
  listStyle: 'none',
  display: 'flex',
  alignItems: 'center',
  cursor: 'pointer',
  outline: 'none',
  userSelect: 'none',

  '&:hover': {
    backgroundColor: '$gray5',
  },
  '&:focus': {
    backgroundColor: '$gray4',
  },
  '&:active': {
    backgroundColor: '$gray3',
  },

  variants: {
    isFocused: {
      true: {
        backgroundColor: '$gray4',
      },
    },

    size: {
      small: {
        padding: '$1 0',
        height: '$controlHeightSmall',
      },
      regular: {
        padding: '$2 0',
        height: '$controlHeightRegular',
      },
      large: {
        padding: '$3 0',
        height: '$controlHeightLarge',
      },
    },
  },

  defaultVariants: {
    size: 'regular',
  },
})

type MenuItemProps<T> = Parameters<typeof MenuItemFactory>[0] & {
  intent?: Intent
  item: Node<T> & DefaultItemProps<T>
  state: TreeState<any>

  icon?: ReactNode

  onAction?: (key: Key) => void
}

export function _MenuItem<T>({ children, onSelect, onKeyPress, onClick, state, item, ...props }: MenuItemProps<T>) {
  const onAction = (props.onAction || onClick || onSelect || onKeyPress) as unknown as typeof props.onAction

  const ref = useRef<HTMLLIElement>(null)
  const isDisabled = state.disabledKeys.has(item.key)
  const isFocused = state.selectionManager.focusedKey === item.key

  const { menuItemProps, keyboardShortcutProps, labelProps } = useMenuItem(
    { key: item.key || props.key, onAction, isDisabled, ...props },
    state,
    ref,
  )

  const color = item.props.intent || props.intent || 'label'
  const Icon = item.props.icon
  const HelperText = item.props.helperText

  return (
    <MenuItemFactory as={item.props.as} {...menuItemProps} {...props} ref={ref} isFocused={isFocused}>
      {typeof Icon === 'function' ? (
        <Icon
          role="presentation"
          size="$4"
          css={{
            color: `$${color}`,
            marginLeft: '$3',
          }}
        />
      ) : (
        Icon
      )}

      <Text {...labelProps} css={{ color: `$${color}`, marginInline: '$3', flex: '1' }} size="caption1">
        {item.rendered ?? children}
      </Text>

      {HelperText ? (
        typeof HelperText === 'function' ? (
          <HelperText
            role="presentation"
            size="$3"
            css={{
              color: '$secondary',
              marginRight: '$3',
            }}
          />
        ) : typeof HelperText === 'string' ? (
          <Text {...keyboardShortcutProps} css={{ marginRight: '$3', color: '$secondary' }} size="caption2">
            {HelperText}
          </Text>
        ) : (
          HelperText
        )
      ) : null}
    </MenuItemFactory>
  )
}

/** @deprecated Use `Item` instead */
export const MenuItem = Item
