import { CSS } from '@stitches/react'
import { useSeparator } from 'react-aria'
import { styled, config } from '../theme'

const StyledSeparator = styled('div', {
  backgroundColor: '$gray4',

  variants: {
    orientation: {
      horizontal: {
        width: '100%',
        height: '1px',
        margin: '$1 0',
      },
      vertical: {
        width: '1px',
        height: '100%',
        margin: '0 $1',
      },
    },
  },
  defaultVariants: {
    orientation: 'horizontal',
  },
})

type SeparatorProps = Parameters<typeof useSeparator>[0] & {
  css?: CSS<typeof config>
}

/**
 * A component that can be used to separate sections of content.
 *
 * Provides some necessary roles for aria.
 * @see https://developer.mozilla.org/en-US/docs/Web/Accessibility/ARIA/Roles/separator_role
 *
 * @author Evan Hennessy
 * @since 1.3.8
 */
export function Separator(props: SeparatorProps) {
  const { separatorProps } = useSeparator(props)

  return <StyledSeparator {...props} {...separatorProps} />
}
