import { isNaN } from 'lodash-es'

export function sum(values: any[], aggregatedValues: number[]): number {
  // It's faster to just add the aggregations together instead of
  // process leaf nodes individually
  return (aggregatedValues || values).reduce(
    (sum, next) => sum + (typeof next === 'number' ? next : !isNaN(next) ? Number(next) : 0),
    0,
  )
}

export function min(values: number[]): number {
  let min = values[0] || 0

  values.forEach(value => {
    value = !isNaN(Number(value)) ? Number(value) : 0
    if (typeof value === 'number') {
      min = Math.min(min, value)
    }
  })

  return min
}

export function max(values: number[]): number {
  let max = values[0] || 0

  values.forEach(value => {
    value = !isNaN(Number(value)) ? Number(value) : 0
    if (typeof value === 'number') {
      max = Math.max(max, value)
    }
  })

  return max
}

export function minMax(values: number[]): string {
  let min = values[0] || 0
  let max = values[0] || 0

  values.forEach(value => {
    value = !isNaN(Number(value)) ? Number(value) : 0
    if (typeof value === 'number') {
      min = Math.min(min, value)
      max = Math.max(max, value)
    }
  })

  return `${min}..${max}`
}

export function average(values: any[]): number {
  return sum(null, values) / values.length
}

export function median(values: any[]) {
  if (!values.length) {
    return null
  }

  const mid = Math.floor(values.length / 2)
  const nums = [...values].sort((a, b) => a - b)
  return values.length % 2 !== 0 ? nums[mid] : (nums[mid - 1] + nums[mid]) / 2
}

export function unique(values: any[]): any[] {
  return Array.from(new Set(values).values())
}

export function uniqueCount(values: any[]): number {
  return new Set(values).size
}

export function count(values: any[]): number {
  return values.length
}
