import { CSS } from '@stitches/react'
import type { ComponentProps, FC } from 'react'
import { config, styled } from '../theme/default'

const _Spacer = styled('div', {})

export type SpacerProps = { size?: CSS<typeof config>['size'] } & ComponentProps<typeof _Spacer>

export const Spacer: FC<SpacerProps> = props => <_Spacer {...props} css={{ size: props.size, ...(props.css as any) }} />
Spacer.defaultProps = {
  size: '$1',
}
