import { ComponentProps, forwardRef, useRef, useState } from 'react'
import { mergeProps, useTextField } from 'react-aria'
import { mergeRefs } from 'react-laag'
import TextAreaAutosize from 'react-textarea-autosize'
import { FieldErrorAndHelpText, FieldLabelAndDescription, FieldLabelAndDescriptionProps, FieldWrapper } from '../form'
import { ValidationMessageProps } from '../form/helpers/types'
import { useCustomValidity } from '../form/helpers/useCustomValidity'
import { HStack } from '../layout'
import { styled } from '../theme/default'

const TextAreaFactory = styled(TextAreaAutosize, {
  $$shadowColor: '$colors$gray3',
  WebkitAppearance: 'none',
  MozAppearance: 'none',
  border: 'none',
  boxShadow: '0 0 0 1px $$shadowColor',
  borderRadius: '$1',
  color: '$label',
  backgroundColor: '$background',
  width: '100%',
  padding: '$2 $3',
  transition: 'box-shadow $fast, height $fast',
  fontFamily: '$sans',
  lineHeight: '1.15',

  '&::placeholder': {
    color: '$placeholderText',
  },

  '&[disabled]': {
    cursor: 'not-allowed',
  },

  '&:focus': {
    $$shadowColor: '$colors$primary',
    outline: 'none',
    backgroundColor: '$background',
    boxShadow: `0 0 0 2px $$shadowColor`,
  },

  '&:disabled': {
    $$shadowColor: '$colors$gray3',
    color: '$gray2',
    backgroundColor: '$gray4',
  },

  '&:invalid': {
    $$shadowColor: '$colors$danger',
    backgroundColor: 'hsl($hsl_danger / 0.15)',

    '&::placeholder': {
      color: 'hsl($hsl_danger / 0.65)',
    },

    '&:focus': {
      boxShadow: `0 0 0 2px $$shadowColor`,
    },
  },

  variants: {
    resize: {
      true: { resize: 'vertical' },
      false: {
        resize: 'none',
      },
    },

    size: {
      regular: {
        fontSize: '$3',
        padding: '$2 $3',
      },
      large: {
        fontSize: '$4',
        padding: '$3 $4',
        borderRadius: '$2',
        height: '$8',
      },
    },
  },

  defaultVariants: {
    resize: false,
    size: 'regular',
  },
})

type AriaTextFieldProps = Parameters<typeof useTextField>[0]
type TextAreaProps = Omit<AriaTextFieldProps, 'isRequired' | 'isDisabled'> &
  ComponentProps<typeof TextAreaFactory> &
  FieldLabelAndDescriptionProps &
  ValidationMessageProps & {
    required?: AriaTextFieldProps['isRequired']
    disabled?: AriaTextFieldProps['isDisabled']

    /** Set the maximum number of lines to grow to before scrolling. */
    maxRows?: number
    /** Set the minimum number of lines to shrink to before scrolling. */
    minRows?: number

    /** Hide the word count when maxLength is set. */
    hideWordCount?: boolean
  }

export const TextArea = forwardRef<HTMLTextAreaElement, TextAreaProps>(
  (
    {
      css,
      maxRows = 8,
      minRows = 3,
      invalid,
      labelProps,
      errorMessage,
      helpText,
      description,
      hideWordCount,
      ...props
    },
    forwardedRef,
  ) => {
    const { label } = props
    const ref = useRef<HTMLTextAreaElement>()
    const aliasProps: Partial<AriaTextFieldProps> = {
      isDisabled: props.disabled,
      isRequired: props.required,
      'aria-details': description as string,
    }
    const { labelProps: ariaLabelProps, inputProps } = useTextField(
      { ...aliasProps, ...props, inputElementType: 'textarea' },
      ref,
    )
    const [textLength, setTextLength] = useState(0)
    const validityProps = useCustomValidity(ref, { errorMessage, invalid })

    const showWordCount = props.maxLength && !hideWordCount
    const helpTextMaybeWithWordCount = showWordCount ? (
      <HStack justify="between" css={{ width: '100%' }}>
        <span>{helpText}</span>
        <span>{`${textLength} / ${props.maxLength}`}</span>
      </HStack>
    ) : (
      helpText
    )

    return (
      <FieldWrapper css={css}>
        <FieldLabelAndDescription
          label={label}
          labelProps={mergeProps(ariaLabelProps, labelProps)}
          description={description}
        />

        <TextAreaFactory
          maxRows={maxRows}
          minRows={minRows}
          resize={props.resize}
          {...(inputProps as TextAreaProps)}
          {...validityProps}
          onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => {
            props.onChange?.(e)
            if (showWordCount) {
              setTextLength(e.currentTarget.value?.length)
            }
          }}
          ref={mergeRefs(ref, forwardedRef) as any}
        />

        <FieldErrorAndHelpText helpText={helpTextMaybeWithWordCount} errorMessage={errorMessage} invalid={invalid} />
      </FieldWrapper>
    )
  },
)

TextArea.displayName = 'TextArea'
TextArea.toString = TextAreaFactory.toString
TextArea.defaultProps = { size: 'regular' }
