import { AriaAttributes, ComponentProps } from 'react'
import { VStack } from '../layout'
import { styled } from '../theme/default'
import { Label } from '../typography'

const MultiSelectFactory = styled('select', {
  cursor: 'pointer',
  display: 'flex',
  WebkitAppearance: 'none',
  MozAppearance: 'none',
  border: 'none',
  boxShadow: '0 0 0 1.5px $colors$gray3',
  fontSize: '$body',
  width: '100%',
  minWidth: '$8',
  borderRadius: '$1',
  color: '$label',
  backgroundColor: '$background',
  paddingLeft: '$3',
  paddingRight: '$3',

  '&:focus': {
    outline: 'none',
    boxShadow: `0 0 0 2px hsl($colors$hsl_primary / 0.25)`,

    option: {
      '&:checked': {
        background: 'linear-gradient(0deg, hsl($colors$hsl_primary / 0.1) 0%, hsl($colors$hsl_primary / 0.1) 100%)',
      },
    },
  },

  '&[disabled]': {
    cursor: 'not-allowed',
    backgroundColor: '$gray4',
  },

  option: {
    whiteSpace: 'nowrap',
    textOverflow: 'ellipsis',
    overflow: 'hidden',
    width: '100%',
    marginTop: 'calc($1 / 2)',
    borderRadius: 'calc($1 / 2)',
    padding: '$1',

    '&:checked': {
      background: 'linear-gradient(0deg, $colors$gray5 0%, $colors$gray5 100%)',
    },
  },

  variants: {
    size: {
      small: {
        borderRadius: 'calc($1 / 2)',
        padding: 'calc($1 / 4) calc($1 / 2)',
      },
      regular: {
        padding: 'calc($1 / 4) calc($1 / 2)',
        fontSize: '$3',
      },
      large: {
        padding: 'calc($1 / 4) calc($1 / 2)',
      },
    },
  },

  defaultVariants: {
    size: 'regular',
  },
})

export type MultiSelectProps = ComponentProps<typeof MultiSelectFactory> & {
  /** Provide a helpful message for why the input is invalid */
  validationMessage?: string

  /** The aria invalid attribute */
  invalid?: AriaAttributes['aria-invalid']

  label?: string
}

/**
 * A simple select field that includes theme styling
 */
export function MultiSelect(props: MultiSelectProps) {
  return (
    <VStack css={{ width: '100%' }} space="small">
      {props.label && <Label>{props.label}</Label>}
      <MultiSelectFactory multiple {...props} />
    </VStack>
  )
}

MultiSelect.defaultProps = {
  disabled: false,
  invalid: false,
}
