import { clamp } from 'lodash-es'
import { useEffect, useRef, useState } from 'react'
import { FormattedMessage } from 'react-intl'
import { TableState, useTable } from 'react-table'
import { ArrowLeftEndIcon, ArrowRightEndIcon, ChevronLeftIcon, ChevronRightIcon } from '../icon'
import { Select } from '../select/Select'
import { Box, HStack } from '../layout'
import { Text } from '../typography/Text'
import type { TableProps } from './types/TableProps'
import { PaginationButton, PaginationWrapper } from './TableElements'

type TablePaginationProps = Partial<ReturnType<typeof useTable>> &
  Pick<TableProps, 'footerOptions' | 'rowCount'> &
  Pick<TableState, 'pageIndex' | 'pageSize'> & {
    align?: 'left' | 'right'
    pageSizes?: number[]
  }

export function TablePagination({
  gotoPage,
  pageCount,
  canNextPage,
  canPreviousPage,
  pageIndex = 0,
  pageSize,
  nextPage,
  setPageSize,
  pageOptions,
  previousPage,
  pageSizes = [10, 20, 30, 40, 50],
  rowCount,
  footerOptions: { sticky = true, align = 'right' } = {},
}: TablePaginationProps) {
  const [tableWidth, setTableWidth] = useState<number>()
  const tableRef = useRef<HTMLTableElement>()

  const startAt = clamp(pageIndex - 2, 0, pageCount - 5)
  const endAt = clamp(pageIndex + 3, 5, pageCount)
  const pageNumbers = pageOptions.slice(startAt, endAt)

  useEffect(() => {
    if (tableRef.current) {
      const widthObserver = new ResizeObserver(entries => {
        if (entries?.[0]?.borderBoxSize?.[0].inlineSize) {
          setTableWidth(entries[0].borderBoxSize[0].inlineSize)
        }
      })
      widthObserver.observe(tableRef.current.closest('table'))
    }
  }, [tableRef])

  return (
    <PaginationWrapper sticky={sticky} align={align} style={{ maxWidth: tableWidth }} ref={tableRef}>
      <HStack css={{ gap: '2px' }}>
        <PaginationButton
          aria-label="First Page"
          onPress={() => gotoPage(0)}
          disabled={!canPreviousPage}
          icon={ArrowLeftEndIcon}
        />
        <PaginationButton
          aria-label="Previous Page"
          onPress={previousPage}
          disabled={!canPreviousPage}
          icon={ChevronLeftIcon}
        />
        {pageNumbers.map(pageNumber => (
          <PaginationButton
            aria-label={`Page ${pageNumber + 1}`}
            active={pageNumber === pageIndex}
            key={pageNumber}
            onPress={() => gotoPage(pageNumber)}
            children={pageNumber + 1}
          />
        ))}
        <PaginationButton aria-label="Next Page" onPress={nextPage} disabled={!canNextPage} icon={ChevronRightIcon} />
        <PaginationButton
          aria-label="Last Page"
          onPress={() => gotoPage(pageCount - 1)}
          disabled={!canNextPage}
          icon={ArrowRightEndIcon}
        />
      </HStack>
      {pageSizes.length > 1 && (
        <Box css={{ width: '$14' }}>
          <Select
            value={pageSize}
            onChange={e => {
              setPageSize(Number(e.target.value))
            }}
          >
            {pageSizes.map(pageSize => (
              <option key={pageSize} value={pageSize}>
                {pageSize}
              </option>
            ))}
          </Select>
        </Box>
      )}
      <Text css={{ whiteSpace: 'nowrap', fontVariant: 'tabular-nums', minWidth: '80px' }} size="caption1">
        <FormattedMessage
          id="table.pagination.results"
          defaultMessage="{start}-{end} of {total}"
          values={{
            start: pageIndex * pageSize + 1,
            end: Math.min(pageIndex * pageSize + pageSize, rowCount ?? pageCount * pageSize),
            total: rowCount ?? pageCount * pageSize,
          }}
        />
      </Text>
    </PaginationWrapper>
  )
}
