import { IdProvider } from '@radix-ui/react-id'
import { merge } from 'lodash-es'
import { ComponentProps, ReactChild } from 'react'
import { OverlayProvider } from 'react-aria'
import { IntlConfig, IntlProvider } from 'react-intl'
import { ConfirmProvider } from '../confirmDialog/ConfirmProvider'
import en from '../locale/en.json'
import { Toaster } from '../toast/Toaster'
import { globalStyles } from './default'
import { DEFAULT_FEATURE_FLAGS, FeatureFlag, FeatureFlagProvider } from './featureFlags'

type ThemeProviderProps = {
  children: ReactChild
  disableGlobalStyles?: boolean
  disableResetStyles?: boolean
  toaster?: Pick<ComponentProps<typeof Toaster>, 'position' | 'defaultDuration' | 'offset'>
  i18n?: Partial<IntlConfig>
  featureFlags?: Partial<Record<FeatureFlag, boolean>>
}
export function ThemeProvider({
  disableGlobalStyles = false,
  disableResetStyles = false,
  children,
  toaster = {},
  i18n = {},
  featureFlags = {},
}: ThemeProviderProps) {
  if (!disableGlobalStyles) {
    globalStyles(disableResetStyles)()
  }

  return (
    <OverlayProvider>
      <IdProvider>
        <FeatureFlagProvider value={{ ...DEFAULT_FEATURE_FLAGS, ...featureFlags }}>
          <IntlProvider
            {...i18n}
            defaultRichTextElements={{ b: chunks => <b>{chunks}</b>, ...i18n.defaultRichTextElements }}
            locale={i18n.locale || navigator?.language}
            defaultLocale={i18n.defaultLocale || 'en'}
            messages={merge(en, i18n.messages)}
          >
            <Toaster {...toaster}>
              <ConfirmProvider>{children}</ConfirmProvider>
            </Toaster>
          </IntlProvider>
        </FeatureFlagProvider>
      </IdProvider>
    </OverlayProvider>
  )
}

