import { ConjsProps } from 'react-awesome-query-builder'
import { Checkbox } from '../../checkbox'
import { Card, HStack } from '../../layout'
import { Radio, RadioGroup } from '../../radio'
import { Label } from '../../typography'

export function QueryBuilderConjunctions({
  id,
  not,
  setNot,
  conjunctionOptions,
  setConjunction,
  selectedConjunction,
  disabled,
  readonly,
  showNot,
  notLabel,
}: ConjsProps & { id: string }) {
  const conjunctionsCount = Object.keys(conjunctionOptions).length
  const lessThanTwo = disabled
  const showOptions = conjunctionsCount > 1 && !lessThanTwo

  const onNotChange = (checked: boolean) => setNot(checked)
  const onChange = (value: string) => setConjunction(value)

  const Not = () => (
    <Checkbox label={notLabel} key={id} id={id + '__not'} checked={not} disabled={readonly} onChange={onNotChange} />
  )

  const Options = () => (
    <RadioGroup
      label="conjunctions"
      hideLabel
      orientation="horizontal"
      defaultValue={selectedConjunction}
      onChange={onChange}
    >
      {Object.keys(conjunctionOptions).map(key => {
        const { id, label } = conjunctionOptions[key]
        return <Radio label={label} key={id} id={id} disabled={readonly} value={key} />
      })}
    </RadioGroup>
  )

  return (
    <Card css={{ backgroundColor: '$groupedBackground', width: 'fit-content' }} border>
      <HStack align="center" css={{ minHeight: '$10', padding: '$2 $4' }}>
        <Label>{!showNot && !showOptions ? 'Rule' : 'Rule:'}</Label>
        {showNot ? Not() : null}
        {showOptions ? Options() : null}
      </HStack>
    </Card>
  )
}
