import { isEmpty } from 'lodash-es'
import { CSSProperties, ReactChild, useEffect, useMemo, useRef } from 'react'
import {
  Cell,
  Column,
  HeaderGroup,
  useGroupBy,
  usePagination,
  useResizeColumns,
  useRowSelect,
  useRowState,
  useSortBy,
  UseSortByColumnProps,
  useTable,
} from 'react-table'
import { useScroll } from 'react-use'
import { DataTransferVerticalIcon } from '../icon'
import { VStack } from '../layout/Stack'
import { useCheckboxes, useColumnSummary, useFormatters, useGridLayout } from './hooks'
import { useDblClickResetColumn } from './hooks/useDblClickResetColumn'
import {
  ResizingHandle,
  StyledTable,
  StyledTableHeaderPill,
  StyledTableHeaderText,
  StyledTableText,
} from './TableElements'
import { TablePagination } from './TablePagination'
import { TableRow } from './TableRow'
import type { TableProps } from './types/TableProps'

const sortingColors = ({ isSorted, isSortedDesc }: UseSortByColumnProps<object>) =>
  isSorted ? (isSortedDesc ? ['$primary', '$gray3'] : ['$gray3', '$primary']) : ['$gray3', '$gray3']

function getData({
  data = [],
  columns,
  pageSize = 10,
  loading = false,
}: {
  data: readonly any[]
  columns: readonly Column<object>[]
  pageSize?: number
  loading?: boolean
}) {
  let loadingData = Array(pageSize).fill({})

  if (loading && isEmpty(data)) {
    loadingData = loadingData.map(() => ({
      ...columns.reduce((acc, col) => ({ ...acc, [typeof col.accessor === 'string' ? col.accessor : col.id]: '' }), {}),
    }))

    return loadingData
  }

  return data
}

type ExtraColumnOptions = {
  sticky?: 'left' | 'right'
  loadingState?: false
  cellFormatter?: (cell: Cell) => ReactChild
}

/**
 * @summary
 * Glass Canvas's Table component. Required Props are `data` and `columns`.
 * **Note:** `data` and `columns` must be memoized using `useMemo` or else suffer performance implications.
 *
 * ### Optional Table Features
 * Table features are off by default.
 * To enable a feature, set the corresponding prop to `true`.
 * - `selectable`: Enables selecting rows with checkbox. Alias for `rowSelect` and `selectAll`
 * - `sortable`: Enables sorting of columns.
 * - `resizable`: Enables resizing of columns.
 * - `pageable`: Enables pagination.
 *
 *
 * @example
 * <Table
 *  data={[{ id: 'Person:1', name: 'John', age: 20 }, { id: 'Person:2', name: 'Jane', age: 30 }]}
 *  columns={[{ Header: 'Name', accessor: 'name' }, { Header: 'Age', accessor: 'age' }]}
 *  selectable
 *  onRowPress={(row) => goTo(`/person/${row.id}`)}
 * />
 *
 * @note Using the `css` prop to style the table may be necessary to fill whatever space you're looking for.
 *
 * @note If you'd like to embed click-able elements in the table, you should consider stopping event propagation so the row is not clicked as a result.
 *
 * @tutorial https://glasscanvas.slack.com/files/U013S5C7CQ3/F02MXQZF2MB/screenshare_-_2021-11-19_11_55_55_am.webm
 *
 * @author [Evan Hennessy]
 */
export function Table({
  columns,
  data,
  initialState,
  rowSelect,
  sortable = false,
  resizable = false,
  selectable = false,
  pageable = false,
  defaultColumn,
  setColumnOrder: setColumnOrderProp,
  headerOptions: { sticky = true, selectAll = true, ...headerOptions } = {},
  footerOptions,
  loading,
  pageCount: controlledPageCount,
  pageIndex: controlledPageIndex,
  pageSizes,
  pageSize: initialPageSize = pageSizes?.[0] ?? 10,
  manualPagination = false,
  manualSortBy = false,
  autoResetPage,
  css,
  border,
  borderBottom,
  borderLeft,
  borderRight,
  borderTop,
  onSortChange,
  onPaginationChange,
  sortBy: controlledSortBy,
  hiddenColumns = [],
  rowCount,
  selectedRowIds: controlledSelectedRowIds,
  onSelectionChange,
  getRowId,
  enableMultiSort,
  as,
  onRowPress,
  rowProps,
  initialSortBy,
  aboveTable: AboveTable,
  belowTable: BelowTable,

  ...props
}: TableProps) {
  const memoizedData = useMemo(() => getData({ data, columns, pageSize: initialPageSize, loading }), [data])
  const memoizedColumns = useMemo(() => columns, [columns])
  const memoizedGetRowId = useMemo(() => getRowId, [getRowId])
  const memoizedDefaultColumns = useMemo(() => defaultColumn, [defaultColumn])
  const memoizedDefaultSortBy = useMemo(() => initialSortBy ?? [], [initialSortBy])

  const {
    getTableProps,
    headerGroups,
    rows,
    prepareRow,
    gotoPage,
    pageCount,
    canNextPage,
    canPreviousPage,
    pageOptions,
    nextPage,
    setPageSize,
    previousPage,
    totalColumnsWidth,
    state: { pageIndex, pageSize, ...state },
  } = useTable(
    {
      getRowId: getRowId ? memoizedGetRowId : undefined,
      disableMultiSort: !enableMultiSort,
      columns: memoizedColumns as Column<ExtraColumnOptions>[],
      selectAll,
      data: memoizedData,
      defaultColumn: memoizedDefaultColumns as Partial<Column<ExtraColumnOptions>>,
      manualSortBy,
      disableRowSelection: !selectable,
      disableSortBy: !sortable,
      disableResizing: !resizable,
      pageCount: controlledPageCount,
      manualPagination,
      autoResetSortBy: !manualSortBy,
      autoResetPage: !manualPagination,
      autoResetSelectedRows: !manualPagination,
      ...props,
      initialState: { pageSize: initialPageSize, hiddenColumns, sortBy: memoizedDefaultSortBy },
    },
    useGroupBy,
    useColumnSummary,
    useFormatters,
    useRowState,
    useGridLayout,
    useSortBy,
    usePagination,
    useResizeColumns,
    useRowSelect,
    useCheckboxes,
    useDblClickResetColumn,
  )

  const tableRef = useRef<HTMLTableElement>(null)
  useScroll(tableRef)

  useEffect(() => onSortChange?.(state.sortBy), [state.sortBy])
  useEffect(() => onSelectionChange?.(state.selectedRowIds), [state.selectedRowIds])
  useEffect(() => {
    if (loading) return
    onPaginationChange?.({ pageIndex, pageSize })
  }, [pageIndex, pageSize])
  useEffect(() => {
    if (loading) return
    if (typeof controlledPageIndex === 'number' && controlledPageIndex !== pageIndex) {
      gotoPage(controlledPageIndex)
    }

    if (typeof initialPageSize === 'number' && initialPageSize !== pageSize) {
      setPageSize(initialPageSize)
    }
  }, [controlledPageIndex, initialPageSize])

  const wrapperComponentsCss: CSSProperties = {
    gridColumn: '1 / -1',
    position: 'sticky',
    left: 0,
    maxWidth: `calc(100vw - ${tableRef?.current?.offsetLeft}px)`,
  }

  return (
    <StyledTable
      {...getTableProps()}
      ref={tableRef}
      as={as}
      css={css}
      border={border}
      borderBottom={borderBottom}
      borderLeft={borderLeft}
      borderRight={borderRight}
    >
      {typeof AboveTable === 'function' && (
        <div style={wrapperComponentsCss}>
          <AboveTable />
        </div>
      )}

      {
        !headerOptions?.hidden &&
          headerGroups.map(headerGroup =>
            headerGroup.headers.map((column: HeaderGroup & ExtraColumnOptions, index) => {
              const position: 'first' | 'last' | undefined = (() => {
                if (index === 0) return 'first'
                if (index === headerGroup.headers.length - 1) return 'last'
                return undefined
              })()

              const borderKey =
                column.sticky === 'left'
                  ? tableRef.current?.scrollLeft > 1
                    ? 'borderRight'
                    : undefined
                  : tableRef.current?.scrollWidth > tableRef.current?.getBoundingClientRect().width + 1
                  ? 'borderLeft'
                  : undefined

              return (
                <StyledTableHeaderText
                  key={column.id}
                  {...column.getHeaderProps()}
                  stickyColumn={!!column.sticky}
                  sticky={sticky}
                  position={position}
                  borderTop={!!borderTop}
                  style={{
                    ...(!!column?.sticky && {
                      [column.sticky]:
                        column.sticky === 'left'
                          ? column.totalLeft + (index === 0 ? 0 : 20)
                          : totalColumnsWidth - column.totalLeft - column.totalWidth - 1,
                      zIndex: 4,
                      ...(!(headerGroup.headers[index + 1] as HeaderGroup & ExtraColumnOptions)?.sticky &&
                        !!borderKey && {
                          [borderKey]: '1px solid var(--gc-colors-gray4)',
                        }),
                    }),
                  }}
                >
                  <StyledTableHeaderPill
                    sortable={column.canSort && sortable}
                    align="center"
                    space="small"
                    {...column.getSortByToggleProps()}
                  >
                    <VStack space="extraSmall">
                      <span>{column.render('Header')}</span>
                      {column.hasColumnSummary && <span>{column.render('ColumnSummary')}</span>}
                    </VStack>

                    {column.canSort && sortable && (
                      <DataTransferVerticalIcon size="$3" multicolored={sortingColors(column)} />
                    )}
                  </StyledTableHeaderPill>

                  {column.canResize && resizable && (
                    <ResizingHandle {...column.getResizerProps()} isResizing={column.isResizing} />
                  )}
                </StyledTableHeaderText>
              )
            }),
          )
      }
      {
        rows.map(row => {
          prepareRow(row)

          const computedRowProps = typeof rowProps === 'function' ? rowProps(row) : rowProps

          return (
            <TableRow key={row.id} onRowPress={onRowPress} row={row} {...row.getRowProps()} {...computedRowProps}>
              {row.cells.map((cell: Cell & { column: Column & ExtraColumnOptions }, index) => {
                const position: 'first' | 'last' | undefined = (() => {
                  if (index === 0) return 'first'
                  if (index === row.cells.length - 1) return 'last'
                  return undefined
                })()

                const borderKey =
                  cell.column.sticky === 'left'
                    ? tableRef.current?.scrollLeft > 1
                      ? 'borderRight'
                      : undefined
                    : tableRef.current?.scrollWidth > tableRef.current?.getBoundingClientRect().width + 1
                    ? 'borderLeft'
                    : undefined

                return (
                  <StyledTableText
                    key={`${cell.row.id}-${cell.column.id}`}
                    {...cell.getCellProps()}
                    stickyColumn={!!cell.column.sticky}
                    loading={cell?.column?.loadingState !== false && loading}
                    position={position}
                    style={{
                      ...(!!cell.column?.sticky && {
                        [cell.column.sticky]:
                          cell.column.sticky === 'left'
                            ? cell.column.totalLeft + (index === 0 ? 0 : 20)
                            : totalColumnsWidth - cell.column.totalLeft - cell.column.totalWidth - 1,
                        zIndex: 3,
                        ...(!(row.cells[index + 1] as Cell & { column: Column & ExtraColumnOptions })?.column
                          ?.sticky && {
                          [borderKey]: '1px solid var(--gc-colors-gray4)',
                        }),
                      }),
                    }}
                  >
                    {cell.render('Cell', { onClick: (e: MouseEvent) => e.stopPropagation() })}
                  </StyledTableText>
                )
              })}
            </TableRow>
          )
        })
      }

      {typeof BelowTable === 'function' && (
        <div style={wrapperComponentsCss}>
          <BelowTable />
        </div>
      )}

      {pageable && (
        <TablePagination
          footerOptions={footerOptions}
          gotoPage={gotoPage}
          pageCount={pageCount}
          canNextPage={canNextPage}
          canPreviousPage={canPreviousPage}
          pageIndex={pageIndex}
          pageOptions={pageOptions}
          pageSize={pageSize}
          nextPage={nextPage}
          setPageSize={setPageSize}
          previousPage={previousPage}
          pageSizes={pageSizes}
          rowCount={rowCount}
        />
      )}
    </StyledTable>
  )
}
