import { FieldHelperProps, FieldInputProps, FieldMetaProps, useField, useFormikContext } from 'formik'
import { FocusEventHandler, useEffect, useState } from 'react'
import { mergeProps } from 'react-aria'
import { useFocusContext } from './focusManager'

interface FormikFieldProps {
  name?: string
  type?: string
}

/**
 * Helps setting `useField` on ElephantKit components as well as adds some other field helpers
 * @private Implementation details are subject to change
 */
export function useFormikHelper<T extends FormikFieldProps>(
  props: T,
): [
  FieldInputProps<T> & { disabled: boolean; focused: boolean; onFocus: FocusEventHandler },
  FieldMetaProps<T> & { submitCount: number },
  FieldHelperProps<any>,
] {
  const [field, meta, helpers] = useField({ name: props.name, type: props.type ?? 'text' })
  const { focus, handleBlur, handleFocus } = useFocusContext() || {}
  const { isSubmitting, submitCount } = useFormikContext()
  const [disabled, setDisabled] = useState(false)
  // Disable the field when submitting
  useEffect(() => setDisabled(isSubmitting), [isSubmitting])

  const fieldProps = mergeProps(field, {
    disabled,
    focused: focus === props?.name,
    onBlur: handleBlur,
    onFocus: handleFocus,
  })

  const fieldMeta = { ...meta, submitCount }

  return [fieldProps, fieldMeta, helpers]
}
