const StyledPaginationButton = styled('button', {
  size: 'clamp($6, 2vw, $10)',
  WebkitFontSmoothing: 'antialiased',
  WebkitAppearance: 'none',
  textDecoration: 'none',
  appearance: 'none',
  border: 'none',
  outline: 'none',
  cursor: 'pointer',
  backgroundColor: 'transparent',
  borderRadius: '$1',
  display: 'inline-flex',
  alignItems: 'center',
  justifyContent: 'center',
  color: '$gray1',
  userSelect: 'none',
  capsize: {
    fontSize: fontPxToNum(3),
  },

  '&:hover': {
    boxShadow: '0 0 0 1px $colors$gray4',
  },

  variants: {
    active: {
      true: {
        boxShadow: '0 0 0 1px $colors$gray2',
        color: '$label',
        fontWeight: '$bold',
        pointerEvents: 'none',

        '&:hover': {
          boxShadow: '0 0 0 1px $colors$gray2',
        },
      },
    },
  },
})

export function PaginationButton({
  icon: Icon,
  children,
  ...props
}: ButtonProps & VariantProps<typeof StyledPaginationButton>) {
  const ref = useRef()
  const { buttonProps } = useButton({ ...(props as any), children }, ref)

  return (
    <StyledPaginationButton {...(props as any)} {...buttonProps} ref={ref}>
      {typeof Icon === 'function' && <Icon size="$2" />}
      {children}
    </StyledPaginationButton>
  )
}
