import { Form as FormikBaseForm, useFormikContext } from 'formik'
import { forwardRef, ReactNode } from 'react'
import { mergeRefs } from 'react-laag'
import { Form as BaseForm } from '../form'
import { FocusContext, FocusManagementContext, useFocusManager } from './focusManager'

export type FormikBaseFormProps = Pick<
  React.FormHTMLAttributes<HTMLFormElement>,
  Exclude<keyof React.FormHTMLAttributes<HTMLFormElement>, 'onReset' | 'onSubmit'>
>

type FormikFormProps<T> = FormikBaseFormProps & {
  initialFocus?: keyof T
  children: ((props: FocusContext) => ReactNode) | ReactNode
}

/**
 * A helper for using `Form` with formik forms.
 * Allows you to set theme styling on the form
 */
export const FormikForm = forwardRef(
  <T extends Record<string, any> = {}>(
    { action, children, initialFocus, ...props }: FormikFormProps<T>,
    forwardedRef: React.Ref<HTMLFormElement>,
  ) => {
    const [ref, value] = useFocusManager({ initialFocus })
    const _action = action ?? '#'
    const { handleReset, handleSubmit } = useFormikContext()

    return (
      <FocusManagementContext.Provider value={value}>
        <BaseForm
          as={FormikBaseForm}
          ref={mergeRefs(ref, forwardedRef)}
          onSubmit={handleSubmit}
          onReset={handleReset}
          action={_action}
          {...props}
        >
          {typeof children === 'function' ? children(value) : children}
        </BaseForm>
      </FocusManagementContext.Provider>
    )
  },
)
