import { createContext, useContext } from 'react'

export enum FeatureFlag {
  Calendar = 'Calendar',
  Select = 'Select',
  MultiSelect = 'MultiSelect',
  Switch = 'Switch',
  DisclosureGroup = 'DisclosureGroup',
  TranslucentToast = 'TranslucentToast',
}

export const DEFAULT_FEATURE_FLAGS: Record<FeatureFlag, boolean> = {
  [FeatureFlag.Calendar]: false,
  [FeatureFlag.Select]: false,
  [FeatureFlag.MultiSelect]: false,
  [FeatureFlag.Switch]: false,
  [FeatureFlag.DisclosureGroup]: false,
  [FeatureFlag.TranslucentToast]: false,
}

export const FeatureFlagContext = createContext(DEFAULT_FEATURE_FLAGS)
export const FeatureFlagProvider = FeatureFlagContext.Provider

export function useFeatureFlag(flag: FeatureFlag): boolean {
  const flags = useContext(FeatureFlagContext)
  return flags[flag]
}
