import { ComponentProps, ReactNode } from 'react'
import { ButtonProps } from 'react-awesome-query-builder'
import { Button } from '../../button'
import { AddCircleIcon, TrashIcon } from '../../icon'
import { Intent } from '../../theme'

type QueryBuilderButtonType =
  | 'addRule'
  | 'addRuleGroupExt'
  | 'addGroup'
  | 'delRule'
  | 'delGroup'
  | 'addRuleGroup'
  | 'delRuleGroup'
type QueryBuilderButtonIconDictionary = Partial<Record<QueryBuilderButtonType, ReactNode>>
type QueryBuilderButtonIntentDictionary = Partial<Record<QueryBuilderButtonType, Intent>>

const QueryBuilderButtonIcons: QueryBuilderButtonIconDictionary = {
  delRule: TrashIcon,
}

const QueryBuilderButtonBeforeIcons: QueryBuilderButtonIconDictionary = {
  addRule: AddCircleIcon,
  addGroup: AddCircleIcon,
  addRuleGroupExt: AddCircleIcon,
  addRuleGroup: AddCircleIcon,
  delRuleGroup: TrashIcon,
  delGroup: TrashIcon,
}

const QueryBuilderButtonIntents: QueryBuilderButtonIntentDictionary = {
  delGroup: 'danger',
  delRuleGroup: 'danger',
  delRule: 'danger',
}

const QueryBuilderButtonAppearance: Record<string, ComponentProps<typeof Button>['appearance']> = {
  addRuleGroup: 'minimal',
  addRuleGroupExt: 'minimal',
  delGroup: 'minimal',
  delRuleGroup: 'minimal',
  delRule: 'minimal',
}

export function QueryBuilderButton({ type, label, onClick }: ButtonProps) {
  const icon = QueryBuilderButtonIcons[type]
  const iconBefore = QueryBuilderButtonBeforeIcons[type]
  const appearance = QueryBuilderButtonAppearance[type] ?? 'outline'
  const intent = QueryBuilderButtonIntents[type]

  return (
    <Button
      intent={intent as any}
      children={label}
      onClick={onClick}
      iconBefore={iconBefore}
      icon={icon ? icon : null}
      appearance={appearance}
    />
  )
}
