import { PressResponder } from '@react-aria/interactions'
import type { CSS } from '@stitches/react'
import {
  Children,
  cloneElement,
  forwardRef,
  Fragment,
  PropsWithChildren,
  ReactElement,
  ReactNode,
  RefObject,
  useRef,
} from 'react'
import {
  DismissButton,
  FocusScope,
  mergeProps,
  OverlayContainer,
  useButton,
  useDialog,
  useModal,
  useOverlay,
  useOverlayPosition,
  useOverlayTrigger,
} from 'react-aria'
import { mergeRefs } from 'react-laag'
import { useOverlayTriggerState } from 'react-stately'
import { StackingProvider } from '../layout'
import { Card } from '../layout/Card'
import { StackingOrder } from '../theme'
import { Heading } from '../typography/Heading'

type AriaPopoverElementProps = {
  title?: ReactNode
  css?: CSS
  popoverRef?: RefObject<HTMLElement>
} & Parameters<typeof useOverlay>[0]

export const AriaPopoverElement = forwardRef<HTMLDivElement, PropsWithChildren<AriaPopoverElementProps>>(
  (
    {
      title,
      children,
      isOpen,
      onClose,
      isKeyboardDismissDisabled,
      shouldCloseOnBlur,
      shouldCloseOnInteractOutside,
      isDismissable = true,
      css,
      popoverRef: passedRef,
      ...props
    },
    forwardedRef,
  ) => {
    const ref = useRef<HTMLElement>()

    const popoverRef = mergeRefs(ref, passedRef, forwardedRef) as unknown as RefObject<HTMLDivElement>

    const { overlayProps } = useOverlay(
      {
        onClose,
        isOpen,
        isDismissable,
        isKeyboardDismissDisabled,
        shouldCloseOnBlur,
        shouldCloseOnInteractOutside,
      },
      popoverRef,
    )

    const { modalProps } = useModal()

    const { dialogProps, titleProps } = useDialog({}, popoverRef)

    return (
      <StackingProvider value={StackingOrder.OVERLAY}>
        {zIndex => (
          <FocusScope restoreFocus>
            <Card
              {...mergeProps(overlayProps, dialogProps, props, modalProps)}
              ref={popoverRef}
              border
              elevation={2}
              css={{
                backgroundColor: '$background',
                overflow: 'hidden',
                minWidth: '200px',
                borderRadius: '$2',
                zIndex,
                ...(css || {}),
              }}
            >
              {title && (
                <Heading size="subHeadline" {...titleProps}>
                  {title}
                </Heading>
              )}
              {children}
              <DismissButton onDismiss={onClose} />
            </Card>
          </FocusScope>
        )}
      </StackingProvider>
    )
  },
)

type AriaPopoverProps = {
  content: ReactNode
  title?: ReactNode
  /** Alias for isOpen */
  isShown?: Partial<Parameters<typeof useOverlayTriggerState>[0]>['isOpen']
} & Partial<Parameters<typeof useOverlayTrigger>[0]> &
  Partial<Parameters<typeof useOverlayPosition>[0]> &
  Partial<Parameters<typeof useOverlayTriggerState>[0]>

export function AriaPopover({
  boundaryElement,
  children,
  containerPadding,
  content,
  crossOffset,
  defaultOpen,
  isOpen,
  isShown,
  offset = 5,
  onClose,
  onOpenChange,
  placement = 'top',
  scrollRef,
  shouldFlip = true,
  shouldUpdatePosition = true,
  title,
  type = 'dialog',
}: PropsWithChildren<AriaPopoverProps>) {
  isOpen ||= isShown

  const state = useOverlayTriggerState({ isOpen, defaultOpen, onOpenChange })
  const triggerRef = useRef<HTMLElement>()
  const overlayRef = useRef()
  const trigger = Children.toArray(children)[0]

  const { triggerProps, overlayProps } = useOverlayTrigger({ type }, state, triggerRef)

  const { overlayProps: positionProps } = useOverlayPosition({
    targetRef: triggerRef,
    overlayRef,
    placement,
    offset,
    isOpen: state.isOpen,
    boundaryElement,
    containerPadding,
    crossOffset,
    onClose,
    scrollRef,
    shouldFlip,
    shouldUpdatePosition,
  })

  const { buttonProps } = useButton({ onPress: state.toggle }, triggerRef)

  return (
    <Fragment>
      <PressResponder {...triggerProps} {...buttonProps} isPressed={state.isOpen}>
        {typeof trigger === 'function'
          ? trigger()
          : cloneElement(trigger as ReactElement, { ref: triggerRef, ...triggerProps })}
      </PressResponder>
      {state.isOpen && (
        <OverlayContainer>
          <AriaPopoverElement
            {...overlayProps}
            {...positionProps}
            ref={overlayRef}
            popoverRef={overlayRef}
            title={title}
            isOpen={state.isOpen}
            onClose={state.close}
          >
            {typeof content === 'function' ? content(state) : content}
          </AriaPopoverElement>
        </OverlayContainer>
      )}
    </Fragment>
  )
}

export const Popover = AriaPopover
