import { ComponentProps, useState } from 'react'
import { Image } from '../image'
import { Box } from '../layout'
import { styled, theme } from '../theme/default'

export function getColorFromHash(hashString: string) {
  const str = String(hashString)
  let hash = 0
  let char: number
  if (str.trim().length === 0) return hash
  for (let i = 0; i < str.length; i++) {
    char = str.charCodeAt(i)
    hash = (hash << 5) - hash + char
    hash &= hash
  }

  return Math.abs(hash)
}

function getIntials(name: string, { fallback = '?', initials = 1 }: { fallback?: string; initials?: number } = {}) {
  if (!name || typeof name !== 'string') return fallback

  return name
    .replace(/\s+/, ' ')
    .split(' ')
    .slice(0, initials)
    .map(v => v && v[0].toUpperCase())
    .join('')
}

const AvatarFactory = styled('div', {
  borderRadius: '$round',
  justifyContent: 'center',
  alignItems: 'center',
  flexShrink: 0,
  display: 'inline-flex',
  overflow: 'hidden',
  userSelect: 'none',
})

export type AvatarProps = ComponentProps<typeof AvatarFactory> &
  Pick<ComponentProps<typeof Image>, 'src'> & {
    /**
     * This size corresponds somewhat to the theme scale
     */
    size?: keyof typeof theme['sizes'] | (number & {})

    /**
     * Used for initials and title
     * @since 1.0
     */
    name: string

    /**
     * Override the hash value for the color.
     * @note Only for advanced usage
     * @since 1.0
     */
    hash?: string

    /**
     * How many initials to use
     * @since 1.0
     * @default 2
     */
    initials?: 1 | 2
  }

export const avatarColors = ['steel', 'jade', 'yellow', 'coral', 'red', 'purple']

/**
 * The `Avatar` component is used to represent users. And should only be used for users.
 * @since 1.0
 * @author Evan Hennessy
 */
export function Avatar({ name, color, hash, src, size = 10, initials = 2, ...props }: AvatarProps) {
  const [imageFailed, setImageFailed] = useState(false)
  const imageUnavailable = !src || imageFailed
  const initialsText = getIntials(name, { initials })

  const hashValue = getColorFromHash(hash || name)
  const hashColor = color ? `$${color}` : theme.colors[avatarColors[hashValue % avatarColors.length]]?.value

  size = Number(size)

  return (
    <AvatarFactory
      title={name}
      css={{ backgroundColor: hashColor ?? '$gray3', size: `${size * 5}px`, maxSize: '500px', minSize: '25px' }}
      {...props}
    >
      {imageUnavailable ? (
        <Box
          as="span"
          aria-hidden="true"
          role="presentation"
          css={{
            color: '$white',
            fontWeight: size <= 15 ? '$medium' : '',

            capsize: {
              fontSize: (size * 5) / 3,
              lineGap: 0,
            },
          }}
        >
          {initialsText}
        </Box>
      ) : (
        <Image
          alt=""
          css={{ objectFit: 'cover', objectPosition: 'center', width: '100%', height: '100%' }}
          src={src}
          onError={() => setImageFailed(true)}
        />
      )}
    </AvatarFactory>
  )
}