import cardValidator from 'card-validator'
import { useMemo } from 'react'
import { CreditCardField, UseValidationInputType, UseValidationReturnType } from './types'

export function useCardValidator(values: Partial<UseValidationInputType>): UseValidationReturnType {
  const number = useMemo(
    () => cardValidator.number(values[CreditCardField.NUMBER] ?? ''),
    [values[CreditCardField.NUMBER]],
  )

  const expiry = useMemo(
    () => cardValidator.expirationDate(values[CreditCardField.EXPIRY] ?? ''),
    [values[CreditCardField.EXPIRY]],
  )

  const cvc = useMemo(
    () => cardValidator.cvv(values[CreditCardField.CODE] ?? '', number?.card?.code?.size ?? 3),
    [values[CreditCardField.CODE]],
  )

  const name = useMemo(
    () => cardValidator.cardholderName(values[CreditCardField.NAME] ?? ''),
    [values[CreditCardField.NAME]],
  )

  const result = useMemo(
    () => ({
      [CreditCardField.NUMBER]: number,
      [CreditCardField.EXPIRY]: expiry,
      [CreditCardField.CODE]: cvc,
      [CreditCardField.NAME]: name,
    }),
    [number, expiry, cvc, name],
  )

  return result
}
