import { useState } from 'react'
import {
  BasicConfig,
  Builder,
  BuilderProps,
  Config,
  Conjunctions,
  FieldOrGroup,
  Funcs,
  ImmutableTree,
  JsonTree,
  LocaleSettings,
  Operator,
  Query,
  Settings,
  Types,
  Utils,
  Widget,
} from 'react-awesome-query-builder'
import { useDebounce } from 'react-use'
import { useConfirm } from '../confirmDialog'
import { queryBuilderStyles } from './queryBuilderStyles'
import {
  QueryBuilderBoolean,
  QueryBuilderButton,
  QueryBuilderButtonGroup,
  QueryBuilderConfirmModal,
  QueryBuilderConjunctions,
  QueryBuilderDate,
  QueryBuilderFieldSelect,
  QueryBuilderMultiSelect,
  QueryBuilderNumber,
  QueryBuilderText,
  QueryBuilderValueSelect,
} from './widgets'

const { uuid, checkTree, loadTree, getTree } = Utils

const conjunctions: Conjunctions = {
  AND: BasicConfig.conjunctions.AND,
  OR: BasicConfig.conjunctions.OR,
}

const OPERATOR_LABELS: Partial<Record<keyof BasicConfig['operators'], string>> = {
  like: 'contains',
  not_like: 'does not contain',
  equal: 'is',
  not_equal: 'is not',
  is_empty: 'is blank',
  is_not_empty: 'is not blank',
  select_equals: 'is',
  select_not_equals: 'is not',
  select_any_in: 'is one of',
  select_not_any_in: 'is not any of',
  greater: 'is greater than',
  greater_or_equal: 'is greater or equal to',
  less: 'is less than',
  less_or_equal: 'is less or equal to',
  between: 'is between',
  not_between: 'is not between',
  starts_with: 'starts with',
  ends_with: 'ends with',
}

const operators: Partial<Record<keyof typeof BasicConfig.operators, Operator>> = {
  ...Object.entries(OPERATOR_LABELS).reduce((obj, [key, label]) => {
    obj[key] = { ...BasicConfig.operators[key], label }
    return obj
  }, {}),
}

const widgets: Record<keyof typeof BasicConfig.widgets, Widget> = {
  ...BasicConfig.widgets,
  boolean: {
    ...BasicConfig.widgets.boolean,
    factory: QueryBuilderBoolean,
    labelNo: 'false',
    labelYes: 'true',
  },
  date: {
    ...BasicConfig.widgets.date,
    factory: QueryBuilderDate,
    dateFormat: 'DD.MM.YYYY',
    valueFormat: 'YYYY-MM-DD',
  },
  time: {
    ...BasicConfig.widgets.time,
    factory: QueryBuilderDate,
    timeFormat: 'HH:mm',
    valueFormat: 'HH:mm:ss',
  },
  datetime: {
    ...BasicConfig.widgets.datetime,
    factory: QueryBuilderDate,
    timeFormat: 'HH:mm',
    dateFormat: 'DD.MM.YYYY',
    valueFormat: 'YYYY-MM-DD HH:mm:ss',
  },
  select: {
    ...BasicConfig.widgets.select,
    factory: QueryBuilderValueSelect,
  },
  multiselect: {
    ...BasicConfig.widgets.multiselect,
    factory: QueryBuilderMultiSelect,
  },
  number: {
    ...BasicConfig.widgets.number,
    factory: QueryBuilderNumber,
  },
  text: {
    ...BasicConfig.widgets.text,
    factory: QueryBuilderText,
  },
} as const

export type Fields = {
  [key: string]: FieldOrGroup & { type: keyof typeof BasicConfig.widgets }
}

const types: Types = {
  ...BasicConfig.types,
}

const localeSettings: LocaleSettings = {
  locale: {
    moment: 'en',
  },
  valueLabel: 'Value',
  valuePlaceholder: 'Value',
  fieldLabel: 'Field',
  operatorLabel: 'Operator',
  fieldPlaceholder: 'Select field',
  operatorPlaceholder: 'Select operator',
  deleteLabel: null,
  addGroupLabel: 'Add group',
  addRuleLabel: 'Add rule',
  addSubRuleLabel: 'Add sub rule',
  delGroupLabel: null,
  notLabel: 'Not',
  valueSourcesPopupTitle: 'Select value source',
  removeRuleConfirmOptions: {
    title: 'Are you sure you want to delete this rule?',
    okText: 'Yes',
    okType: 'danger',
  },
  removeGroupConfirmOptions: {
    title: 'Are you sure you want to delete this group?',
    okText: 'Yes',
    okType: 'danger',
  },
}

const settings: Settings = {
  ...BasicConfig.settings,
  ...localeSettings,

  valueSourcesInfo: {
    value: {
      label: 'Value',
    },
    field: {
      label: 'Field',
      widget: 'field',
    },
    func: {
      label: 'Function',
      widget: 'func',
    },
  },

  canReorder: false,
  canRegroup: false,
  showLabels: false,
  maxNesting: 3,
  canLeaveEmptyGroup: true,
  groupActionsPosition: 'bottomLeft',

  delGroupLabel: 'Delete Group',
  deleteLabel: 'Delete',

  useConfirm: useConfirm as () => Function,
  renderConfirm: QueryBuilderConfirmModal,

  renderButton: QueryBuilderButton,
  renderButtonGroup: QueryBuilderButtonGroup,
  renderConjs: QueryBuilderConjunctions,
  renderField: QueryBuilderFieldSelect,
  renderOperator: QueryBuilderFieldSelect,
  renderFunc: QueryBuilderFieldSelect,
}

const funcs: Funcs = {}

const BASE_CONFIG: Config = {
  conjunctions,
  operators,
  widgets,
  types,
  settings,
  funcs,
  fields: {},
}

const EMPTY_VALUE: JsonTree = {
  id: uuid(),
  type: 'group',
}

type QueryBuilderProps = {
  initialValue?: JsonTree
  fields: Fields
  settings?: Partial<Settings>
  onChange?: ({}: { immutableTree: ImmutableTree; config: Config; jsonTree: JsonTree }) => void
}

export function QueryBuilder({ initialValue, fields, onChange, settings = {} }: QueryBuilderProps) {
  const config = {
    ...BASE_CONFIG,
    settings: { ...BASE_CONFIG.settings, ...settings },
    fields: { ...BASE_CONFIG.fields, ...fields },
  }
  const [state, setState] = useState({ tree: checkTree(loadTree(initialValue ?? EMPTY_VALUE), config), config })

  useDebounce(
    () => {
      const { tree: immutableTree, config } = state
      const jsonTree = getTree(immutableTree)
      onChange?.({ immutableTree, config, jsonTree })
    },
    200,
    [state],
  )

  const onBuilderChange = (immutableTree: ImmutableTree, config: Config) => {
    setState({ tree: immutableTree, config })
  }

  const renderBuilder = (props: BuilderProps) => <Builder {...props} />

  return (
    <div className={queryBuilderStyles()}>
      <Query {...config} value={state.tree} onChange={onBuilderChange} renderBuilder={renderBuilder} />
    </div>
  )
}
