import { remove as removeDiacritics } from 'diacritics'

// https://developer.mozilla.org/en/docs/Web/JavaScript/Guide/Regular_Expressions#Using_special_characters
const SPECIAL_CHARS_REGEX = /[.*+?^${}()|[\]\\]/g

// http://www.ecma-international.org/ecma-262/5.1/#sec-15.10.2.6
const WORD_CHARACTER_REGEX = /[a-z0-9_]/i

const WHITESPACES_REGEX = /\s+/

function escapeRegexCharacters(string: string) {
  return string.replace(SPECIAL_CHARS_REGEX, '\\$&')
}

type MatchOptions = {
  /** @default false */
  findAllOccurrences?: boolean
  /** @default true */
  insideWords?: boolean
  /** @default false */
  requireMatchAll?: boolean
}

type Match = [number, number]

export function match(text: string, query: string, options?: MatchOptions): Match[] {
  options = {
    insideWords: true,
    findAllOccurrences: true,
    requireMatchAll: false,
    ...options,
  }

  text = removeDiacritics(text)
  query = removeDiacritics(query)

  return (
    query
      .trim()
      .split(WHITESPACES_REGEX)
      // If query is blank, we'll get empty string here, so let's filter it out.
      .filter(function (word) {
        return word.length > 0
      })
      .reduce(function (result, word) {
        const wordLen = word.length
        const prefix = !options.insideWords && WORD_CHARACTER_REGEX.test(word[0]) ? '\\b' : ''
        const regex = new RegExp(prefix + escapeRegexCharacters(word), 'i')
        let occurrence = regex.exec(text)
        let index: number

        if (options.requireMatchAll && occurrence === null) {
          text = ''
          return []
        }

        while (occurrence) {
          index = occurrence.index
          result.push([index, index + wordLen])

          // Replace what we just found with spaces so we don't find it again.
          text = text.slice(0, index) + new Array(wordLen + 1).join(' ') + text.slice(index + wordLen)

          if (!options.findAllOccurrences) {
            break
          }

          occurrence = regex.exec(text)
        }

        return result
      }, [])
      .sort(function (match1, match2) {
        return match1[0] - match2[0]
      })
  )
}

export type ParsePart = {
  text: string
  highlight: boolean
}

export function parse(text: string, matches: Match[]): ParsePart[] {
  const result = []

  if (matches.length === 0) {
    result.push({
      text: text,
      highlight: false,
    })
  } else {
    if (matches[0][0] > 0) {
      result.push({
        text: text.slice(0, matches[0][0]),
        highlight: false,
      })
    }
  }

  matches.forEach(function (match, i) {
    const startIndex = match[0]
    const endIndex = match[1]

    result.push({
      text: text.slice(startIndex, endIndex),
      highlight: true,
    })

    if (i === matches.length - 1) {
      if (endIndex < text.length) {
        result.push({
          text: text.slice(endIndex, text.length),
          highlight: false,
        })
      }
    } else if (endIndex < matches[i + 1][0]) {
      result.push({
        text: text.slice(endIndex, matches[i + 1][0]),
        highlight: false,
      })
    }
  })

  return result
}

export function highlightToParts(text: string, query: string, options?: MatchOptions) {
  return parse(text, match(text, query, options))
}
