import type { ComponentProps } from 'react'
import { Pre } from '../typography/Pre'
import { styled } from '../theme/default'
import { Heading } from '../typography'

const StyledDebugBlock = styled('div', {
  backgroundColor: '$gray6',
  padding: '$3',
  color: '$primary',
  border: '1px solid',
  borderColor: '$primaryTint',
  borderRadius: '$2',
  display: 'flex',
  flexDirection: 'column',
  gap: '$1',

  variants: {
    floating: {
      left: {
        position: 'fixed',
        bottom: '$2',
        left: '$2',
      },
      right: {
        position: 'fixed',
        bottom: '$2',
        right: '$2',
      },
    },
  },
})

export function DebugBlock({ object, title, ...props }: { object: object } & ComponentProps<typeof StyledDebugBlock>) {
  return (
    <StyledDebugBlock {...props}>
      {title && <Heading size="groupHeadline">{title}</Heading>}
      <Pre>{JSON.stringify(object, null, 3)}</Pre>
    </StyledDebugBlock>
  )
}
