import { ComponentProps, Key } from 'react'
import { Node } from '@react-types/shared'
import { useMenuSection, useSeparator } from 'react-aria'
import { Section, TreeState } from 'react-stately'
import { _MenuItem } from './MenuItem'
import { styled } from '../theme/default'
import { Heading } from '../typography'
import { MenuDivider } from './MenuDivider'

const MenuGroupFactory = styled('li', {
  paddingBlock: '$1',
  padding: 0,
  listStyle: 'none',
})

type MenuGroupProps = ComponentProps<typeof MenuGroupFactory> & {
  /** @private */
  section: Node<any>
  /** @private */
  state?: TreeState<any>

  onAction?: (key: Key) => void
}

export function _MenuGroup({ section, state, onAction }: MenuGroupProps) {
  let { itemProps, headingProps, groupProps } = useMenuSection({
    heading: section.rendered,
    'aria-label': section['aria-label'],
  })

  let { separatorProps } = useSeparator({
    elementType: 'li',
  })

  return (
    <>
      {section.key !== state.collection.getFirstKey() && (
        <MenuDivider
          {...separatorProps}
          style={{
            borderTop: '1px solid $colors$gray4',
            margin: '2px 5px',
          }}
        />
      )}
      <li {...itemProps}>
        {section.rendered && (
          <Heading size="groupHeadline" {...headingProps} css={{ padding: '$2', color: '$secondaryLabel' }}>
            {section.rendered}
          </Heading>
        )}
        <MenuGroupFactory {...groupProps}>
          {[...section.childNodes].map(node => (
            <_MenuItem
              as={node.props.as}
              key={node.key}
              item={node}
              state={state}
              onAction={onAction}
              {...node.props}
            />
          ))}
        </MenuGroupFactory>
      </li>
    </>
  )
}

/** @deprecated Use `Section` instead */
export const MenuGroup = Section
