type ButtonFactoryProps = Omit<ComponentProps<typeof ButtonFactory>, 'icon' | 'onClick'>

export type ButtonProps = ButtonFactoryProps & {
  /**
   * Sets a loading state on a button which disables and shows a spinner.
   * Useful for async operations.
   * @since 1.0
   */
  loading?: boolean

  /** Force active state. */
  isPressed?: boolean

  /**
   * Label used when button is in loading state
   * Set as false to keep text
   * @default 'Loading...'
   */
  loadingLabel?: string | boolean

  /** @deprecated Use onPress instead */
  onClick?: (e: MouseEvent<HTMLButtonElement>) => void

  onPress?: (e: MouseEvent<HTMLButtonElement>) => void

  // These are deprecated
  /** @deprecated will be removed in 1.0 */
  outline?: true
  /**
   * Adds an icon before the `Button` content
   * @since 1.0
   */
  iconBefore?: ReactNode
  /**
   * Adds an icon before the `Button` content
   * @since 1.0
   */
  iconAfter?: ReactNode
  /**
   * Button content becomes this icon only.
   * If you want text, use `iconBefore` or `iconAfter`
   * @since 1.0
   */
  icon?: ReactNode
  'aria-label'?: string
}

const iconSizes = {
  small: '$2',
  regular: '$3',
  large: '$3',
}

const spinnerSizes = {
  small: '$3',
  regular: '$4',
  large: '$4',
}
const textSizes = {
  small: 2,
  regular: 3,
  large: 4,
}

// TODO: Remove omit fix when this is merged https://github.com/modulz/stitches/issues/421
type ButtonComponent = Polymorphic.ForwardRefComponent<typeof ButtonFactory, ButtonProps>

/**
 * The `Button` component is the most common button component.
 * It contains a label and optional icons before or after the label.
 * @since 1.0
 * @author Evan Hennessy
 * @example ```
 * <Button color="primary">Click Me</Button>
 * ```
 */
export const Button = forwardRef<HTMLButtonElement, ButtonProps>(
  (
    { loadingLabel = 'Loading...', children, disabled, onClick, onPress = onClick, ...props }: ButtonProps,
    forwardedRef,
  ) => {
    const ref = useRef<HTMLButtonElement>()
    const { buttonProps, isPressed } = useButton({ onPress, isDisabled: disabled, ...(props as any) }, ref)

    let { iconBefore: IconBefore } = props
    let { iconAfter: IconAfter } = props
    const { icon: Icon } = props

    let iconSize = iconSizes[props.size as string]

    if (props.loading) {
      IconBefore = Spinner
      children = loadingLabel ? loadingLabel : children
      props.color = undefined
      iconSize = spinnerSizes[props.size as string]
    }

    if (props.intent) {
      delete props.color
    }

    if (Icon) {
      IconBefore = null
      IconAfter = null
      props.css = Object.assign({}, props.css, { padding: '$2 $1' })
    }

    return (
      <ButtonFactory
        {...mergeProps(props, buttonProps)}
        icon={!!Icon}
        isDisabled={buttonProps.disabled}
        isPressed={isPressed}
        ref={mergeRefs(forwardedRef, ref)}
        whileTap={{ scale: 0.9999999999 }}
      >
        {typeof IconBefore === 'function' ? (
          <IconBefore size={iconSize} css={{ marginLeft: '-$1', marginRight: '$2' }} />
        ) : (
          IconBefore || null
        )}
        {Icon ? (
          typeof Icon === 'function' ? (
            <Icon size={iconSize} css={{ marginLeft: '$1', marginRight: '$1' }} />
          ) : (
            Icon
          )
        ) : (
          <Text css={{ fontWeight: '$medium', capsize: { fontSize: fontPxToNum(textSizes[props.size as string]) } }}>
            {children}
          </Text>
        )}
        {typeof IconAfter === 'function' ? (
          <IconAfter size={iconSize} css={{ marginLeft: '$2', marginRight: '-$1' }} />
        ) : (
          IconAfter || null
        )}
      </ButtonFactory>
    )
  },
) as ButtonComponent

Button.toString = ButtonFactory.toString

Button.displayName = 'Button'

Button.defaultProps = {
  type: 'button',
  color: 'primary',
  size: 'regular',
}
