import { ConfirmModalProps } from 'react-awesome-query-builder'
import type { ConfirmDialogOptions } from '../../confirmDialog/types'

export function QueryBuilderConfirmModal({
  okText: confirmLabel = 'OK',
  onOk,
  title = 'Are you sure?',
  cancelText: cancelLabel = 'Cancel',
  confirmFn,
}: ConfirmModalProps & { confirmFn: (props: ConfirmDialogOptions) => Promise<void> }) {
  confirmFn({
    title: null,
    children: title,
    confirmLabel,
    cancelLabel,
    intent: 'danger',
    dialogProps: { contentContainerProps: { css: { textAlign: 'center' } } },
  })
    .then(onOk)
    .catch(() => {})
}
