import { AnimatePresence, motion } from 'framer-motion'
import { createContext, useContext, useState } from 'react'
import { OverlayContainer } from 'react-aria'
import { Box, StackingProvider } from '../layout'
import { StackingOrder } from '../theme/default'
import { Text } from '../typography'
import { Toast } from './Toast'
import { ToastConfig, ToasterProps, ToastOffsetConfig } from './types'

const ToasterContext = createContext<{
  add: (toast: ToastConfig) => void
  remove: (id: ToastConfig['id']) => void
  closeAll: () => void
  toasts: ToastConfig[]
}>(null)

ToasterContext.displayName = 'ToasterContext'

/**
 * You need to add a Toaster to the root of your app to be able to pop toasts
 */
export function Toaster({ children, position = 'left', offset = '$2', defaultDuration = 5000 }: ToasterProps) {
  const [toasts, setToasts] = useState<ToastConfig[]>([])

  let toastOffset: ToastOffsetConfig = (() => {
    if (typeof offset === 'string') {
      return { top: offset, side: offset }
    } else {
      return { top: offset.top || '$2', side: offset.side || '$2' }
    }
  })()

  const add = (toast: ToastConfig) => {
    if (toast.id) {
      if (toasts.find(t => t.id === toast.id)) {
        return
      }
    } else {
      toast.id = +new Date()
    }

    toast.isShown = true

    setToasts([...toasts, toast])
  }

  const remove = (id: ToastConfig['id']) => {
    setToasts(toasts.filter(t => t.id !== id))
  }

  // Close toast first so it will be removed when the animation ends
  const close = (id: ToastConfig['id']) => {
    setToasts(toasts.map(toast => (toast.id === id ? { ...toast, isShown: false } : toast)))
  }

  const closeAll = () => {
    setToasts([])
  }

  const positionProps = {
    left: { left: toastOffset.side, alignItems: 'flex-start' },
    right: { right: toastOffset.side, alignItems: 'flex-end' },
    center: { transform: 'translateX(-50%)', left: '50%', alignItems: 'center' },
  }

  const motionProps = {
    left: {
      exit: { x: '-100%', height: 0, opacity: 0 },
      initial: { x: '-100%' },
      animate: { x: '0%' },
    },
    right: {
      exit: { x: '100%', height: 0, opacity: 0 },
      initial: { x: '100%' },
      animate: { x: '0%' },
    },
    center: {
      exit: { scale: 0.75, height: 0, opacity: 0 },
      initial: { y: '-100%' },
      animate: { y: '0%' },
    },
  }

  const onRemove = (id: ToastConfig['id']) => () => {
    close(id)
    remove(id)
  }

  return (
    <ToasterContext.Provider value={{ add, remove, closeAll, toasts }}>
      {children}
      <StackingProvider value={StackingOrder.TOASTER}>
        {zIndex => (
          <OverlayContainer>
            <Box
              css={{
                position: 'fixed',
                display: 'flex',
                flexDirection: 'column',
                height: 0,
                top: toastOffset.top,
                zIndex,
                ...positionProps[position],
              }}
            >
              <AnimatePresence initial={false}>
                {toasts.map(({ id, description, isShown, ...toast }) =>
                  isShown ? (
                    <motion.div
                      key={id}
                      layout
                      transition={{
                        type: 'spring',
                        damping: 20,
                        stiffness: 250,
                      }}
                      {...motionProps[position]}
                    >
                      <Toast css={{ marginBottom: '$1' }} duration={defaultDuration} {...toast} onRemove={onRemove(id)}>
                        <Text
                          size="caption1"
                          css={{ color: '$secondary', maxWidth: '300px', capsize: { fontSize: 14, lineGap: 5 } }}
                        >
                          {description}
                        </Text>
                      </Toast>
                    </motion.div>
                  ) : null,
                )}
              </AnimatePresence>
            </Box>
          </OverlayContainer>
        )}
      </StackingProvider>
    </ToasterContext.Provider>
  )
}

export const useToaster = () => useContext(ToasterContext)
