import { ComponentProps, Ref, forwardRef, useEffect, useState } from 'react'
import { styled } from '../theme/default'

export const ImageFactory = styled('img', {
  variants: {
    defaultFallback: {
      true: {
        backgroundColor: '$gray3',
        width: '100%',
      },
    },
  },
})

type ImageProps = ComponentProps<typeof ImageFactory> & {
  /**
   * Uses a fallback image in the case of an error loading the image
   */
  fallback?: ImageProps['src']
}

export const Image = forwardRef((props: ImageProps, ref: Ref<HTMLImageElement>) => {
  const [imageFailed, setImageFailed] = useState(false)
  const [imageSource, setImageSource] = useState(props.src)

  useEffect(() => {
    if (imageFailed && props.fallback) {
      setImageSource(props.fallback)
    }
  }, [imageFailed])

  return (
    <ImageFactory
      ref={ref}
      {...props}
      src={imageSource}
      defaultFallback={imageFailed && !props.fallback}
      onError={e => {
        props.onError?.(e)
        setImageFailed(true)
      }}
    />
  )
})

Image.displayName = 'Image'

Image.defaultProps = {
  loading: 'lazy',
  alt: '',
}