import { Node } from '@react-types/shared'
import { ReactNode, useRef } from 'react'
import { mergeProps, useFocusRing, useOption } from 'react-aria'
import { ComboBoxState, ListState } from 'react-stately'
import { styled } from '../theme/default'
import { Text } from '../typography'
import { highlightToParts, ParsePart } from '../utils/highlighter'

const OptionFactory = styled('li', {
  padding: '$2',
  outline: 'none',
  cursor: 'pointer',
  userSelect: 'none',
  transition: 'background-color $fast',
  width: '100%',
  display: 'flex',
  placeContent: 'center',
  flexDirection: 'column',
  gap: '$1',
  color: '$label',

  mark: {
    backgroundColor: 'hsl($hsl_primary / 0.3)',
    color: 'inherit',
  },

  variants: {
    size: {
      small: {
        padding: '$1 $2',
        minHeight: '$controlHeightSmall',
      },
      regular: {
        padding: '$2 $3',
        minHeight: '$controlHeightRegular',
      },
      large: {
        padding: '$3 $4',
        minHeight: '$controlHeightLarge',
      },
    },

    isFocusVisible: {
      true: {
        backgroundColor: '$gray4',
      },
    },

    isFocused: {
      true: { backgroundColor: '$gray4' },
    },

    isSelected: {
      true: {
        backgroundColor: '$primary',

        '&, span': {
          color: '$onPrimary',
        },

        '@supports (hsl(from $primary h s calc(l - 10%)))': {
          '&:hover': {
            backgroundColor: 'hsl(from $primary h s calc(l - 10%))',
          },
        },
      },
      false: {
        backgroundColor: 'transparent',

        '&:hover': {
          backgroundColor: '$gray5',
        },
      },
    },

    isDisabled: {
      true: {
        backgroundColor: '$gray6',
        color: '$gray2',
        cursor: 'not-allowed',

        '&:hover': {
          backgroundColor: '$gray6',
        },
      },
    },
  },

  compoundVariants: [
    {
      isFocusVisible: true,
      isSelected: true,
      css: {
        backgroundColor: 'hsl($hsl_primary / 0.8)',
      },
    },
  ],

  defaultVariants: { size: 'regular' },
})

type OptionProps = {
  item: Node<any> & { showHighlight?: boolean }
  state: ListState<any> | ComboBoxState<any>
  showHighlight?: boolean
}

export function Option({ item, state, showHighlight }: OptionProps) {
  const ref = useRef()
  let matchedWords: ParsePart[] = []
  let highlightedText: ReactNode

  if ('inputValue' in state && showHighlight) {
    matchedWords = highlightToParts(item.textValue, state.inputValue)
    if (matchedWords.length > 0) {
      highlightedText = matchedWords.map(part => (part.highlight ? <mark>{part.text}</mark> : <>{part.text}</>))
    }
  }

  const { optionProps, isSelected, isDisabled, isFocused, descriptionProps, labelProps } = useOption(
    { key: item.key },
    state,
    ref,
  )

  const { isFocusVisible, focusProps } = useFocusRing()

  return (
    <OptionFactory
      {...mergeProps(optionProps, focusProps)}
      isFocusVisible={isFocusVisible}
      isSelected={isSelected}
      isDisabled={isDisabled}
      isFocused={isFocused}
      ref={ref}
    >
      {typeof item.rendered === 'string' ? (
        <Text lineClamp {...labelProps}>
          {highlightedText ?? item.rendered}
        </Text>
      ) : (
        <div {...labelProps} style={{ display: 'contents' }}>
          {typeof item.rendered === 'function' ? item.rendered({ ...item, matchedWords }) : item.rendered}
        </div>
      )}

      {item.props?.description && (
        <Text size="caption2" css={{ color: '$gray1' }} lineClamp {...descriptionProps}>
          {item.props.description}
        </Text>
      )}
    </OptionFactory>
  )
}
