import { CSS } from '@stitches/react'
import { AnimatePresence, motion } from 'framer-motion'
import { noop } from 'lodash-es'
import { ComponentProps, ReactNode, useEffect, useRef, useState } from 'react'
import { OverlayContainer, usePreventScroll } from 'react-aria'
import { Key } from 'w3c-keys'
import { StackingOrder, styled } from '../theme/default'
import { StackingProvider } from './StackingProvider'
export type OverlayProps = {
  /**
   * Control whether overlay should be shown. Will have to
   */
  isShown?: boolean
  /**
   * Boolean indicating if clicking the overlay should close the overlay.
   */
  shouldCloseOnClick?: boolean
  /**
   * Boolean indicating if pressing escape should close the overlay.
   */
  shouldCloseOnEscapePress?: boolean
  /**
   * Boolean indicating whether to prevent scrolling beneath the overlay
   */
  preventBodyScroll?: boolean
  /**
   * Called when overlay is hidden
   */
  onHide?: (overlayRef?: HTMLDivElement) => void
  /**
   * Called when overlay is shown
   */
  onShow?: (overlayRef?: HTMLDivElement) => void
  children?: ((close?: () => void) => ReactNode) | ReactNode
  containerCSS?: ComponentProps<typeof OverlayInner>['css']
}

const OverlayInner = styled(motion.div, {
  transition: '$normal',
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,
  position: 'fixed',

  '&::before': {
    filter: 'brightness(25%)',
    backgroundColor: '$overlay',
    left: 0,
    top: 0,
    position: 'fixed',
    display: 'block',
    width: `100%`,
    height: '100%',
    content: '""',
  },
})

export function Overlay({
  isShown: initShown,
  onShow,
  onHide,
  shouldCloseOnClick,
  shouldCloseOnEscapePress,
  children,
  containerCSS,
  preventBodyScroll,
}: OverlayProps) {
  const [isShown, setShown] = useState(initShown)
  const overlayRef = useRef<HTMLDivElement>(null)

  usePreventScroll({ isDisabled: !preventBodyScroll || !isShown })

  useEffect(() => {
    setShown(initShown)
  }, [initShown])

  useEffect(() => {
    if (isShown) {
      document.body.addEventListener('keydown', onEscape, false)
      onShow(overlayRef.current)
    } else {
      document.body.removeEventListener('keydown', onEscape, false)
      onHide(overlayRef.current)
    }
  }, [isShown])

  const onEscape = (e: KeyboardEvent) => {
    if (e.key === Key.Escape && shouldCloseOnEscapePress) {
      close()
    }
  }

  const close = () => {
    onHide(overlayRef.current)
    setShown(false)
  }

  return (
    <StackingProvider value={StackingOrder.OVERLAY}>
      {zIndex => (
        <OverlayContainer>
          <AnimatePresence>
            {isShown && (
              <OverlayInner
                initial={{ opacity: 0 }}
                animate={{ opacity: 1, transition: { delayChildren: 0.1, duration: 0.25 } }}
                exit={{ opacity: 0 }}
                transition={{ type: 'tween' }}
                onClick={e => {
                  if (e.target !== e.currentTarget || !shouldCloseOnClick) {
                    return
                  }
                  close()
                }}
                ref={overlayRef}
                css={{
                  $$zIndex: zIndex,
                  zIndex,
                  '> *': { zIndex: zIndex + 1 },
                  ...(containerCSS as CSS),
                }}
              >
                {typeof children === 'function' ? children(close) : children}
              </OverlayInner>
            )}
          </AnimatePresence>
        </OverlayContainer>
      )}
    </StackingProvider>
  )
}

Overlay.defaultProps = {
  shouldCloseOnClick: true,
  shouldCloseOnEscapePress: true,
  onShow: noop,
  onHide: noop,
  preventBodyScroll: true,
  isShown: false,
  containerCSS: {},
}
