import { MouseEvent, PropsWithChildren } from 'react'
import { Row } from 'react-table'
import { StyledTableRow } from './TableElements'
import { TableProps } from './types/TableProps'

export function TableRow({
  children,
  onRowPress,
  row,
  ...props
}: PropsWithChildren<Pick<TableProps, 'onRowPress'>> & { row: Row }) {
  const blacklistedTargetTypes = ['BUTTON', 'INPUT', 'LABEL', 'A']

  const rowProps = onRowPress
    ? {
        ...props,
        tabIndex: 0,
        hasRowAction: true,
        onClick: (e: MouseEvent<HTMLTableRowElement> & { target: EventTarget & HTMLElement }) => {
          if (
            blacklistedTargetTypes.includes(e.target.tagName) ||
            !!e.target.closest('menu') ||
            !!e.target.onclick ||
            !!e.target.onselect
          ) {
            e.stopPropagation()
            return
          }
          onRowPress(row)
        },
      }
    : props

  return <StyledTableRow {...rowProps}>{children}</StyledTableRow>
}
