import { ComponentProps } from 'react'
import { useTimeoutFn } from 'react-use'
import { Alert } from '../alert'
import { styled } from '../theme/default'
import { FeatureFlag, useFeatureFlag } from '../theme/featureFlags'
import { ToastConfig } from './types'

const ToastFactory = styled(Alert, {
  boxShadow: '$2',
  borderRadius: '$2',
  pointerEvents: 'all',
  flexShrink: 0,
})

type ToastProps = ComponentProps<typeof Alert> & ToastConfig

/**
 * This is the Toast UI element.
 * You shouldn't use this directly but instead call `toast.danger()`
 * @see {Toaster.tsx}
 */
export function Toast(props: ToastProps) {
  const translucentToast = useFeatureFlag(FeatureFlag.TranslucentToast)
  const toasterDuration = props.duration ?? 5000

  const [_, __, cancel] = useTimeoutFn(() => {
    if (toasterDuration === 'forever') return

    props.onRemove()
  }, Number(toasterDuration))

  if (toasterDuration === 'forever') {
    cancel()
  }

  return <ToastFactory {...props} appearance={translucentToast ? 'toast' : 'card'} />
}

Toast.defaultProps = {
  isRemoveable: true,
  icon: 'none',
}
