import { ReactNode, useRef } from 'react'
import { useButton } from 'react-aria'
import { styled } from '../theme/default'
import { Text } from '../typography/Text'

const OptionFactory = styled('li', {
  padding: '$2',
  outline: 'none',
  cursor: 'pointer',
  userSelect: 'none',
  transition: 'background-color $fast',
  width: '100%',
  display: 'flex',
  placeContent: 'center',
  flexDirection: 'column',
  gap: '$1',

  variants: {
    sticky: {
      bottom: {
        backgroundColor: 'hsl($hsl_background / 0.85)',
        backdropFilter: 'blur(20px) saturate(180%)',
        position: 'sticky',
        bottom: 0,
        borderTop: '1px solid $gray4',
      },
    },

    size: {
      small: {
        padding: '$1 $2',
        height: '$controlHeightSmall',
      },
      regular: {
        padding: '$2 $3',
        height: '$controlHeightRegular',
      },
      large: {
        padding: '$3 $4',
        height: '$controlHeightLarge',
      },
    },

    isFocusVisible: {
      true: {
        backgroundColor: '$gray4',
      },
    },

    isFocused: {
      true: { backgroundColor: '$gray4' },
    },

    isSelected: {
      true: {
        backgroundColor: '$primary',

        '&, span': {
          color: '$onPrimary',
        },

        '@supports (hsl(from $primary h s calc(l - 10%)))': {
          '&:hover': {
            backgroundColor: 'hsl(from $primary h s calc(l - 10%))',
          },
        },
      },
      false: {
        backgroundColor: 'transparent',

        '&:hover': {
          backgroundColor: 'hsl($hsl_gray5 / 0.85)',
        },
      },
    },

    isDisabled: {
      true: {
        backgroundColor: '$gray6',
        color: '$gray2',
        cursor: 'not-allowed',

        '&:hover': {
          backgroundColor: '$gray6',
        },
      },
    },
  },

  compoundVariants: [
    {
      isFocusVisible: true,
      isSelected: true,
      css: {
        backgroundColor: 'hsl($hsl_primary / 0.8)',
      },
    },
  ],

  defaultVariants: { size: 'regular', isSelected: false, isDisabled: false, sticky: 'bottom' },
})

export type ListActionProps = {
  title: string
  rendered?: ReactNode
  onAction: () => void
}

export function ListAction({ onAction, rendered, title }: ListActionProps) {
  const ref = useRef()
  const { buttonProps } = useButton({ onPress: onAction }, ref)

  return (
    <OptionFactory {...(buttonProps as any)} sticky="bottom" ref={ref}>
      <Text lineClamp>{rendered ?? title}</Text>
    </OptionFactory>
  )
}
