import { CSS, VariantProps } from '@stitches/react'
import { motion, useReducedMotion } from 'framer-motion'
import { forwardRef, SVGAttributes, useRef } from 'react'
import { useCheckbox, useFocusRing, usePress, VisuallyHidden } from 'react-aria'
import { mergeRefs } from 'react-laag'
import { useToggleState } from 'react-stately'
import { config, styled } from '../theme/default'
import { Label } from '../typography'

const CheckboxWrapper = styled('svg', {
  $$size: '24px',
  $$strokeWidth: '2px',

  WebkitFontSmoothing: 'antialiased',
  textDecoration: 'none',
  border: 'none',
  outline: 'none',

  size: 'calc($$size + calc($$strokeWidth * 4))',
  transition: '$fast',
  cursor: 'pointer',

  variants: {
    disabled: {
      true: {
        $$fill: '$colors$gray5',

        '&, rect, circle': {
          cursor: 'not-allowed',
        },

        circle: {
          fill: '$colors$gray3',
        },
      },
    },
  },
})

const CheckboxBackground = styled('rect', {
  size: '$$size',
  transition: '$fast',
  fill: 'none',
  strokeWidth: '$$strokeWidth',

  variants: {
    isSelected: {
      true: {
        stroke: '$checkbox',
        fill: '$checkbox',
      },
      false: {
        fill: 'none',
        stroke: '$gray3',

        '&:hover': {
          fill: '$gray5',
        },

        '&:active': {
          fill: '$gray2',
        },
      },
    },

    focused: {
      true: {
        stroke: '$checkbox',
      },
      false: {},
    },

    disabled: {
      true: {
        stroke: '$gray4',
        fill: '$gray5',

        '&:active': {
          fill: '$gray5',
        },
      },
    },
  },

  compoundVariants: [
    {
      isSelected: true,
      focused: true,
      css: {
        stroke: '$checkbox',
      },
    },
  ],
})

const CheckboxOutline = styled(motion.rect, {
  transition: '$fast',
  fill: 'none',
  stroke: '$checkbox',
  size: 'calc($$size + 5px)',
  strokeWidth: '$$strokeWidth',
})

const CheckboxLabel = styled('label', {
  $$checkboxSize: '$sizes$6',
  display: 'flex',
  alignItems: 'center',
  cursor: 'pointer',
  position: 'relative',
  userSelect: 'none',

  variants: {
    labelOffset: {
      true: {
        paddingTop: '$3',
      },
    },

    appearance: {
      default: {
        gap: '$2',
      },

      outline: {
        transition: '$fast',
        padding: '$2 $3',
        boxShadow: 'inset 0 0 0 1px $colors$muted',
        borderRadius: '$1',
        width: '100%',
        justifyContent: 'space-between',

        '&::after': {
          content: '""',
          size: '$$size',
        },
      },
    },

    isSelected: {
      true: {},
      false: {},
    },
  },

  compoundVariants: [
    {
      isSelected: true,
      appearance: 'outline',
      css: {
        boxShadow: 'inset 0 0 0 2px $colors$checkbox',
        span: { color: '$checkbox' },
      },
    },
  ],

  defaultVariants: {
    appearance: 'default',
  },
})

const CheckboxCheck = styled(motion.path, {
  transformOrigin: '50% 50%',
  transform: 'translate(calc(-50% + 9.5px), calc(-50% + 9.5px)) scale(0.6)',
  strokeLinecap: 'round',
  strokeLinejoin: 'round',
  fill: 'none',
  stroke: '$onPrimary',
  strokeWidth: '4px',
})

type CheckboxLabelProps = Omit<VariantProps<typeof CheckboxLabel>, 'isSelected'>

type AriaCheckboxProps = Parameters<typeof useCheckbox>[0]

type CheckboxProps = Omit<AriaCheckboxProps, 'isSelected' | 'defaultSelected'> &
  CheckboxLabelProps & {
    label?: string
    /**
     * State in addition to "checked" and "unchecked".
     * When true, the checkbox displays a "minus" icon.
     */
    indeterminate?: boolean
    /**
     * When true, the aria-invalid attribute is true.
     * Used for accessibility.
     */
    invalid?: string | boolean

    /**
     * When true, the checkbox is disabled.
     */
    disabled?: boolean

    /**
     * When true, the checkbox is required.
     */
    required?: boolean

    /**
     * Whether the element should be selected (uncontrolled).
     */
    defaultChecked?: AriaCheckboxProps['defaultSelected']

    /**
     * Whether the element should be selected (controlled).
     */
    checked?: AriaCheckboxProps['isSelected']

    /** Limits animation of checkbox.
     * This is also done automatically when the browser is in reduced motion.
     */
    reducedMotion?: boolean

    css?: CSS<typeof config>
  }

const checkVariants = {
  hidden: {
    pathLength: 0,
    opacity: 0,
  },
  visible: {
    pathLength: 1,
    opacity: 1,
  },
}

export const Checkbox = forwardRef<HTMLInputElement, CheckboxProps>(
  ({ labelOffset, css, children, appearance = 'default', reducedMotion = false, ...props }, forwardedRef) => {
    const ref = useRef<HTMLInputElement>()
    const shouldReduceMotion = reducedMotion || useReducedMotion()

    const aliasedProps: Partial<AriaCheckboxProps> = {
      isSelected: props.checked,
      defaultSelected: props.defaultChecked,
      isIndeterminate: props.indeterminate,
      isDisabled: props.disabled,
      isRequired: props.required,
    }

    const state = useToggleState({ ...props, ...aliasedProps })

    const { inputProps } = useCheckbox({ ...props, ...aliasedProps, 'aria-label': props?.label, children }, state, ref)
    const { isFocusVisible, isFocused, focusProps } = useFocusRing({ within: true })
    const { pressProps } = usePress({ isDisabled: props.isDisabled })

    return (
      <CheckboxLabel css={css} labelOffset={labelOffset} appearance={appearance} isSelected={state.isSelected}>
        <VisuallyHidden>
          <input {...inputProps} {...focusProps} ref={mergeRefs(ref, forwardedRef)} />
        </VisuallyHidden>

        <CheckboxWrapper
          aria-hidden="true"
          disabled={inputProps.disabled}
          {...(pressProps as unknown as SVGAttributes<SVGSVGElement>)}
        >
          <CheckboxBackground
            rx={3}
            x={4}
            y={4}
            isSelected={state.isSelected}
            disabled={inputProps.disabled}
            focused={isFocusVisible || isFocused}
          />
          <CheckboxCheck
            variants={checkVariants}
            initial={false}
            animate={state.isSelected ? 'visible' : 'hidden'}
            d={props.indeterminate ? 'M14 27l25.5 0' : 'M14.1 27.2l7.1 7.2 16.7-16.8'}
            transition={{
              pathLength: { type: 'spring', duration: shouldReduceMotion ? 0 : 0.4, bounce: 0 },
              opacity: { duration: shouldReduceMotion ? 0 : 0.01 },
            }}
          />
          {(isFocusVisible || isFocused) && state.isSelected && <CheckboxOutline rx={5} x={1.5} y={1.5} />}
        </CheckboxWrapper>
        <Label as="span" size="regular" disabled={props.disabled}>
          {props.label || children}
        </Label>
      </CheckboxLabel>
    )
  },
)

Checkbox.displayName = 'Checkbox'