import { ComponentProps } from 'react'
import { mergeProps } from 'react-aria'
import { Checkbox } from '../checkbox'
import { useFormikHelper } from './useFormikHelper'

/**
 * A helper for using `Checkbox` with formik forms.
 * Automatically sets errors and assigns `onChange` and `checked` based on the field name
 *
 * @throws if not used in a Formik Context
 */

export function FormikCheckboxField(props: ComponentProps<typeof Checkbox>) {
  const [field, _, helpers] = useFormikHelper({ ...props, type: 'checkbox' })

  return (
    <Checkbox
      {...mergeProps(field, props)}
      onChange={value => {
        props?.onChange?.(value)
        helpers.setValue(value)
      }}
    />
  )
}
