import { AnimatePresence, motion } from 'framer-motion'
import { cloneElement, ComponentProps, ReactElement, ReactNode, useEffect, useState } from 'react'
import { DismissButton, FocusScope, OverlayContainer } from 'react-aria'
import { Placement, TriggerProps, useLayer } from 'react-laag'
import { Card, StackingProvider } from '../layout'
import { StackingOrder } from '../theme/default'
import { Paragraph } from '../typography'

export type PopoverProps = ComponentProps<typeof Card> & {
  content: ((props: { close: () => void; isOpen: boolean }) => ReactNode) | ReactNode
  children:
    | ((props: {
        setOpen: (isOpen: boolean) => void
        toggle: () => void
        open: () => void
        close: () => void
        isOpen: boolean
        triggerProps: TriggerProps
      }) => ReactNode)
    | ReactNode
  onClose?: () => void
  placement?: Placement
  showDelay?: number
  hideDelay?: number
  triggerOffset?: number
  containerOffset?: number
  isShown?: boolean
  closeOnClickOutside?: boolean
  closeOnLeaveViewport?: boolean
  automaticPlacement?: boolean
}

export function Popover({
  children,
  content,
  showDelay = 0,
  hideDelay = 125,
  placement = 'bottom-start',
  triggerOffset = 10,
  containerOffset = 15,
  isShown = false,
  closeOnClickOutside = true,
  closeOnLeaveViewport = true,
  automaticPlacement = true,
  onClose,
  ...props
}: PopoverProps) {
  const [isOpen, setOpen] = useState(isShown)

  // helper function to close the menu
  function close() {
    onClose?.()
    setOpen(false)
  }

  function open() {
    setOpen(!isOpen)
  }

  function toggle() {
    setOpen(!isOpen)
  }

  useEffect(() => setOpen(isShown), [isShown])

  const { renderLayer, triggerProps, layerProps, layerSide } = useLayer({
    isOpen,
    container: document?.querySelector<HTMLDivElement>('[data-overlay-container="true"]'),
    onOutsideClick: closeOnClickOutside && close, // close the menu when the user clicks outside
    onDisappear: closeOnLeaveViewport && close, // close the menu when the menu gets scrolled out of sight
    overflowContainer: true, // keep the menu positioned inside the container
    auto: automaticPlacement, // automatically find the best placement
    placement, // we prefer to place the menu "bottom-center"
    triggerOffset, // keep some distance to the trigger
    containerOffset, // give the menu some room to breath relative to the container
    arrowOffset: 16, // let the arrow have some room to breath also,
  })

  return (
    <>
      {typeof children === 'function'
        ? children({ triggerProps, isOpen, setOpen, toggle, open, close })
        : cloneElement(children as ReactElement, { ...triggerProps, isPressed: isOpen, onClick: open })}
      <StackingProvider value={StackingOrder.POSITIONER}>
        {zIndex =>
          renderLayer(
            <AnimatePresence>
              {isOpen && (
                <OverlayContainer>
                  <FocusScope restoreFocus>
                    <Card
                      {...(props as any)}
                      {...layerProps}
                      border
                      elevation={2}
                      role="dialog"
                      as={motion.div}
                      initial={{
                        opacity: 0,
                        y: layerSide === 'center' ? 0 : layerSide === 'top' ? triggerOffset : -triggerOffset,
                        scale: layerSide === 'center' ? 0.8 : 1,
                      }}
                      animate={{ opacity: 1, y: 0, scale: 1 }}
                      exit={{
                        opacity: 0,
                        scale: layerSide === 'center' ? 0.8 : 1,
                        y: layerSide === 'center' ? 0 : layerSide === 'top' ? triggerOffset : -triggerOffset,
                      }}
                      transition={{
                        type: 'spring',
                        stiffness: 800,
                        damping: 25,
                        opacity: { type: 'tween', duration: 0.1 },
                      }}
                      css={{
                        backgroundColor: '$background',
                        overflow: 'hidden',
                        minWidth: '200px',
                        borderRadius: '$2',
                        zIndex,
                        ...(props?.css || {}),
                      }}
                    >
                      {typeof content === 'string' ? (
                        <Paragraph>{content}</Paragraph>
                      ) : typeof content === 'function' ? (
                        content({ close, isOpen })
                      ) : (
                        content
                      )}
                      <DismissButton onDismiss={close} />
                    </Card>
                  </FocusScope>
                </OverlayContainer>
              )}
            </AnimatePresence>,
          )
        }
      </StackingProvider>
    </>
  )
}

export type { Placement } from 'react-laag'
