import { AnimatePresence, motion, MotionProps, useAnimation, useMotionValue } from 'framer-motion'
import { pick } from 'lodash-es'
import { Children, ComponentProps, ReactElement, useEffect } from 'react'
import { Route } from 'react-router'
import { HashRouter, MemoryRouter, Switch, useHistory, useLocation } from 'react-router-dom'
import { useMap } from 'react-use'
import { Overwrite } from 'utility-types'
import { Card } from '../layout'
import { styled } from '../theme/default'
import { HeightContext, stackRouterTransition } from './heightContext'
import { StackHeader } from './StackHeader'
import { StackRouteProps } from './StackPage'

const StackRoute = (props: ComponentProps<typeof Route> & Partial<StackRouteProps>) => <Route {...props} />
export { StackRoute as Route }

type RouterSpecificProps = ComponentProps<typeof HashRouter> & ComponentProps<typeof MemoryRouter>

export type StackRouterProps = {
  hashRouting?: boolean
  containerProps?: Overwrite<ComponentProps<typeof Card>, MotionProps>
  children: ReactElement<StackRouteProps> | ReactElement<StackRouteProps>[]
} & Omit<RouterSpecificProps, 'children'>

const StackRouterCard = styled(motion.div, Card, {
  $$headerHeight: '$sizes$16',

  width: '100%',
  overflow: 'hidden',
  borderRadius: '$3',

  defaultVariants: {
    elevation: 2,
    border: true,
  },
})

function StackRouterEngine({ children, containerProps = {}, ...props }: StackRouterProps) {
  const [routeMeta, { set }] = useMap<Record<string, StackRouteProps>>({})
  const location = useLocation()
  const height = useMotionValue(0)
  const heightAnimation = useAnimation()

  useEffect(() => {
    height.onChange(value => {
      heightAnimation.start({ height: value, transition: stackRouterTransition })
    })

    return () => height.destroy()
  }, [height])

  const { pathname } = location

  useEffect(() => {
    Children.forEach(children, (child, index) => {
      if (child?.props?.path) {
        const start = child.props.start ?? index === 0 ? true : false
        const hasHeader = child.props.hasHeader ?? true
        const hasBackButton = child.props.hasBackButton ?? (!!hasHeader && !start)

        set(child.props.path as string, {
          title: child.props.title,
          hasHeader,
          hasBackButton,
          start,
        })
      }
    })
  }, [children])

  const routeProps = routeMeta[pathname] ?? {}

  return (
    <StackRouterCard {...pick(props, 'css', 'as', 'border', 'elevation')} {...containerProps}>
      <StackHeader {...routeProps} />
      <motion.div
        style={{
          position: 'relative',
          display: 'grid',
          gridTemplateColumns: '100% 100%',
        }}
        animate={heightAnimation}
      >
        <HeightContext.Provider value={height}>
          <AnimatedSwitch>{children}</AnimatedSwitch>
        </HeightContext.Provider>
      </motion.div>
    </StackRouterCard>
  )
}

export const StackRouter = ({ hashRouting = false, ...props }: StackRouterProps) => {
  const Router: typeof MemoryRouter & typeof HashRouter = hashRouting ? HashRouter : MemoryRouter

  const initialEntries: string[] = [(Children.toArray(props.children)?.[0] as ReactElement).props.path as string]

  return (
    <Router
      initialEntries={initialEntries}
      {...pick(props, ['initialEntries', 'initialIndex', 'basename', 'hashtype'])}
    >
      <StackRouterEngine {...props} />
    </Router>
  )
}

function AnimatedSwitch({ children }) {
  const location = useLocation()
  const history = useHistory()

  return (
    <AnimatePresence initial={false} custom={{ action: history.action }} presenceAffectsLayout>
      <Switch location={location} key={location.pathname}>
        {children}
      </Switch>
    </AnimatePresence>
  )
}
