import { useEffect, useState } from 'react'
import { useLabel } from 'react-aria'
import { HStack, VStack } from '../layout'
import { Label } from '../typography'
import { BaseCreditCardCodeField } from './CreditCardCodeField'
import { BaseCreditCardExpiryField } from './CreditCardExpiryField'
import { BaseCreditCardNameField } from './CreditCardNameField'
import { BaseCreditCardNumberField } from './CreditCardNumberField'
import { CreditCardField, CreditCardInputProps } from './types'
import { useCardValidator } from './useCardValidator'
import { FieldWrapper } from '../form'

const DEFAULT_PLACEHOLDERS: Record<CreditCardField, string> = {
  [CreditCardField.NUMBER]: 'Card number',
  [CreditCardField.NAME]: 'Name on card',
  [CreditCardField.EXPIRY]: 'MM / YY',
  [CreditCardField.CODE]: 'CVC',
}

const DEFAULT_NAMES: Record<CreditCardField, string> = {
  [CreditCardField.NUMBER]: 'cardNumber',
  [CreditCardField.NAME]: 'cardName',
  [CreditCardField.EXPIRY]: 'cardExpiry',
  [CreditCardField.CODE]: 'cardCvc',
}

const ARIA_LABELS: Record<CreditCardField, string> = {
  [CreditCardField.NUMBER]: 'Card number',
  [CreditCardField.NAME]: 'Name on card',
  [CreditCardField.EXPIRY]: 'Expiry',
  [CreditCardField.CODE]: 'CVC',
}

export function CreditCardInput({ css, onChange, initialValues = {}, ...props }: CreditCardInputProps) {
  const [fieldValues, setFieldValues] = useState<CreditCardInputProps['initialValues']>({})
  const [fieldsTouched, setFieldsTouched] = useState<Partial<Record<CreditCardField, boolean>>>({})

  const placeholders = props.placeholders === false ? {} : { ...DEFAULT_PLACEHOLDERS, ...props.placeholders }
  const names = props.names === false ? {} : { ...DEFAULT_NAMES, ...props.names }
  const labels = props.labels ?? {}

  const { fieldProps, labelProps } = useLabel({ label: props.label || 'Credit card field', id: props.id })

  const validation = useCardValidator(fieldValues)

  useEffect(() => {
    onChange?.({ values: fieldValues, meta: validation, touched: fieldsTouched })
  }, [validation, fieldValues, fieldsTouched])

  useEffect(() => {
    if (initialValues) {
      setFieldValues(initialValues)
    }
  }, [])

  function handlers(name: string): any {
    return {
      onChange: (value: string) => setFieldValues({ ...fieldValues, [name]: value }),
      onBlur: () => setFieldsTouched({ ...fieldsTouched, [name]: true }),
      invalid: fieldsTouched[name] && !validation?.[name].isValid,
      value: fieldValues[name],
      placeholder: placeholders[name],
      label: labels[name],
      'aria-label': ARIA_LABELS[name],
      errorMessage: fieldsTouched[name] && !validation?.[name].isValid && `${ARIA_LABELS[name]} is invalid`,
      validation,
      name: names[name],
    }
  }

  return (
    <FieldWrapper as="fieldset" css={css} {...fieldProps}>
      {props.label && (
        <Label size={'regular' as any} {...labelProps}>
          {props.label}
        </Label>
      )}
      <VStack space="small">
        <BaseCreditCardNumberField card={validation?.cardNumber?.card} {...handlers(CreditCardField.NUMBER)} />
        <BaseCreditCardNameField {...handlers(CreditCardField.NAME)} />
        <HStack space="small">
          <BaseCreditCardExpiryField {...handlers(CreditCardField.EXPIRY)} />
          <BaseCreditCardCodeField
            codeSize={validation?.cardNumber?.card?.code?.size}
            {...handlers(CreditCardField.CODE)}
            placeholder={validation?.cardNumber?.card?.code?.name || placeholders[CreditCardField.CODE]}
          />
        </HStack>
      </VStack>
    </FieldWrapper>
  )
}
