import { BooleanWidgetProps, Utils } from 'react-awesome-query-builder'
import { Switch } from '../../switch'

export function QueryBuilderBoolean({ customProps, value, setValue, labelYes, labelNo, readonly }: BooleanWidgetProps) {
  const onChange = () => {
    setValue(!value)
  }
  const id = Utils.uuid()

  return (
    <Switch
      id={id}
      selected={!!value}
      disabled={readonly}
      onChange={onChange}
      label={value ? labelYes : labelNo}
      {...customProps}
    />
  )
}
