import { ChangeEvent } from 'react'
import { SelectWidgetProps } from 'react-awesome-query-builder'
import { MultiSelect } from '../../select'
import { mapListValues } from '../utils'

export function QueryBuilderMultiSelect({ listValues, value, placeholder, setValue, readonly }: SelectWidgetProps) {
  const renderOptions = () =>
    mapListValues(listValues, ({ title, value }) => {
      return (
        <option key={value} value={value}>
          {title}
        </option>
      )
    })

  const getMultiSelectValues = (multiselect: ChangeEvent<HTMLSelectElement>['target']) => {
    let values = []
    const options = multiselect.options
    for (let i = 0; i < options.length; i++) {
      const opt = options[i]
      if (opt.selected) {
        values.push(opt.value)
      }
    }
    if (!values.length) values = undefined // doesn't allow []
    return values
  }

  const onChange = (e: ChangeEvent<HTMLSelectElement>) => setValue(getMultiSelectValues(e.target))

  const hasValue = value != null
  return (
    <MultiSelect
      placeholder={placeholder}
      onChange={onChange}
      value={hasValue ? value : listValues?.[0]?.value}
      disabled={readonly}
    >
      {renderOptions()}
    </MultiSelect>
  )
}
