import { Node } from '@react-types/shared'
import { useListBoxSection, useSeparator } from 'react-aria'
import { ComboBoxState, ListState } from 'react-stately'
import { Box } from '../layout/Box'
import { styled } from '../theme/default'
import { Heading } from '../typography/Heading'
import { fontPxToNum } from '../typography/utils'
import { Option } from './Option'

const SectionHeading = styled(Heading, 'span', {
  textTransform: 'uppercase',
  letterSpacing: '0.6px',
  fontWeight: '$regular',

  variants: {
    controlSize: {
      small: {
        padding: '$1 $2',
        capsize: {
          fontSize: fontPxToNum(1) / 2,
        },
      },
      regular: {
        padding: '$2 $3',
        capsize: {
          fontSize: fontPxToNum(1),
        },
      },
      large: {
        padding: '$3 $4',
        capsize: {
          fontSize: fontPxToNum(2),
        },
      },
    },
  },

  defaultVariants: {
    controlSize: 'regular',
  },
})

const Divider = styled('li', {
  borderTop: '1px solid $gray4',
  margin: '2px 5px',
})

type SectionProps = {
  section: Node<any>
  state: ListState<any> | ComboBoxState<any>
  showHighlight?: boolean
}

export function ListSection({ section, state, showHighlight }: SectionProps) {
  let { itemProps, headingProps, groupProps } = useListBoxSection({
    heading: section.rendered,
    'aria-label': section['aria-label'],
  })

  let { separatorProps } = useSeparator({
    elementType: 'li',
  })

  return (
    <>
      {section.key !== state.collection.getFirstKey() && <Divider {...separatorProps} />}
      <li {...itemProps}>
        {section.rendered && (
          <SectionHeading controlSize="regular" size="groupHeadline" {...headingProps}>
            {section.rendered}
          </SectionHeading>
        )}
        <Box as="ul" {...groupProps} css={{ padding: 0, listStyle: 'none' }}>
          {[...section.childNodes].map(item => (
            <Option showHighlight={showHighlight} key={item.key} item={item} state={state} {...item.props} />
          ))}
        </Box>
      </li>
    </>
  )
}
