import { useTextField } from '@react-aria/textfield'
import { AriaAttributes, ComponentProps, ReactNode } from 'react'
import { AlertCircleIcon } from '../icon'
import { Box, HStack, VStack } from '../layout'
import { styled } from '../theme/default'
import { Label, Text } from '../typography'
import { fontPxToNum } from '../typography/utils'
import { ValidationMessageProps } from './helpers/types'
import { DEFAULT_VALIDITY_MESSAGES } from './helpers/useCustomValidity'

export const FieldWrapper = styled('div', {
  width: '100%',
  display: 'flex',
  flexDirection: 'column',
  gap: '8px',
})

export const LabelDescriptionWrapper = styled(VStack, {
  width: '100%',

  defaultVariants: { space: 'extraSmall' },
})

export function FieldDescription({ description, ...props }: { description?: ReactNode }) {
  return description ? (
    <Box css={{ padding: 'calc($1 / 2) 0' }} {...props}>
      <Text css={{ color: '$gray1' }} size="caption2">
        {description}
      </Text>
    </Box>
  ) : null
}

export type FieldLabelAndDescriptionProps = {
  description?: ReactNode
  descriptionProps?: Partial<ComponentProps<typeof Text>>

  label?: ReactNode
  labelProps?: Partial<ComponentProps<typeof Label>>
}

export function FieldLabelAndDescription({
  description,
  label,
  labelProps,
  descriptionProps,
}: FieldLabelAndDescriptionProps) {
  if (!label && !description) return null

  return (
    <VStack space="extraSmall">
      {label && (
        <Label size="regular" {...labelProps}>
          {label}
        </Label>
      )}
      <FieldDescription description={description} {...descriptionProps} />
    </VStack>
  )
}

type FieldErrorMessageProps = ReturnType<typeof useTextField>['errorMessageProps'] &
  Pick<Parameters<typeof useTextField>[0], 'errorMessage' | 'aria-errormessage'> & {
    invalid?: AriaAttributes['aria-invalid']
  }

export function FieldErrorMessage({ errorMessage, invalid, ...props }: FieldErrorMessageProps) {
  return errorMessage || invalid ? (
    <HStack css={{ color: '$danger' }} align="center" space="extraSmall" {...props}>
      <AlertCircleIcon size="12.5px" />
      <Text css={{ capsize: { fontSize: fontPxToNum(2), lineGap: 5 } }}>
        {errorMessage || DEFAULT_VALIDITY_MESSAGES[String(invalid)]}
      </Text>
    </HStack>
  ) : null
}

const FieldHelpText = styled('span', {
  color: '$gray1',
  capsize: { fontSize: fontPxToNum(2), lineGap: 5 },
})

/**
 * This validation message is shared across form fields
 */
export function FieldErrorAndHelpText({
  helpText,
  helpTextProps,
  invalid,
  errorMessage,
  errorMessageProps,
}: ValidationMessageProps) {
  if (!helpText && !invalid && !errorMessage) return null

  return (
    <VStack css={{ paddingTop: '$1', width: '100%' }} space="small">
      {(errorMessage || invalid) && (
        <FieldErrorMessage {...errorMessageProps} errorMessage={errorMessage} invalid={invalid} />
      )}
      {helpText && <FieldHelpText {...helpTextProps}>{helpText}</FieldHelpText>}
    </VStack>
  )
}
