/**
 * Calculates the number of columns to be displayed in the amount selector.
 * Evenly divides the items into 2/3/4 columns.
 */
export function calculateColumns(items: number): number {
  if (items >= 4 && items % 4 === 0) {
    return 4
  } else if (items >= 3 && items % 3 === 0) {
    return 3
  } else if (items >= 2 && items % 2 === 0) {
    return 2
  } else if (items > 4) {
    return 3
  } else {
    return 1
  }
}
