import { motion, PanInfo, useAnimation } from 'framer-motion'
import { AriaAttributes, ComponentProps, ReactNode, useEffect, useRef, useState } from 'react'
import { FocusScope, mergeProps, useButton, useDialog } from 'react-aria'
import { Button, ButtonProps } from '../button'
import { CloseIcon } from '../icon'
import { Box, Card, HStack, Overlay, OverlayProps, Pane } from '../layout'
import { useModalCount } from '../layout/ModalCountProvider'
import { styled } from '../theme/default'
import { Heading, Paragraph } from '../typography'

const DialogContainer = styled(Card, {
  $$topOffset: '12vmin',
  $$sideOffset: '$space$3',
  $$width: '550px',

  backgroundColor: '$background',
  flexDirection: 'column',
  display: 'flex',
  maxWidth: '100%',
  alignSelf: 'center',
  marginBottom: '$1',
  marginInline: '0',
  borderRadius: '$2',
  boxShadow: '$4',
  maxHeight: `calc(100% - $$topOffset * 2)`,
  marginTop: '$$topOffset',
  zIndex: 'calc($$zIndex + 1)',
  width: '$$width',

  '@tabletPortrait': {
    maxWidth: `calc(100% - $$topOffset * 2)`,
    alignSelf: 'initial',
    marginBottom: '0px',
    marginInline: '$$sideOffset',
  },
})

const DialogContentContainer = styled(Box, {
  $$minHeightContent: '$space$16',

  display: 'flex',
  overflow: 'auto',
  padding: '$3',
  flexDirection: 'column',
  minHeight: '$$minHeightContent',
  touchAction: 'auto !important',
})

export type DialogProps = Omit<OverlayProps, 'onHide' | 'onShow'> & {
  onOpen?: (close: () => void) => void
  onCancel?: (close: () => void) => void
  overlayProps?: ComponentProps<typeof Overlay>
  sideOffset?: ComponentProps<typeof Card>['css']['marginInline']
  topOffset?: ComponentProps<typeof Card>['css']['marginTop']
  minHeightContent?: ComponentProps<typeof Card>['css']['minHeight']
  contentContainerProps?: ComponentProps<typeof Box>
  containerProps?: ComponentProps<typeof Card>
  hasHeader?: boolean
  hasClose?: boolean
  hasCancel?: boolean
  hasFooter?: boolean
  loading?: boolean
  disabled?: boolean
  cancelLabel?: string
  confirmLabel?: string
  intent?: ButtonProps['intent']
  onConfirm?: (close: () => void) => void
  confirmIcon?: ReactNode
  width?: ComponentProps<typeof Card>['css']['width']
  /**
   * The title for the dialog
   * For accessibility reasons a title is always required even without a header
   */
  title: string
} & Parameters<typeof useDialog>[0]
export function Dialog({
  onCancel,
  onOpen,
  onConfirm,
  isShown,
  shouldCloseOnClick,
  shouldCloseOnEscapePress,
  overlayProps,
  preventBodyScroll,
  width,
  minHeightContent,
  containerProps,
  sideOffset,
  topOffset,
  contentContainerProps,
  children,
  hasHeader,
  hasClose,
  hasCancel,
  hasFooter,
  cancelLabel,
  loading,
  disabled,
  confirmLabel,
  title,
  intent,
  containerCSS,
  confirmIcon: ConfirmIcon,
  ...props
}: DialogProps) {
  const dialogRef = useRef<HTMLDivElement>()
  const closeButtonRef = useRef<HTMLButtonElement>()

  const controls = useAnimation()
  const [modalCount, setModalCount] = useModalCount()
  const [initialModalNumber, setInitialModalNumber] = useState(0)

  const offset = (modalCount + 1 - initialModalNumber) * 30
  const scale = 1 - offset / 1000

  useEffect(() => {
    controls.start({ y: offset * -1, scale })
  }, [modalCount - initialModalNumber])

  const { dialogProps, titleProps } = useDialog(props, dialogRef)
  const { buttonProps: closeButtonProps } = useButton(props, closeButtonRef)

  return (
    <Overlay
      onHide={() => {
        controls.start({ y: 50, opacity: 0 })
        onCancel?.(null)
        setModalCount(count => Math.max(0, count - 1))
      }}
      onShow={() => {
        controls.start({ y: 0, opacity: 1 })
        onOpen?.(null)
        setInitialModalNumber(modalCount + 1)
        setModalCount(count => count + 1)
      }}
      shouldCloseOnClick={shouldCloseOnClick}
      shouldCloseOnEscapePress={shouldCloseOnEscapePress}
      isShown={isShown}
      preventBodyScroll={preventBodyScroll}
      containerCSS={{
        $$topOffset: topOffset,
        $$sideOffset: sideOffset,
        $$width: width,
        $$minHeightContent: minHeightContent,

        display: 'flex',
        alignItems: 'flex-start',
        justifyContent: 'center',
      }}
      {...overlayProps}
    >
      {close => (
        <FocusScope autoFocus restoreFocus contain>
          <DialogContainer
            ref={dialogRef}
            {...(mergeProps(dialogProps, containerProps) as any)}
            as={motion.div}
            drag="y"
            onDragEnd={async (_: PointerEvent, info: PanInfo) => {
              const offset = info.offset.y
              const velocity = info.velocity.y

              if (offset > 100 || (offset > 0 && velocity > 300)) {
                await controls.start({
                  y: offset + 150,
                  opacity: 0,
                  transition: {
                    velocity,
                  },
                })

                onCancel?.(close)
              } else {
                controls.start({ y: 0, opacity: 1 })
              }
            }}
            animate={controls}
            initial={{ y: 150, opacity: 0 }}
            transition={{
              type: 'spring',
              damping: 12,
              stiffness: 100,
            }}
            role="dialog"
            css={containerCSS}
          >
            {hasHeader && (
              <Pane
                css={{
                  padding: '$3',
                  flexShrink: 0,
                  borderBottom: '1px solid $colors$muted',
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                {title && (
                  <Heading {...titleProps} size="subHeadline" css={{ flex: 1 }}>
                    {title}
                  </Heading>
                )}
                {hasClose && (
                  <Button
                    appearance="minimal"
                    icon={CloseIcon}
                    size="small"
                    onClick={() => onCancel?.(close)}
                    css={{ marginLeft: 'auto' }}
                  />
                )}
              </Pane>
            )}
            <DialogContentContainer
              onScroll={e => {
                e.nativeEvent.stopPropagation()
              }}
              {...contentContainerProps}
            >
              {typeof children === 'function' ? (
                children(close)
              ) : typeof children === 'string' ? (
                <Paragraph>{children}</Paragraph>
              ) : (
                children
              )}
            </DialogContentContainer>
            {hasFooter && (
              <Pane css={{ borderTop: '1px solid $colors$muted' }}>
                <HStack space="small" css={{ padding: '$3', textAlign: 'right', justifyContent: 'flex-end' }}>
                  {/* Cancel should be first to make sure focus gets on it first. */}
                  {hasCancel && (
                    <Button
                      {...(closeButtonProps as AriaAttributes)}
                      color="secondary"
                      autoFocus
                      tabIndex={0}
                      onClick={() => onCancel?.(close)}
                    >
                      {cancelLabel}
                    </Button>
                  )}

                  <Button
                    tabIndex={0}
                    color="primary"
                    loading={loading}
                    disabled={disabled}
                    onClick={() => onConfirm?.(close)}
                    intent={intent}
                    iconBefore={ConfirmIcon}
                  >
                    {confirmLabel}
                  </Button>
                </HStack>
              </Pane>
            )}
          </DialogContainer>
        </FocusScope>
      )}
    </Overlay>
  )
}

Dialog.defaultProps = {
  isShown: false,
  hasHeader: true,
  hasClose: true,
  hasFooter: true,
  hasCancel: true,
  width: '550px',
  topOffset: '12vmin',
  sideOffset: '$3',
  minHeightContent: '$16',
  confirmLabel: 'Confirm',
  isConfirmLoading: false,
  isConfirmDisabled: false,
  cancelLabel: 'Cancel',
  shouldCloseOnOverlayClick: true,
  shouldCloseOnEscapePress: true,
  preventBodyScrolling: false,
  overlayProps: {},
}
