import { useLabel } from 'react-aria'
import { FormatNumberOptions, useIntl } from 'react-intl'
import { Heading } from '../typography/Heading'
import { Box } from '../layout/Box'
import { styled } from '../theme/default'
import { VStack } from '../layout/Stack'

const StatisticWrapper = styled('div', VStack, {
  isolation: 'isolate',
  position: 'relative',

  variants: {
    loading: {
      true: {
        '.gc-statistic-value': {
          opacity: '0',
        },

        '&::after': {
          content: "''",
          height: '$2',
          width: 'min(80%, $20)',
          backgroundColor: '$gray3',
          position: 'absolute',
          bottom: '0',
          transform: 'translateY(-50%)',
          borderRadius: '10000px',
        },
      },
    },
  },
})

type StatisticProps = {
  /** A descriptive label for the Statistic */
  label: string
  /** The value of the Statistic */
  value: number | string
  /** Sets the loading state for the statistic */
  loading?: boolean
  /** Formatting options for the number */
  formatOptions?: FormatNumberOptions | true
}

const DEFAULT_FORMAT_OPTIONS: FormatNumberOptions = {
  style: 'decimal',
}

/**
 * Label / Number pair that displays a value and a label.
 * @example
 * <Statistic label="Total" value={12345} />
 */
export function Statistic({ label, value, loading = false, formatOptions, ...props }: StatisticProps) {
  const { labelProps, fieldProps } = useLabel({ label, ...props })
  const { formatNumber } = useIntl()

  if (typeof value === 'string') {
    value = Number(value)
  }

  if (typeof value === 'number' && !!formatOptions) {
    if (formatOptions === true) {
      formatOptions = DEFAULT_FORMAT_OPTIONS
    }
    value = formatNumber(value, formatOptions)
  }

  return (
    <StatisticWrapper loading={loading} space="small">
      <Heading
        // @ts-ignore
        as="label"
        size="groupHeadline"
        css={{ color: '$secondary', capsize: { fontSize: 10, lineGap: 3 } }}
        {...(labelProps as {})}
      >
        {label}
      </Heading>

      <Box
        as="span"
        className="gc-statistic-value"
        css={{ fontSize: 'clamp(24px, 5vw, 35px)', fontWeight: '$light' }}
        {...fieldProps}
      >
        {value}
      </Box>
    </StatisticWrapper>
  )
}
