import { Box, VStack } from '../layout'
import { TextInput } from '../textinput'
import { darkTheme } from '../theme/dark'
import { Heading, Paragraph } from '../typography'
import type { ImageUploadProps } from './types'

type MetadataEditorProps = Pick<ImageUploadProps, 'metadata' | 'onMetadataChange'>
export function MetadataEditor({ metadata = {}, onMetadataChange }: MetadataEditorProps) {
  return (
    <Box css={{ width: '100%', paddingTop: '$5' }}>
      <VStack>
        <Heading size="subHeadline">Add a description (Alt Text)</Heading>
        <Paragraph>
          You can add a description to your photos so they&apos;re accessible to more people.
          <br /> Good descriptions are concise but present what&apos;s in your photos accurately enough to understand
          their context
        </Paragraph>
        <TextInput
          label="Image Description"
          placeholder="Describe this photo..."
          value={metadata?.alt}
          onChange={value => onMetadataChange({ ...metadata, alt: value })}
          className={darkTheme.className}
          css={{ maxWidth: '400px' }}
        />
      </VStack>
    </Box>
  )
}
