import * as Polymorphic from '@radix-ui/react-polymorphic'
import { ItemRenderer, SectionProps } from '@react-types/shared'
import React, { Key, ReactElement, ReactNode } from 'react'
import { Item, Section } from 'react-stately'
import { DefaultItemProps, DefaultSectionProps } from './types'

interface PartialNode<T> {
  type?: string
  key?: Key
  value?: T
  element?: ReactElement
  wrapper?: (element: ReactElement) => ReactElement
  rendered?: ReactNode
  textValue?: string
  'aria-label'?: string
  index?: number
  renderer?: ItemRenderer<T>
  hasChildNodes?: boolean
  childNodes?: () => IterableIterator<PartialNode<T>>
  props?: any
  shouldInvalidate?: (context: unknown) => boolean
}

const _Item = Item as Polymorphic.ForwardRefComponent<'li', DefaultItemProps<{}>>

const _Section = Section

// @ts-ignore
_Section.getCollectionNode = function* getCollectionNode<T>(props: SectionProps<T>): Generator<PartialNode<T>> {
  let { children, title, items, ...rest } = props
  yield {
    type: 'section',
    props: rest,
    hasChildNodes: true,
    rendered: title,
    'aria-label': props['aria-label'],
    *childNodes() {
      if (typeof children === 'function') {
        if (!items) {
          throw new Error('props.children was a function but props.items is missing')
        }

        for (let item of items) {
          yield {
            type: 'item',
            value: item,
            renderer: children,
          }
        }
      } else {
        let items: PartialNode<T>[] = []
        React.Children.forEach(children, child => {
          items.push({
            type: 'item',
            element: child,
          })
        })

        yield* items
      }
    },
  }
}

const __Section = _Section as Polymorphic.ForwardRefComponent<'ul', DefaultSectionProps<{}>>

export * from './ActionItem'
export { _Item as Item, __Section as Section }
