import { useRadioGroupState } from '@react-stately/radio'
import { ComponentProps, forwardRef } from 'react'
import { useRadioGroup, useVisuallyHidden } from 'react-aria'
import type { Overwrite } from 'utility-types'
import { styled } from '../theme/default'
import { Label } from '../typography/Label'
import type { RadioProps } from './Radio'
import { RadioContext, RadioContextType } from './RadioContext'

const RadioGroupWrapper = styled('fieldset', {
  display: 'inline-flex',
  gap: '$small',

  variants: {
    orientation: {
      horizontal: {
        flexDirection: 'row',
      },
      vertical: {
        flexDirection: 'column',
      },
    },
  },

  defaultVariants: {
    orientation: 'vertical',
  },
})
type AriaRadioGroupStateProps = Parameters<typeof useRadioGroupState>[0]
type AriaRadioGroupProps = Parameters<typeof useRadioGroup>[0]

type RadioSizes = RadioProps['size']

type RadioGroupProps = Overwrite<ComponentProps<typeof RadioGroupWrapper>, AriaRadioGroupProps> & {
  required?: AriaRadioGroupStateProps['isRequired']
  disabled?: AriaRadioGroupStateProps['isDisabled']
  orientation?: AriaRadioGroupStateProps['orientation']

  value?: AriaRadioGroupStateProps['value']

  label: string
  hideLabel?: boolean

  /**
   * The size of the radio buttons in this group.
   * @default 'regular'
   */
  size?: RadioSizes
}

/**
 * Decorates the useRadioGroupState function to add the size prop.
 */
function useRadioGroupStateWithSize(props: RadioGroupProps): RadioContextType {
  const { size, orientation } = props
  const state = useRadioGroupState(props)

  return {
    ...state,
    size: size as RadioContextType['size'],
    orientation,
  }
}

/**
 * Radio groups allow users to select a single item from a list of mutually exclusive options.
 * @see https://www.w3.org/TR/wai-aria-practices-1.1/#radiogroup
 * @since 1.12.0
 * @author Alyssa Gutierrez
 * @coauthor Evan Hennessy
 *
 * @example
 * ```tsx
 * <RadioGroup label="Label is Required">
 *  <Radio value="1">One</Radio>
 *  <Radio value="2">Two</Radio>
 *  <Radio value="3">Three</Radio>
 * </RadioGroup>
 * ```
 */
export const RadioGroup = forwardRef<HTMLFieldSetElement, RadioGroupProps>(
  ({ disabled, required, hideLabel, ...props }, forwardedRef) => {
    const propsAliases = { isDisabled: disabled, isRequired: required }
    const { label, children } = props
    const state = useRadioGroupStateWithSize({ ...propsAliases, ...props })
    const { radioGroupProps, labelProps } = useRadioGroup({ ...propsAliases, ...props }, state)
    const { visuallyHiddenProps } = useVisuallyHidden()

    return (
      <RadioGroupWrapper orientation={props.orientation} {...radioGroupProps} ref={forwardedRef}>
        <Label
          as="legend"
          css={{ paddingInline: '0px', paddingBottom: '$small' }}
          {...labelProps}
          {...(hideLabel ? visuallyHiddenProps : {})}
        >
          {label}
        </Label>
        <RadioContext.Provider value={state}>{children}</RadioContext.Provider>
      </RadioGroupWrapper>
    )
  },
)

RadioGroup.defaultProps = {
  orientation: 'vertical',
}
