import { NumberWidgetProps } from 'react-awesome-query-builder'
import { TextInput } from '../../textinput'

export function QueryBuilderNumber({
  value,
  setValue,
  readonly,
  min,
  max,
  step,
  placeholder,
  customProps,
}: NumberWidgetProps) {
  const onChange = (value: string) => {
    let val: string | number = value
    if (val === '' || val === null) val = undefined
    else val = Number(val)
    setValue(val)
  }

  const numberValue = value == undefined ? '' : value

  return (
    <TextInput
      type="number"
      value={numberValue}
      placeholder={!readonly ? placeholder : ''}
      min={min}
      max={max}
      step={step}
      disabled={readonly}
      onChange={onChange}
      {...customProps}
    />
  )
}
