import { AriaAttributes, MutableRefObject, useEffect } from 'react'
import { ValidationMessageProps } from './types'

export const DEFAULT_VALIDITY_MESSAGES: Record<string, string> = {
  spelling: 'Spelling is invalid',
  grammar: 'Grammar is invalid',
  true: 'Field is invalid',
}

type UseValidityProps = {
  'aria-invalid'?: AriaAttributes['aria-invalid']
}

export function useCustomValidity(
  ref: MutableRefObject<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>,
  { invalid, errorMessage }: ValidationMessageProps,
): UseValidityProps {
  useEffect(() => {
    ref?.current.setCustomValidity(
      !!invalid ? errorMessage?.toString() ?? DEFAULT_VALIDITY_MESSAGES[String(invalid)] : '',
    )
  }, [invalid])

  return {
    'aria-invalid': !!invalid,
  }
}
