import cardValidator from 'card-validator'
import { useFormikContext } from 'formik'
import { useCallback, useEffect, useMemo } from 'react'
import { mergeProps } from 'react-aria'
import { useIMask } from 'react-imask'
import { useFormikHelper } from '../formik'
import {
  CreditCardAmexIcon,
  CreditCardDiscoverIcon,
  CreditCardIcon,
  CreditCardMastercardIcon,
  CreditCardVisaIcon,
} from '../icon'
import { TextInput } from '../textinput'
import { CreditCardChildFieldProps, CreditCardField, UseValidationReturnType } from './types'

const DEFAULT_NAME = CreditCardField.NUMBER
const DEFAULT_PLACEHOLDER = 'Card Number'
const DEFAULT_ARIA_LABEL = 'Credit Card Number'

export function FormikCreditCardNumberField({
  name = DEFAULT_NAME,
  placeholder = DEFAULT_PLACEHOLDER,
  ...props
}: CreditCardChildFieldProps) {
  const { values, registerField, unregisterField } = useFormikContext()

  const [field, meta, helpers] = useFormikHelper({ name, placeholder, ...props })

  useEffect(() => {
    registerField(name, {
      validate: value => (cardValidator.number(value).isValid ? undefined : `Invalid card number`),
    })

    return () => unregisterField(name)
  }, [registerField, unregisterField, name])

  const validation = useMemo(() => {
    return cardValidator.number(values[CreditCardField.NUMBER])
  }, [values?.[CreditCardField.NUMBER]])

  return (
    <BaseCreditCardNumberField
      aria-label={DEFAULT_ARIA_LABEL}
      {...mergeProps(field, props, { name, placeholder })}
      card={validation?.card}
      onChange={value => {
        props?.onChange?.(value)
        helpers.setValue(value)
      }}
      invalid={(meta.touched || meta.submitCount > 0) && !!meta.error}
      errorMessage={(meta.touched || meta.submitCount > 0) && meta.error}
    />
  )
}

function getCardIcon(cardType: string) {
  switch (cardType) {
    case 'discover':
      return CreditCardDiscoverIcon
    case 'american-express':
      return CreditCardAmexIcon
    case 'mastercard':
      return CreditCardMastercardIcon
    case 'visa':
      return CreditCardVisaIcon
    default:
      return CreditCardIcon
  }
}

export function BaseCreditCardNumberField({
  card,
  onChange,
  value,
  ...props
}: CreditCardChildFieldProps & { card?: UseValidationReturnType['cardNumber']['card'] }) {
  const { gaps = [4, 8, 12], lengths = [16] } = card ?? {}

  const gapsToMask = useCallback(
    ({ gaps, lengths }: { gaps: number[]; lengths: number[] }) => {
      let mask = '0'.repeat(lengths[0])

      gaps.forEach((gap, i) => {
        mask = mask.slice(0, gap + i) + ' ' + mask.slice(gap + i)
      })

      return mask
    },
    [gaps, lengths],
  )

  const { ref, maskRef, unmaskedValue, setUnmaskedValue } = useIMask({
    mask: gapsToMask({ gaps, lengths }),
  })

  useEffect(() => {
    if (unmaskedValue && unmaskedValue !== value) {
      onChange?.(unmaskedValue)
    }
  }, [unmaskedValue])

  useEffect(() => {
    if (value && unmaskedValue !== String(value)) {
      setUnmaskedValue(String(value))
    }
  }, [value])

  useEffect(() => {
    maskRef.current.updateOptions({ mask: gapsToMask({ gaps, lengths }) })
  }, [gaps, lengths])

  const Icon = getCardIcon(card?.type)

  return (
    <TextInput
      ref={ref}
      {...props}
      css={{ input: { paddingLeft: '$10' } }}
      iconBefore={() => <Icon multicolored size="$5" name={card?.niceType} />}
      autoComplete="ccname"
      autoCorrect="off"
      spellCheck="false"
      id="cardName"
      type="text"
    />
  )
}
