import { FormikValues } from 'formik'
import { createContext, useCallback, useContext, useEffect, useRef, useState, FocusEvent } from 'react'

export type FocusContext = {
  focus: string
  handleFocus: (e: FocusEvent) => void
  handleBlur: (e: FocusEvent) => void
  setFocus: (name: string) => void
  focusNext: () => void
  focusPrevious: () => void
}

export const FocusManagementContext = createContext<FocusContext>(null)

// type FocusManagementReturnType<T> = [keyof T, (key: keyof T) => void]

export function useFocusContext() {
  const focusContext = useContext(FocusManagementContext)

  return focusContext
}

export function useFocusManager<T extends FormikValues>({
  initialFocus,
}: {
  initialFocus: keyof T
}): [React.Ref<HTMLFormElement>, FocusContext] {
  const ref = useRef<HTMLFormElement>()
  const [focus, setFocus] = useState<keyof T>(null)

  const handleBlur = useCallback(
    (e: FocusEvent<HTMLInputElement>) => {
      const field = e?.target?.name

      if (field === focus) {
        setFocus(null)
      }
    },
    [focus, setFocus, ref],
  )

  const handleFocus = useCallback(
    (e: FocusEvent<HTMLInputElement>) => {
      const field = e?.target?.name
      if (field !== focus) {
        setFocus(field)
      }
    },
    [focus, setFocus, ref],
  )

  const setFieldFocus = useCallback(
    (name: string) => {
      const target = ref?.current?.querySelector<HTMLInputElement>(`[name="${name}"]`)

      if (target) {
        setFocus(target?.name as keyof T)
        target?.focus?.()
      }
    },
    [focus, setFocus, ref],
  )

  const focusNext = useCallback(() => {
    const fields = ref?.current?.querySelectorAll<HTMLInputElement>('[name]')

    if (fields) {
      const currentIndex = Array.from(fields).findIndex(field => field.name === focus)

      if (currentIndex !== -1) {
        const nextIndex = currentIndex + 1

        if (nextIndex < fields.length) {
          if (!!fields[nextIndex]?.name) {
            setFieldFocus(fields[nextIndex].name)
          }
        }
      }
    }
  }, [focus, setFocus, ref])

  const focusPrevious = useCallback(() => {
    const fields = ref?.current?.querySelectorAll<HTMLInputElement>('[name]')

    if (fields) {
      const currentIndex = Array.from(fields).findIndex(field => field.name === focus)

      if (currentIndex !== -1) {
        const prevIndex = currentIndex - 1

        if (prevIndex >= 0) {
          if (!!fields[prevIndex]?.name) {
            setFieldFocus(fields[prevIndex].name)
          }
        }
      }
    }
  }, [focus, setFocus, ref])

  useEffect(() => {
    if (initialFocus) {
      setFieldFocus(initialFocus as string)
    }
  }, [initialFocus])

  return [
    ref,
    {
      focus: focus as string,
      handleFocus,
      handleBlur,
      setFocus: setFieldFocus,
      focusNext,
      focusPrevious,
    },
  ]
}
