import { isEqual } from 'lodash-es'
import { useEffect, useMemo, useState } from 'react'
import { FormattedMessage } from 'react-intl'
import { FileMaybeWithPreview } from '../fileUpload/fileUploadMachine'
import { Box, HStack, VStack } from '../layout'
import { Heading, Paragraph } from '../typography'
import { Image } from './Image'
import { FocusedImage, FocusPicker } from './utils/image-focus'

export function convertFocusCoordinatesToPosition2D(focusCoordinates: Position2D): Position2D {
  const position2D: Position2D = { x: 0.5, y: 0.5 }
  position2D.x = Number(((focusCoordinates.x + 1) / 2).toFixed(2))
  position2D.y = Number(((focusCoordinates.y * -1 + 1) / 2).toFixed(2))

  return position2D
}

interface UseFocusPickerArgs {
  src: string
  focusPosition?: Position2D
  onChange?: (position: Position2D) => void
}
function useFocusPicker({
  src,
  focusPosition,
  onChange = () => null,
}: UseFocusPickerArgs): [(instance: HTMLImageElement | null) => void, FocusPicker | undefined] {
  const [imageRef, setImageRef] = useState<HTMLImageElement | null>(null)

  const picker = useMemo(() => {
    if (imageRef) {
      return new FocusPicker(imageRef, {
        focus: focusPosition,
        onChange,
      })
    }
    return undefined
  }, [imageRef])

  useEffect(() => {
    if (picker) {
      picker.disable()
      picker.enable()
    }
  }, [src])

  useEffect(() => {
    if (picker && !isEqual(picker.focus, focusPosition)) {
      picker.setFocus(focusPosition)
    }
  }, [focusPosition])

  return [setImageRef, picker]
}

type FocusEditorProps = {
  focusPosition?: Position2D
  file: FileMaybeWithPreview
  onChange: (focus: Position2D) => void
}

function getSrcFromFileObject(file: FileMaybeWithPreview): string {
  try {
    if (file.preview) return file.preview
    return URL.createObjectURL(file)
  } catch (error) {
    if (__DEV__) console.error(error)
    return ''
  }
}

export function FocusEditor({ file, focusPosition, onChange }: FocusEditorProps) {
  const [src, setSrc] = useState(getSrcFromFileObject(file))

  useEffect(() => {
    if (file) {
      setSrc(getSrcFromFileObject(file))
    }
  }, [file])

  const [setImageRef, picker] = useFocusPicker({ src, focusPosition, onChange })

  useEffect(() => {
    return () => {
      if (picker) {
        picker.disable()
      }
    }
  }, [picker])

  return (
    <Box css={{ width: '100%' }}>
      <VStack>
        <Heading>Set Focal Point</Heading>
        <Paragraph>
          <FormattedMessage id="focuseditor.subheading" />
        </Paragraph>
        <HStack align="center" justify="center" space="extraLarge">
          <Box>
            <Box css={{ padding: '$1 0 $3' }}>
              <Heading size="subHeadline">Original</Heading>
            </Box>
            <Box css={{ maxWidth: '300px' }}>
              <Image ref={setImageRef} src={src} css={{ borderRadius: '$2', boxShadow: '$2' }} />
            </Box>
          </Box>

          <Box>
            <VStack>
              <Heading size="subHeadline">Preview</Heading>
              <Box css={{ backgroundColor: 'hsl(0 0% 0% / 0.5)', padding: '$5', borderRadius: '$4' }}>
                <HStack>
                  {[
                    { width: 200, height: 300 },
                    { width: 200, height: 200 },
                    { width: 300, height: 200 },
                  ].map(dimensions => (
                    <ImageFocusPreview
                      key={JSON.stringify(dimensions)}
                      dimensions={dimensions}
                      focusPosition={focusPosition}
                      src={src}
                    />
                  ))}
                </HStack>
              </Box>
            </VStack>
          </Box>
        </HStack>
      </VStack>
    </Box>
  )
}

type ImageFocusPreviewProps = {
  src: string
  dimensions: { width: number; height: number }
  focusPosition: Position2D
}
function ImageFocusPreview({ src, dimensions, focusPosition }: ImageFocusPreviewProps) {
  const [imageRef, setImageRef] = useState<HTMLImageElement | null>(null)

  useMemo(() => {
    if (imageRef) {
      return new FocusedImage(imageRef, {
        updateOnContainerResize: true,
        focus: focusPosition,
        debounceTime: 17,
        updateOnWindowResize: true,
      })
    }
    return undefined
  }, [focusPosition, imageRef])

  return (
    <Box css={{ ...dimensions, borderRadius: '$2', boxShadow: '$2' }}>
      <Box as="img" ref={setImageRef} src={src} />
    </Box>
  )
}
