import { TextWidgetProps } from 'react-awesome-query-builder'
import { TextInput } from '../../textinput'

export function QueryBuilderDate({ value, setValue, readonly, maxLength, placeholder, customProps }: TextWidgetProps) {
  const onChange = (value: string) => {
    let val = value
    if (val === '') val = undefined // don't allow empty value
    setValue(val)
  }

  const dateValue = value || new Date()

  return (
    <TextInput
      type="date"
      value={dateValue}
      placeholder={!readonly ? placeholder : ''}
      maxLength={maxLength}
      onChange={onChange}
      {...customProps}
    />
  )
}
