import * as RadixTabs from '@radix-ui/react-tabs'
import { ComponentProps, useEffect, useRef, useState } from 'react'
import { ChevronRightIcon } from '../icon'
import { styled } from '../theme/default'

export const TabRoot = styled(RadixTabs.Root, {
  display: 'flex',
  flexDirection: 'column',

  '&[data-orientation="vertical"]': {
    flexDirection: 'row',
  },
})

export const TabList = styled(RadixTabs.List, {
  flexShrink: 0,
  display: 'flex',

  '&[data-orientation="vertical"]': {
    flexDirection: 'column',
  },
})

export const TabPanel = styled(RadixTabs.Panel, {
  outline: 'none',
  borderRadius: '$1',
})

export const TabFactory = styled(RadixTabs.Tab, {
  color: '$gray2',
  padding: '$4 $2',
  marginInline: '$2',
  cursor: 'pointer',
  outline: 'none',
  transition: 'color $normal',
  borderBottom: '$sizes$1 solid transparent',
  borderTop: '$sizes$1 solid transparent',
  fontWeight: '$medium',

  // Horizontal
  '&[data-orientation="horizontal"]': {
    '&[aria-selected="true"], &.active': {
      borderBottom: '$sizes$1 solid $colors$primary',
    },
  },

  // Vertical
  '&[data-orientation="vertical"]': {
    padding: '$4 0',
    marginInline: '0',
    width: '100%',
    display: 'flex',
    alignItems: 'center',

    '&:not(:last-child)': {
      borderBottom: '1px solid $colors$gray3',
    },

    svg: { display: 'none', marginInlineStart: 'auto' },
    '&[aria-selected="true"], &.active': {
      svg: { display: 'revert' },
    },
  },

  // General Active
  '&[aria-selected="true"], &.active': {
    '&, &:hover, &:focus': {
      color: '$label',
    },
  },

  '&:hover, &:focus': {
    color: '$gray1',
  },

  '@highcontrast': {
    color: '$label',
  },
})

export function Tab({ children, ...props }: ComponentProps<typeof TabFactory>) {
  const ref = useRef<HTMLDivElement>(null)
  const [showIcon, setShowIcon] = useState(false)
  useEffect(() => setShowIcon(ref.current.dataset.orientation === 'vertical'), [])

  return (
    <TabFactory ref={ref} {...props}>
      {children}
      {showIcon ? <ChevronRightIcon size="$2" /> : null}
    </TabFactory>
  )
}
