export const capsizeFontConfig = {
  'Modern Era': {
    capHeight: 700,
    ascent: 785,
    descent: -215,
    lineGap: 0,
    unitsPerEm: 1000,
  },
  'Recoleta Alt': {
    capHeight: 710,
    ascent: 1000,
    descent: -360,
    lineGap: 0,
    unitsPerEm: 1000,
  },
} as const

const { globalCss, config, styled, css, theme, keyframes, getCssText, prefix, toString, createTheme } = createStitches({
  prefix: 'gc',
  theme: themeConfig,
  media: {
    tabletPortrait: '(min-width: 767px)',
    tabletLandscape: '(min-width: 1023px)',
    laptop: '(min-width: 1100px)',
    desktop: '(min-width: 1919px)',
    ultrawide: '(min-width: 2300px)',

    touch: '(pointer: coarse)',
    mouse: '(pointer: fine) and (hover: hover)',

    highcontrast: '(forced-colors: active)',
  },
  utils: {
    marginInline: (value: PropertyValue<'margin'>) => ({
      marginLeft: value,
      marginRight: value,
    }),
    marginBlock: (value: PropertyValue<'margin'>) => ({
      marginTop: value,
      marginBottom: value,
    }),
    paddingInline: (value: PropertyValue<'padding'>) => ({
      paddingLeft: value,
      paddingRight: value,
    }),
    paddingBlock: (value: PropertyValue<'padding'>) => ({
      paddingTop: value,
      paddingBottom: value,
    }),
    size: (value: PropertyValue<'width'>) => ({
      width: value,
      height: value,
    }),
    minSize: (value: PropertyValue<'minWidth'>) => ({
      minWidth: value,
      minHeight: value,
    }),
    maxSize: (value: PropertyValue<'maxWidth'>) => ({
      maxWidth: value,
      maxHeight: value,
    }),
    /**
     * Allows a custom capsize config to be used
     * @see https://seek-oss.github.io/capsize/
     *
     * @example
     * ```jsx
     * <Box css={{ capsize: { fontSize: 16, lineGap: 1.5 } }}>
     *   My capsized text
     * </Box>
     * ```
     */
    capsize: ({
      metrics = 'Modern Era',
      fontSize = 16,
      lineGap = 1.5,
    }: {
      /**
       * Custom metrics to use in the capsize calculation
       * These should be added to the theme config dictionary
       */
      metrics?: keyof typeof capsizeFontConfig
      /**
       * The desired font size in pixels
       */
      fontSize?: number
      /**
       * The desired gap between lines of text in pixels
       * This is something different than line height and more similar to "grid-gap"
       */
      lineGap?: number
    }) => {
      const cappedStyles = createStyleObject({
        fontMetrics: capsizeFontConfig[metrics],
        fontSize,
        lineGap,
      })
      return {
        // Capsize doesn't prefix these pseudo-styles
        '&::before': cappedStyles['::before'],
        '&::after': cappedStyles['::after'],
        fontSize: cappedStyles.fontSize,
        lineHeight: cappedStyles.lineHeight,
      }
    },
  },
})

/**
 * The starting values for z-indices.
 * @note These are just starting values and should be passed into a `StackingProvider` to ensure that later mounted elements are stacked on top
 */
export enum StackingOrder {
  FOCUSED = 2,
  DEFAULT = 5,
  POSITIONER = 10,
  OVERLAY = 20,
  TOASTER = 30,
}

