import { motion, Variants } from 'framer-motion'
import { ComponentProps, useContext, useRef } from 'react'
import { Route, useHistory } from 'react-router-dom'
import { Pane } from '../layout/Pane'
import { styled } from '../theme/default'
import { HeightContext, stackRouterTransition } from './heightContext'

export type StackRouteProps = ComponentProps<typeof Route> & {
  title?: string
  start?: boolean
  /** Whether to show the header on this route */
  hasHeader?: boolean
  hasBackButton?: boolean
}

const MotionPane = styled(motion.div, Pane, {
  padding: '$5',
  backgroundColor: '$background',
  width: '100%',
  height: '100%',
})

export function StackPage({ children, ...props }: Partial<StackRouteProps> & ComponentProps<typeof Pane>) {
  const history = useHistory()
  const ref = useRef<HTMLDivElement>(null)
  const height = useContext(HeightContext)

  const variants: Variants = {
    initial() {
      const isPush = history.action === 'PUSH'

      return {
        height: height.getPrevious() || '100%',
        transition: stackRouterTransition,
        ...(isPush
          ? {
              top: 0,
              right: 0,
              left: 0,
              bottom: 0,
              zIndex: 9999,
              x: '100%',
              position: 'relative',
            }
          : {
              gridColumn: '1',
              x: '-25%',
              zIndex: 0,
              filter: 'brightness(0.8)',
            }),
      }
    },
    enter() {
      const isPush = history?.action === 'PUSH'

      const refHeight = ref?.current?.firstElementChild.getBoundingClientRect().height + 50
      height.set(refHeight)

      return {
        x: 0,
        height: '100%',
        transition: stackRouterTransition,
        transitionEnd: { position: 'static' },
        filter: 'brightness(1)',
        ...(isPush ? { position: 'relative', top: 0, right: 0, bottom: 0, left: 0 } : {}),
      }
    },
    exit({ action = history.action } = {}) {
      // if isPop is true, this is the top page being dismissed (sliding to the right)
      // if isPop is false, this is the botom page being dismissed (sliding to the left)
      const isPop = action === 'POP'

      return {
        transition: stackRouterTransition,
        position: 'absolute',
        top: 0,
        right: 0,
        left: 0,
        bottom: 0,

        ...(isPop ? { x: '100%', zIndex: 9999 } : { x: '-10%', zIndex: 0, filter: 'brightness(0.8)' }),
      }
    },
  }

  return (
    <MotionPane
      ref={ref}
      initial="initial"
      animate="enter"
      exit="exit"
      variants={variants}
      css={props?.css}
      style={{ height: 0 }}
    >
      {children}
    </MotionPane>
  )
}
