import cardValidator from 'card-validator'
import { useFormikContext } from 'formik'
import { useEffect, useMemo } from 'react'
import { mergeProps } from 'react-aria'
import { useIMask } from 'react-imask'
import { useFormikHelper } from '../formik'
import { TextInput } from '../textinput/TextInput'
import { CreditCardChildFieldProps, CreditCardField } from './types'

const DEFAULT_PLACEHOLDER = 'CVC'
const DEFAULT_NAME = CreditCardField.CODE
const DEFAULT_ARIA_LABEL = 'CVC'

export function FormikCreditCardCodeField({
  name = DEFAULT_NAME,
  placeholder = DEFAULT_PLACEHOLDER,
  ...props
}: CreditCardChildFieldProps<string>) {
  const { registerField, unregisterField, values } = useFormikContext()

  const [field, meta, helpers] = useFormikHelper({ name, ...props })

  useEffect(() => {
    registerField(name, {
      validate: value => (cardValidator.cvv(value).isValid ? undefined : `Invalid Code`),
    })

    return () => unregisterField(name)
  }, [registerField, unregisterField, name])

  const validation = useMemo(() => {
    return cardValidator.number(values[CreditCardField.NUMBER])
  }, [values?.[CreditCardField.NUMBER]])

  return (
    <BaseCreditCardCodeField
      aria-label={DEFAULT_ARIA_LABEL}
      {...mergeProps(field, props, { name, placeholder })}
      placeholder={validation?.card?.code?.name || placeholder}
      codeSize={validation?.card?.code?.size}
      invalid={(meta.touched || meta.submitCount > 0) && !!meta.error}
      onChange={value => {
        props?.onChange?.(value)
        helpers.setValue(value)
      }}
      errorMessage={(meta.touched || meta.submitCount > 0) && meta.error}
    />
  )
}

export function BaseCreditCardCodeField({
  onChange,
  label,
  codeSize = 3,
  placeholder,
  ...props
}: CreditCardChildFieldProps<string> & { codeSize?: number }) {
  const sizeToMask = (size: number) => '0'.repeat(size)

  const { ref, maskRef, unmaskedValue } = useIMask({
    mask: sizeToMask(codeSize),
  })

  useEffect(() => {
    maskRef.current.updateOptions({ mask: sizeToMask(codeSize) })
  }, [codeSize])

  useEffect(() => {
    if (unmaskedValue) {
      onChange?.(unmaskedValue)
    }
  }, [unmaskedValue])

  return (
    <TextInput
      {...props}
      label={!!label ? label ?? placeholder : null}
      placeholder={placeholder || DEFAULT_PLACEHOLDER}
      aria-label={placeholder || label || 'CVC'}
      ref={ref}
      autoComplete="cc-csc"
      autoCorrect="off"
      spellCheck="false"
      id="cardCvc"
      inputMode="numeric"
    />
  )
}
