import { forwardRef, ReactChild } from 'react'
import { ChevronDownIcon } from '../icon'
import { Box } from '../layout/Box'
import { Popover } from '../popover'
import { styled } from '../theme/default'
import { Button, ButtonProps } from './Button'

type SplitButtonProps = ButtonProps & {
  content?: ReactChild
}

const SplitButtonFactory = styled(Button, {
  '&&': {
    borderTopRightRadius: 0,
    borderBottomRightRadius: 0,
  },
})

const SplitButtonDropDownFactory = styled(Button, {
  borderTopLeftRadius: 0,
  borderBottomLeftRadius: 0,
  width: '$6',

  svg: {
    marginInline: '4px',
  },
})

export const SplitButton = forwardRef<HTMLButtonElement, SplitButtonProps>(
  ({ children, content, ...props }, forwardedRef) => {
    if (props.appearance === 'minimal') {
      delete props.appearance
    }

    return (
      <Box css={{ display: 'flex' }}>
        <SplitButtonFactory {...props} ref={forwardedRef}>
          {children}
        </SplitButtonFactory>

        <Popover content={content}>
          <SplitButtonDropDownFactory
            split
            appearance={props.appearance}
            color={props.color}
            size={props.size}
            icon={ChevronDownIcon}
          />
        </Popover>
      </Box>
    )
  },
)
