import { ComponentProps } from '@stitches/react'
import { forwardRef } from 'react'
import { Button } from '../button/Button'
import { Select } from '../select'
import { TextInput } from '../textinput'
import { styled } from '../theme/default'
import { Label } from '../typography'

const CONTROLS_SELECTOR = `&:is(${Button}, ${TextInput}, ${Select}), ${Button}, ${TextInput}, ${Select}`
const INPUT_SELECTOR = `&:is(${TextInput}, ${Select}), ${TextInput}, ${Select}`

export const InputGroupFactory = styled('div', {
  display: 'flex',
  alignItems: 'center',
  width: '100%',

  variants: {
    size: {
      small: {
        [CONTROLS_SELECTOR]: {
          height: '$controlHeightSmall',
        },
        [INPUT_SELECTOR]: {
          height: 'calc($controlHeightSmall - 2px)',
        },
      },
      regular: {
        [CONTROLS_SELECTOR]: {
          height: '$controlHeightRegular',
        },
        [INPUT_SELECTOR]: {
          height: 'calc($controlHeightRegular - 2px)',
        },
      },
      large: {
        [CONTROLS_SELECTOR]: {
          height: '$controlHeightLarge',
        },
        [INPUT_SELECTOR]: {
          height: 'calc($controlHeightLarge - 2px)',
        },
      },
    },
  },

  '> :first-child': {
    [CONTROLS_SELECTOR]: {
      borderTopRightRadius: 0,
      borderBottomRightRadius: 0,
    },
    [`&:is(${Label}), ${Label}`]: {
      breakAfter: 'always',
    },
  },

  '> :not(:first-child):not(:last-child)': {
    [CONTROLS_SELECTOR]: {
      borderRadius: 0,
    },

    [`&${Button}`]: {
      border: '1px solid #FFFFFF50',
      borderWidth: '0 0.5px',
    },
  },

  '> :last-child': {
    [CONTROLS_SELECTOR]: {
      borderTopLeftRadius: 0,
      borderBottomLeftRadius: 0,
    },

    [`> ${TextInput}`]: {
      marginLeft: '1px',
    },
  },

  defaultVariants: {
    size: 'regular',
  },
})

/** 
 * Provides a wrapper and needed styles for a group of controls.
 * This component controls the sizes of the controls and the spacing between them.
 * 
 * @example
 * ```
 * <InputGroup size="small">
 *    <TextInput />
 *    <Button>Submit</Button>
 * </InputGroup>
 * ```
 * 
 * @since 1.2.6
 * @author [Evan Hennessy]<evan@glasscanvas.io>
 * 
 */
export const InputGroup = forwardRef<HTMLDivElement, ComponentProps<typeof InputGroupFactory>>(
  (props, forwardedRef) => {
    return <InputGroupFactory ref={forwardedRef} {...props} />
  },
)

InputGroup.displayName = 'InputGroup'