import { mergeRefs } from 'react-laag'
import { isEmpty } from 'lodash-es'
import { AriaAttributes, ComponentProps, forwardRef, useRef, useState } from 'react'
import { useCustomValidity } from '../form/helpers'
import { ChevronDownIcon } from '../icon'
import { Box } from '../layout'
import { styled } from '../theme/default'

const SelectFactory = styled('select', {
  $$shadowColor: '$colors$gray3',

  cursor: 'pointer',
  display: 'flex',
  WebkitAppearance: 'none',
  MozAppearance: 'none',
  border: 'none',
  boxShadow: '0 0 0 1px $$shadowColor',
  fontSize: '$body',
  width: '100%',
  minWidth: '$8',
  borderRadius: '$1',
  color: '$label',
  backgroundColor: '$background',
  paddingLeft: '$3',
  paddingRight: '$3',

  '&:focus': {
    $$shadowColor: '$colors$primary',
    outline: 'none',
    boxShadow: `0 0 0 1px $$shadowColor, 0 0 0 1px $$shadowColor inset`,
  },

  '&[disabled]': {
    cursor: 'not-allowed',
    backgroundColor: '$gray4',
  },

  '&:invalid': {
    $$shadowColor: '$colors$danger',
    backgroundColor: 'hsl($hsl_danger / 0.15)',

    '&::placeholder': {
      color: 'hsl($hsl_danger / 0.65)',
    },

    '&:focus': {
      boxShadow: `0 0 0 2px $$shadowColor`,
    },
  },

  variants: {
    size: {
      small: {
        borderRadius: 'calc($1 / 2)',
        padding: '0 $4 0 $3',
        height: 'calc($controlHeightSmall - 2px)',
        fontSize: '$2',
      },
      regular: {
        padding: '0 $5 0 $3',
        height: 'calc($controlHeightRegular - 2px)',
        fontSize: '$3',
      },
      large: {
        padding: '0 $6 0 $4',
        height: 'calc($controlHeightLarge - 2px)',
      },
    },

    placeholder: {
      true: {
        color: '$placeholderText',
      },
      false: {},
    },
  },

  defaultVariants: {
    size: 'regular',
  },
})

export type SelectProps = Omit<ComponentProps<typeof SelectFactory>, 'placeholder'> & {
  /** Provide a helpful message for why the input is invalid */
  validationMessage?: string

  /** The aria invalid attribute */
  invalid?: AriaAttributes['aria-invalid']

  /** An optional placeholder */
  placeholder?: string
}

/**
 * A simple select field that includes theme styling
 */
export const Select = forwardRef<HTMLSelectElement, SelectProps>(({ placeholder, ...props }, forwardedRef) => {
  const [value, setValue] = useState(props.value || props.defaultValue)
  const ref = useRef<HTMLSelectElement>()
  const validityProps = useCustomValidity(ref, props)

  return (
    <Box
      css={{
        display: 'inline-flex',
        flex: '1',
        width: '100%',
        position: 'relative',
        alignItems: 'center',
      }}
    >
      <SelectFactory
        {...validityProps}
        {...props}
        onChange={e => {
          props.onChange?.(e)
          setValue(e.currentTarget.value)
        }}
        placeholder={isEmpty(value) && !!placeholder}
        ref={mergeRefs(forwardedRef, ref)}
      >
        {placeholder && (
          <option selected value="" disabled>
            {placeholder}
          </option>
        )}
        {props.children}
      </SelectFactory>
      <ChevronDownIcon
        color="$gray1"
        css={{
          height: '25%',
          position: 'absolute',
          right: '$2',
          top: '40%',
          pointerEvents: 'none',
          backgroundImage: 'linear-gradient(to left transparent, $colors$background)',
          color: '$gray2',
        }}
      />
    </Box>
  )
})

Select.toString = SelectFactory.toString

Select.defaultProps = {
  disabled: false,
  invalid: false,
}
