import { assign, debounce, noop } from 'lodash-es'

interface Focus {
  x: number
  y: number
}

/**
 * @param {Focus} focusCoordinates
 * @returns {void}
 */
type OnFocusChange = (focusCoordinates: { x: number; y: number }) => void

const CONTAINER_STYLES = {
  position: 'relative',
  overflow: 'hidden',
}

const ABSOLUTE_STYLES = {
  position: 'absolute',
  top: '0',
  right: '0',
  bottom: '0',
  left: '0',
}

interface FocusPickerOptions {
  /**
   * Callback that receives FocusCoordinates on change
   */
  onChange?: OnFocusChange
  /**
   * Focus to initialize with
   */
  focus?: Focus
  /**
   * src attribute for the retina
   */
  retina?: string
}

interface FocusedImageOptions {
  /**
   * Time in MS before debounceApplyShift fires
   *
   * Defaults to `17`
   */
  debounceTime?: number
  /**
   * Should window resize events fire debounceApplyShift?
   *
   * Defaults to `true`
   */
  updateOnWindowResize?: boolean
  /**
   * Should container resize (even from CSS) fire debounceApplyShift?
   *
   * Defaults to `false`
   */
  updateOnContainerResize?: boolean
  /**
   * Focus coordinates to initialize with
   *
   * Default value is `undefined`
   */
  focus?: Focus
  /**
   * Container position
   *
   * Default value is "relative"
   */
  containerPosition?: 'fixed' | 'relative' | 'absolute' | 'sticky'
}

const IMAGE_STYLES = {
  // Get rid of bottom padding from default display
  display: 'block',
  // Make image fill container
  maxWidth: '100%',
  // Prevent Android refresh on pull down
  touchAction: 'none',
}

const RETINA_STYLES = {
  position: 'absolute',
  cursor: 'move',

  // Center the retina
  transform: 'translate(-50%, -50%)',
}

const DEFAULT_OPTIONS: FocusPickerOptions = {
  onChange: noop,
  // retina,
}

export class FocusPicker {
  container: HTMLElement
  img: HTMLImageElement
  retina: HTMLImageElement
  focus: Focus
  private isDragging: boolean
  private options: FocusPickerOptions
  private _enabled: boolean = false

  constructor(imageNode: HTMLImageElement, options: FocusPickerOptions = {}) {
    // Merge options in
    this.options = assign({}, DEFAULT_OPTIONS, options)

    // Set up references
    this.img = imageNode
    this.container = imageNode.parentElement

    // Styles and DOM config
    this.img.draggable = false
    // Assign styles
    assign(this.img.style, IMAGE_STYLES)
    assign(this.container.style, CONTAINER_STYLES)

    // Initialize Focus coordinates
    this.focus = this.getFocus()

    // Create and attach the retina focal point, start listeners and attach focus
    this.enable()
  }

  private getFocus(): Focus {
    return this.options.focus
      ? this.options.focus
      : {
          x: parseFloat(this.img.getAttribute('data-focus-x')) || 0,
          y: parseFloat(this.img.getAttribute('data-focus-y')) || 0,
        }
  }

  /**
   * Creates the focal point retina and
   */
  public enable() {
    if (!this._enabled) {
      // Create and attach the retina focal point
      this.retina = document.createElement('img')
      this.retina.src = this.options.retina
      this.retina.draggable = false
      this.container.appendChild(this.retina)
      assign(this.retina.style, RETINA_STYLES)
      this.startListening()
      this.setFocus(this.focus)
      this._enabled = true
    }
  }

  public disable() {
    if (this._enabled && this.retina) {
      this.stopListening()
      this.container.removeChild(this.retina)
      this._enabled = false
    }
  }

  get enabled(): boolean {
    return this._enabled
  }

  public startListening() {
    // Bind container events
    this.container.addEventListener('mousedown', this.startDragging)
    this.container.addEventListener('mousemove', this.handleMove)
    this.container.addEventListener('mouseup', this.stopDragging)
    this.container.addEventListener('mouseleave', this.stopDragging)
    this.container.addEventListener('touchend', this.stopDragging)

    // temporarily cast config objs until this issue is resolved
    // https://github.com/Microsoft/TypeScript/issues/9548
    this.container.addEventListener('touchstart', this.startDragging, {
      passive: true,
    } as any)
    this.container.addEventListener('touchmove', this.handleMove, {
      passive: true,
    } as any)

    this.img.addEventListener('load', this.updateRetinaPositionFromFocus)
  }

  public stopListening() {
    this.container.removeEventListener('mousedown', this.startDragging)
    this.container.removeEventListener('mousemove', this.handleMove)
    this.container.removeEventListener('mouseup', this.stopDragging)
    this.container.removeEventListener('mouseleave', this.stopDragging)
    this.container.removeEventListener('touchend', this.stopDragging)
    this.container.removeEventListener('touchstart', this.startDragging)
    this.container.removeEventListener('touchmove', this.handleMove)
    this.img.removeEventListener('load', this.updateRetinaPositionFromFocus)
  }

  public setFocus(focus: Focus) {
    this.focus = focus
    this.img.setAttribute('data-focus-x', focus.x.toString())
    this.img.setAttribute('data-focus-y', focus.y.toString())
    this.updateRetinaPositionFromFocus()
    this.options.onChange(focus)
  }

  private startDragging = (e: MouseEvent | TouchEvent) => {
    e.preventDefault()
    this.isDragging = true
    e instanceof MouseEvent
      ? this.updateCoordinates(e.clientX, e.clientY)
      : this.updateCoordinates(e.touches[0].clientX, e.touches[0].clientY)
  }

  private handleMove = (e: MouseEvent | TouchEvent) => {
    e.preventDefault()
    if (e instanceof MouseEvent) {
      this.updateCoordinates(e.clientX, e.clientY)
    } else {
      const touch = e.touches[0]
      const touchedEl = document.elementFromPoint(touch.pageX, touch.pageY)
      touchedEl !== this.retina && touchedEl !== this.img
        ? this.stopDragging()
        : this.updateCoordinates(touch.clientX, touch.clientY)
    }
  }

  private stopDragging = () => {
    this.isDragging = false
  }

  private calculateOffsetFromFocus() {
    const { width, height } = this.img.getBoundingClientRect()
    const offsetX = width * (this.focus.x / 2 + 0.5)
    const offsetY = height * (this.focus.y / -2 + 0.5)
    return { offsetX, offsetY }
  }

  private updateRetinaPositionFromFocus = () => {
    this.updateRetinaPosition(this.calculateOffsetFromFocus())
  }

  private updateRetinaPosition = (offsets: { offsetX: number; offsetY: number }) => {
    this.retina.style.top = `${offsets.offsetY}px`
    this.retina.style.left = `${offsets.offsetX}px`
  }

  private updateCoordinates(clientX: number, clientY: number) {
    if (!this.isDragging) return // bail if not dragging
    const { width, height, left, top } = this.img.getBoundingClientRect()

    // Calculate FocusPoint coordinates
    const offsetX = clientX - left
    const offsetY = clientY - top
    const x = (offsetX / width - 0.5) * 2
    const y = (offsetY / height - 0.5) * -2

    // TODO: Figure out an elegant way to use the setFocus API without
    // having to recalculate the offset from focus
    this.setFocus({ x, y })
  }
}

const IMG_STYLES = {
  // Set these styles in case the image dimensions
  // are smaller than the container's
  minHeight: '100%',
  minWidth: '100%',
}

const RESIZE_LISTENER_OBJECT_STYLES = {
  height: '100%',
  width: '100%',
  border: 'none',

  // set these styles to emulate "visibility: hidden"
  // can't use visibility because it breaks the object
  // events in Firefox
  opacity: 0,
  zIndex: -1,
  pointerEvents: 'none',
}

const DEFAULT_IMAGE_OPTIONS: FocusedImageOptions = {
  debounceTime: 17,
  updateOnWindowResize: true,
  updateOnContainerResize: false,
  containerPosition: 'relative',
}

export class FocusedImage {
  focus: Focus
  options: FocusedImageOptions
  container: HTMLElement
  img: HTMLImageElement
  resizeListenerObject: HTMLObjectElement
  listening: boolean = false
  debounceApplyShift: () => void

  constructor(imageNode: HTMLImageElement, options: FocusedImageOptions = {}) {
    // Merge in options
    this.options = assign(DEFAULT_IMAGE_OPTIONS, options)

    // Set up element references
    this.img = imageNode
    this.container = imageNode.parentElement

    // Set up instance
    if (this.img['__focused_image_instance__']) {
      this.img['__focused_image_instance__'].stopListening()
      this.img.removeEventListener('load', this.applyShift)
    }
    this.img['__focused_image_instance__'] = this

    // Add image load event listener
    this.img.addEventListener('load', this.applyShift)

    // Set up styles
    assign(this.container.style, CONTAINER_STYLES)
    this.container.style.position = this.options.containerPosition
    assign(this.img.style, IMG_STYLES, ABSOLUTE_STYLES)

    // Create debouncedShift function
    this.debounceApplyShift = debounce(this.applyShift, this.options.debounceTime)

    // Initialize focus
    this.focus = this.options.focus
      ? this.options.focus
      : {
          x: parseFloat(this.img.getAttribute('data-focus-x')) || 0,
          y: parseFloat(this.img.getAttribute('data-focus-y')) || 0,
        }

    // Start listening for resize events
    this.startListening()

    // Set focus
    this.setFocus(this.focus)
  }

  public setFocus = (focus: Focus) => {
    this.focus = focus
    this.img.setAttribute('data-focus-x', focus.x.toString())
    this.img.setAttribute('data-focus-y', focus.y.toString())
    this.applyShift()
  }

  public applyShift = () => {
    const { naturalWidth: imageW, naturalHeight: imageH } = this.img
    const { width: containerW, height: containerH } = this.container.getBoundingClientRect()

    // Amount position will be shifted
    let hShift = '0'
    let vShift = '0'

    if (!(containerW > 0 && containerH > 0 && imageW > 0 && imageH > 0)) {
      return false // Need dimensions to proceed
    }

    // Which is over by more?
    const wR = imageW / containerW
    const hR = imageH / containerH

    // Reset max-width and -height
    this.img.style.maxHeight = null
    this.img.style.maxWidth = null

    // Minimize image while still filling space
    if (imageW > containerW && imageH > containerH) {
      this.img.style[wR > hR ? 'maxHeight' : 'maxWidth'] = '100%'
    }

    if (wR > hR) {
      hShift = `${this.calcShift(hR, containerW, imageW, this.focus.x)}%`
    } else if (wR < hR) {
      vShift = `${this.calcShift(wR, containerH, imageH, this.focus.y, true)}%`
    }

    this.img.style.top = vShift
    this.img.style.left = hShift
  }

  public startListening() {
    if (this.listening) {
      return
    }
    this.listening = true
    if (this.options.updateOnWindowResize) {
      window.addEventListener('resize', this.debounceApplyShift)
    }
    if (this.options.updateOnContainerResize) {
      const object = document.createElement('object')
      assign(object.style, RESIZE_LISTENER_OBJECT_STYLES, ABSOLUTE_STYLES)
      // Use load event callback because contentDocument doesn't exist
      // until this fires in Firefox
      object.addEventListener('load', (_: Event) =>
        object.contentDocument.defaultView.addEventListener('resize', () => this.debounceApplyShift()),
      )
      object.type = 'text/html'
      object.setAttribute('aria-hidden', 'true')
      object.tabIndex = -1
      this.container.appendChild(object)
      object.data = 'about:blank'
      this.resizeListenerObject = object
    }
  }

  public stopListening() {
    if (!this.listening) {
      return
    }
    this.listening = false
    window.removeEventListener('resize', this.debounceApplyShift)
    if (this.resizeListenerObject && this.resizeListenerObject.contentDocument) {
      this.resizeListenerObject.contentDocument.defaultView.removeEventListener('resize', this.debounceApplyShift)
      this.container.removeChild(this.resizeListenerObject)
      this.resizeListenerObject = null
    }
  }

  // Calculate the new left/top percentage shift of an image
  private calcShift(
    conToImageRatio: number,
    containerSize: number,
    imageSize: number,
    focusSize: number,
    toMinus?: boolean,
  ) {
    const containerCenter = Math.floor(containerSize / 2) // Container center in px
    const focusFactor = (focusSize + 1) / 2 // Focus point of resize image in px
    const scaledImage = Math.floor(imageSize / conToImageRatio) // Can't use width() as images may be display:none
    let focus = Math.floor(focusFactor * scaledImage)
    if (toMinus) focus = scaledImage - focus
    let focusOffset = focus - containerCenter // Calculate difference between focus point and center
    const remainder = scaledImage - focus // Reduce offset if necessary so image remains filled
    const containerRemainder = containerSize - containerCenter
    if (remainder < containerRemainder) focusOffset -= containerRemainder - remainder
    if (focusOffset < 0) focusOffset = 0

    return (focusOffset * -100) / containerSize
  }
}
