import { ComponentProps, ReactNode } from 'react'
import { FormattedMessage } from 'react-intl'
import { Button, ButtonProps } from '../button'
import { GenericEmptyIcon } from '../icon'
import { Box, Card, VStack } from '../layout'
import { styled } from '../theme/default'
import { Heading } from '../typography'
import { Link } from 'react-router-dom'

type EmptyStateActionButtonProps =
  | {
      title: string
      // Makes a button with an onClick handler
      onClick: () => void
      to?: never
      href?: never
    }
  | {
      title: string
      // Makes a button with a react router route
      to: ComponentProps<typeof Link>['to']
      onClick?: never
      href?: never
    }
  | {
      title: string
      // Makes a button with an HTML anchor path
      href: string
      onClick?: never
      to?: never
    }

type EmptyStateProps = ComponentProps<typeof Card> & {
  illustration?: ReactNode | false

  intent?: ButtonProps['intent']

  title?: string | number

  /**
   * Optionally provide a custom description.
   * @default "This is where you'll be able to see your content."
   */
  description?: string

  /**
   * Optionally provide a context for the default description.
   */
  descriptionContext?: string

  /**
   * The action config for the empty state
   * The props used here will determine the button type,
   * - `to`: React Router Link,
   * - `href`: HTML Anchor
   * - `onClick`: Button
   */
  action?: EmptyStateActionButtonProps

  /**
   * Common appearance options for the empty state
   * @default 'inline'
   */
  appearance?: 'inline' | 'card'
}

function getActionButtonElementType(actionProps: EmptyStateActionButtonProps): ReactNode {
  if (actionProps.href) {
    return 'a'
  }

  if (actionProps.to) {
    return Link
  }

  return 'button'
}

const DescriptionFactory = styled('span', {
  capsize: {
    lineGap: 15,
  },
  textAlign: 'center',
  maxWidth: '600px',
})

const EmptyStateWrapper = styled('div', {
  padding: '$2',
  width: '100%',

  variants: {
    appearance: {
      card: {
        padding: '$9',
      },
      inline: {},
    },
  },
})

/**
 * A component that indicates an empty list or missing content
 * @author Alyssa Gutierrez
 * @author Evan Hennessy
 * @author April Nickel
 */
export function EmptyState({
  intent,
  illustration: Illustration = GenericEmptyIcon,
  title,
  description,
  descriptionContext,
  action,
  appearance = 'inline',
  ...props
}: EmptyStateProps) {
  Illustration = typeof Illustration === 'function' ? <Illustration size="300px" /> : Illustration

  return (
    <EmptyStateWrapper as={appearance === 'card' ? Card : undefined} appearance={appearance} border {...props}>
      <VStack align="center" justify="center">
        {Illustration && (
          <VStack align="center" css={{ paddingBottom: '$4' }}>
            {Illustration}
          </VStack>
        )}
        <VStack align="center">
          <Heading
            size="title3"
            css={{
              textAlign: 'center',
              capsize: { fontSize: 24, lineGap: 15 },
            }}
          >
            {title ? title : <FormattedMessage id="emptystate.title" />}
          </Heading>
          <DescriptionFactory>
            {description ? (
              description
            ) : (
              <FormattedMessage
                id="emptystate.description"
                defaultMessage={description}
                values={{ context: descriptionContext }}
              />
            )}
          </DescriptionFactory>
        </VStack>
        {action?.title && (action?.onClick || action?.to || action?.href) ? (
          <Box css={{ paddingTop: '$3' }}>
            <Button as={getActionButtonElementType(action) as any} intent={intent} {...action}>
              {action.title}
            </Button>
          </Box>
        ) : null}
      </VStack>
    </EmptyStateWrapper>
  )
}
