import { ItemElement, ItemProps, ItemRenderer } from '@react-types/shared'
import React, { Key, ReactElement, ReactNode } from 'react'
import { DefaultItemProps } from './types'

interface PartialNode<T> {
  type?: string
  key?: Key
  value?: T
  element?: ReactElement
  wrapper?: (element: ReactElement) => ReactElement
  rendered?: ReactNode
  textValue?: string
  'aria-label'?: string
  index?: number
  renderer?: ItemRenderer<T>
  hasChildNodes?: boolean
  childNodes?: () => IterableIterator<PartialNode<T>>
  props?: any
  shouldInvalidate?: (context: unknown) => boolean
}

function ActionItem<T>(_: ItemProps<T>): ReactElement {
  return null
}

ActionItem.getCollectionNode = function* getCollectionNode<T>(
  props: ItemProps<T>,
  context: any,
): Generator<PartialNode<T>> {
  let { childItems, title, children } = props

  let rendered = props.title || props.children
  let textValue = props.textValue || (typeof rendered === 'string' ? rendered : '') || props['aria-label'] || ''
  if (!textValue && !context?.suppressTextValueWarning) {
    console.warn(
      '<Item> with non-plain text contents is unsupported by type to select for accessibility. Please add a `textValue` prop.',
    )
  }

  yield {
    type: 'item',
    props: { ...props, __hasAction: true },
    rendered,
    textValue,
    'aria-label': props['aria-label'],
    hasChildNodes: hasChildItems(props),
    *childNodes() {
      if (childItems) {
        for (let child of childItems) {
          yield {
            type: 'item',
            subtype: 'action',
            value: child,
          }
        }
      } else if (title) {
        let items: PartialNode<T>[] = []
        React.Children.forEach(children, child => {
          items.push({
            type: 'item',
            element: child as ItemElement<T>,
          })
        })

        yield* items
      }
    },
  }
}

function hasChildItems<T>(props: ItemProps<T>) {
  if (props.hasChildItems != null) {
    return props.hasChildItems
  }

  if (props.childItems) {
    return true
  }

  if (props.title && React.Children.count(props.children) > 0) {
    return true
  }

  return false
}

let _ActionItem = ActionItem as <T>(props: ItemProps<T> & DefaultItemProps<T>) => JSX.Element
export { _ActionItem as ActionItem }
