import { useNumberFieldState } from '@react-stately/numberfield'
import { VariantProps } from '@stitches/react'
import { pick } from 'lodash-es'
import { ComponentProps, forwardRef, useRef } from 'react'
import { useButton, useLocale, useNumberField } from 'react-aria'
import { mergeRefs } from 'react-laag'
import { FieldDescription, FieldWrapper, LabelDescriptionWrapper, FieldErrorAndHelpText } from '../form'
import { useCustomValidity } from '../form/helpers'
import { ValidationMessageProps } from '../form/helpers/types'
import { ChevronDownIcon, ChevronUpIcon } from '../icon'
import { Box, HStack } from '../layout'
import { TextInputFactory } from '../textinput/TextInputFactory'
import { styled } from '../theme/default'
import { NumberFormatOptions } from '../typings'
import { Label } from '../typography'

const NumberInputFactory = styled(TextInputFactory, {
  '&:focus': {
    boxShadow: `0 0 0 2px $$shadowColor`,
  },

  variants: {
    inputStyle: {
      none: { textAlign: 'left' },
      accounting: {
        textAlign: 'right',
        paddingLeft: '$4',
      },
    },

    stepper: {
      true: {},
      false: { paddingRight: '$9' },
    },

    /** @private */
    focused: {
      true: { boxShadow: `0 0 0 2px $$shadowColor` },
    },
  },
})

const NumberButtonsGroup = styled('div', {
  display: 'flex',
  flexDirection: 'column',
  position: 'absolute',
  right: '0',
  top: '0',
  alignItems: 'center',
})

const NumberButton = styled('button', {
  $$borderRadius: '$radii$1',

  WebkitFontSmoothing: 'antialiased',
  WebkitAppearance: 'none',
  textDecoration: 'none',
  border: 'none',
  outline: 'none',
  cursor: 'pointer',
  transition: '225ms',
  borderRadius: '$$borderRadius',
  fontFamily: '$sans',
  fontWeight: '$medium',
  userSelect: 'none',
  display: 'inline-flex',
  justifyContent: 'center',
  alignItems: 'center',
  transform: 'scale(1)',
  alignSelf: 'start',
  backgroundColor: '$gray4',
  color: '$primary',

  '&:not([disabled]):hover': {
    backgroundColor: '$gray3',
  },
  '&:active': {
    $$shadowColor: '$colors$primary',
    boxShadow: `0 0 0 3px $$shadowColor`,
    transform: 'scale(0.99)',
  },
  '&:focus': {
    boxShadow: `0 0 0 3px $$shadowColor`,
  },

  '&::-moz-focus-inner': {
    border: 0,
  },

  '&[disabled]': {
    pointerEvents: 'none',
    color: '$gray1',
    backgroundColor: '$gray5',
    backgroundImage: 'none',
    boxShadow: 'none',
    cursor: 'not-allowed',
  },

  variants: {
    position: {
      top: {
        borderBottomLeftRadius: 0,
        borderTopLeftRadius: 0,
        borderBottomRightRadius: 0,
      },

      bottom: {
        borderBottomLeftRadius: 0,
        borderTopLeftRadius: 0,
        borderTopRightRadius: 0,
      },
    },

    size: {
      small: {
        $$borderRadius: 'calc($radii$1 / 2)',
        height: 'calc(($controlHeightSmall / 2) - 1px)',
        paddingInline: '$2',
      },
      regular: {
        height: 'calc(($controlHeightRegular / 2) - 1px)',
        paddingInline: '$3',
      },
      large: {
        height: 'calc(($controlHeightLarge / 2) - 1px)',
        paddingInline: '$3',
      },
    },
  },

  defaultVariants: {
    size: 'regular',
  },
})

type AriaNumberInputProps = Parameters<typeof useNumberField>[0]
type NumberInputProps = VariantProps<typeof NumberInputFactory> &
  Omit<ComponentProps<typeof NumberInputFactory>, 'onChange'> &
  AriaNumberInputProps &
  ValidationMessageProps & {
    /**
     * Add an optional description below the label
     */
    description?: string

    /** Options for formatting the number
     * @see https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Intl/NumberFormat
     */
    formatOptions?: NumberFormatOptions

    /** Optionally hide the stepper buttons */
    stepper?: boolean
    /**
     * The label for the field.
     * @required even if hidden
     */
    label?: string
    'aria-label'?: string
  }

/**
 * Number Input used primarily in forms.
 * @status Stable
 * @category Inputs
 * @Author Evan Hennessy
 * @see https://react-stately.dev/numberinput
 * @example
 * <NumberInput
 *  label="Number Input"
 *  value={numberInputValue}
 *  onChange={(value) => setNumberInputValue(value)}
 * />
 */
export const NumberInput = forwardRef<HTMLInputElement, NumberInputProps>(
  (
    { size, description, helpText, errorMessage, stepper = false, css, inputStyle = 'none', ...props },
    forwardedRef,
  ) => {
    const { locale } = useLocale()

    const aliasProps: Partial<AriaNumberInputProps> = {
      isDisabled: props.disabled,
      isRequired: props.required,
      isReadOnly: props.readOnly,
      validationState: props.invalid ? 'invalid' : 'valid',
    }

    const state = useNumberFieldState({ ...aliasProps, ...props, locale })
    const inputRef = useRef()
    const { decrementButtonProps, groupProps, incrementButtonProps, inputProps, labelProps, descriptionProps } =
      useNumberField({ ...aliasProps, ...props }, state, inputRef)

    const { buttonProps: incrementProps } = useButton(incrementButtonProps, null)
    const { buttonProps: decrementProps } = useButton(decrementButtonProps, null)

    const validityProps = useCustomValidity(inputRef, { ...props, errorMessage })

    if (inputProps.value === null) {
      inputProps.value = NaN
    }

    let value = inputProps.value
    if (inputStyle === 'accounting' && props.formatOptions?.currency) {
      const currencySymbol = new Intl.NumberFormat(locale, props.formatOptions).formatToParts(10)[0].value
      value = (value as string).replace(currencySymbol, '')
    }

    return (
      <FieldWrapper css={css}>
        {(props.label || description) && (
          <LabelDescriptionWrapper>
            <Label size={size as any} {...labelProps}>
              {props.label}
            </Label>
            <FieldDescription description={description} {...descriptionProps} />
          </LabelDescriptionWrapper>
        )}
        <HStack align="center" css={{ position: 'relative' }} {...groupProps}>
          {inputStyle === 'accounting' && <Box css={{ position: 'absolute', left: '$3' }}>$</Box>}
          <NumberInputFactory
            size={size as any}
            inputStyle={inputStyle}
            stepper={stepper}
            {...validityProps}
            {...pick(props, ['position', 'size', 'css', 'data-testid'])}
            {...(inputProps as any)}
            name={props.name}
            value={value}
            placeholder={props.placeholder}
            ref={mergeRefs(inputRef, forwardedRef)}
          />
          {stepper && (
            <NumberButtonsGroup>
              <NumberButton type="button" size={size} disabled={props.disabled} position="top" {...incrementProps}>
                <ChevronUpIcon size="8px" />
              </NumberButton>
              <NumberButton type="button" size={size} disabled={props.disabled} position="bottom" {...decrementProps}>
                <ChevronDownIcon size="8px" />
              </NumberButton>
            </NumberButtonsGroup>
          )}
        </HStack>
        <FieldErrorAndHelpText helpText={helpText} errorMessage={errorMessage} invalid={props.invalid} />
      </FieldWrapper>
    )
  },
)

NumberInput.toString = NumberInputFactory.toString
NumberInput.defaultProps = {
  minValue: 0,
  size: 'regular',
}