import { clamp } from 'lodash-es'
import { ActionType, Hooks, Meta, TableInstance, TableProps, TableState } from 'react-table'

export function useGridLayout(hooks: Hooks) {
  hooks.stateReducers.push(reducer)
  hooks.getTableProps.push(getTableProps)
  hooks.getHeaderProps.push(getHeaderProps)
}

useGridLayout.pluginName = 'useGridLayout'

type GridLayoutExtension = {
  state: { gridLayout: any }
}

const getTableProps = (props: TableProps, { instance }: Meta<{}> & { instance: GridLayoutExtension }) => {
  const columnWidths = instance.visibleColumns.map(column => {
    const index = instance.allColumns.findIndex(col => col.id === column.id)
    const width = instance.state.gridLayout.columnWidths[index] ?? `auto`
    const maxWidth = column.maxWidth ?? 500
    column.minWidth ||= 100

    if (width === 'max-content') {
      return 'max-content'
    }

    if (column.width !== 150 && width === `auto`) {
      return typeof column.width === 'number'
        ? `minmax(auto, ${clamp(column.width, column.minWidth, maxWidth)}px)`
        : `minmax(auto, ${maxWidth}px})`
    }

    return width === 'auto' ? `minmax(${width}, 1fr)` : `${clamp(width, column.minWidth, maxWidth)}px`
  })

  return [
    props,
    {
      style: {
        display: `inline-grid`,
        gridTemplateColumns: columnWidths.join(` `),
      },
    },
  ]
}

const getHeaderProps = (props, { column }) => [
  props,
  {
    id: `header-cell-${column.id}`,
    style: {
      position: `sticky`, //enables a scroll wrapper to be placed around the table and have sticky headers
    },
  },
]

function reducer(
  state: TableState & GridLayoutExtension['state'],
  action: ActionType,
  _: TableState,
  instance: TableInstance,
) {
  if (action.type === `init`) {
    return {
      gridLayout: {
        columnWidths: instance.columns.map(() => `auto`),
      },
      ...state,
    }
  }

  if (action.type === `columnStartResizing`) {
    const { columnId } = action
    const columnIndex = instance.visibleColumns.findIndex(col => col.id === columnId)
    const elWidth = getElementWidth(columnId)

    if (elWidth !== undefined) {
      return {
        ...state,
        gridLayout: {
          ...state.gridLayout,
          columnId,
          columnIndex,
          startingWidth: elWidth,
        },
      }
    } else {
      return state
    }
  }

  if (action.type === `columnResizing`) {
    const { columnIndex, startingWidth, columnWidths } = state.gridLayout

    const change = state.columnResizing.startX - action.clientX
    const newWidth = startingWidth - change
    const columnWidthsCopy = [...columnWidths]
    columnWidthsCopy[columnIndex] = clamp(newWidth, 20, 500)

    return {
      ...state,
      gridLayout: {
        ...state.gridLayout,
        columnWidths: columnWidthsCopy,
      },
    }
  }

  if (action.type === `resetColumnSize`) {
    const { columnIndex, columnWidths } = state.gridLayout

    const columnWidthsCopy = [...columnWidths]
    columnWidthsCopy[columnIndex] = 'max-content'

    return {
      ...state,
      gridLayout: {
        ...state.gridLayout,
        columnWidths: columnWidthsCopy,
      },
    }
  }
}

function getElementWidth(columnId: string) {
  const width = document.getElementById(`header-cell-${columnId}`)?.offsetWidth

  if (width !== undefined) {
    return width
  }
}
