import { actions, HeaderProps, Hooks, TableInstance } from 'react-table'
import { TableProps } from '../types/TableProps'

export function useDblClickResetColumn(hooks: Hooks) {
  hooks.getResizerProps.push(getResizerProps)
}

actions.resetColumnSize = 'resetColumnSize'

const getResizerProps = (
  props: TableProps,
  { instance, header }: { header: HeaderProps<object>; instance: TableInstance<object> },
) => {
  const { dispatch } = instance

  return [
    props,
    {
      onDoubleClick: () => {
        dispatch({ type: actions.resetColumnSize, columnId: header.id })
      },
    },
  ]
}
