import { CSS } from '@stitches/react'
import { ComponentProps } from 'react'
import { Box } from '../layout'
import { keyframes, styled } from '../theme/default'

const spin = keyframes({
  '0%': {
    transform: 'rotate(0)',
  },
  '100%': {
    transform: 'rotate(360deg)',
  },
})

const offsetStroke = keyframes({
  '0%': {
    strokeDashoffset: '96',
  },
  '100%': {
    strokeDashoffset: 0,
  },
})

const SpinnerContainer = styled('svg', {
  animation: `${spin} 2s linear infinite`,
})

const SpinnerCircle = styled('circle', {
  strokeDashoffset: '96',
  strokeDasharray: 48,
  strokeWidth: '1.92px',
  strokeMiterlimit: 16,
  strokeLinecap: 'round',
  animation: `${offsetStroke} 1.6s cubic-bezier(0.4, 0.15, 0.6, 0.85) infinite`,
  stroke: 'currentColor',
  fill: 'transparent',
})

export type SpinnerProps = ComponentProps<typeof Box> & {
  /**
   * The size of the spinner
   */
  size?: ComponentProps<typeof Box>['css']['size']
  /**
   * The color of the spinner
   */
  // color?: TCssWithBreakpoints
  css?: CSS
}

/**
 * Used for indeterminate loading. Make sure to set a color that will constrast with the background.
 */
export function Spinner({ size, color = '', css, ...props }: SpinnerProps) {
  return (
    <Box css={{ lineHeight: 0, color, size, ...css }} {...props}>
      <SpinnerContainer x="0px" y="0px" viewBox="0 0 24 24">
        <SpinnerCircle cx="12" cy="12" r="9.6" />
      </SpinnerContainer>
    </Box>
  )
}

Spinner.defaultProps = {
  size: '$5',
}
