import { AriaTextFieldOptions, useTextField } from '@react-aria/textfield'
import { ChangeEvent, ComponentProps, forwardRef, InputHTMLAttributes, useRef } from 'react'
import { mergeProps } from 'react-aria'
import { mergeRefs } from 'react-laag'
import { FieldErrorAndHelpText, FieldLabelAndDescription, FieldLabelAndDescriptionProps, FieldWrapper } from '../form'
import { useCustomValidity } from '../form/helpers'
import { ValidationMessageProps } from '../form/helpers/types'
import { Box } from '../layout/Box'
import { Spinner } from '../spinner/Spinner'
import { TextInputFactory } from './TextInputFactory'

const iconSizes = {
  small: '$2',
  regular: '$3',
  large: '$4',
}

type TextInputProps = Omit<ComponentProps<typeof TextInputFactory>, 'iconBefore' | 'onChange'> &
  Omit<AriaTextFieldOptions<'input'>, 'onChange'> &
  FieldLabelAndDescriptionProps &
  ValidationMessageProps & {
    iconBefore?: JSX.Element | React.ReactNode
    loading?: boolean
    onChange?: (value: string, event?: ChangeEvent<HTMLInputElement>) => void
  }

export type { TextInputProps }

export const TextInput = forwardRef(({ errorMessage, ...props }: TextInputProps, forwardedRef) => {
  const ref = useRef<HTMLInputElement>(null)
  const { label, labelProps } = props

  const { descriptionProps, errorMessageProps, inputProps, labelProps: ariaLabelProps } = useTextField(props, ref)
  const validityProps = useCustomValidity(ref, props)

  const iconSize = iconSizes[props.size as string]

  const { iconBefore: IconBefore } = props

  return (
    <FieldWrapper css={props?.css}>
      <FieldLabelAndDescription
        label={label}
        labelProps={mergeProps(ariaLabelProps, labelProps)}
        description={props.description}
        descriptionProps={descriptionProps}
      />

      <Box css={{ position: 'relative', isolation: 'isolate', width: '100%' }}>
        {typeof IconBefore === 'function' ? (
          <Box
            css={{
              display: 'flex',
              alignItems: 'center',
              pointerEvents: 'none',
              color: '$placeholderText',
              left: 'calc($5 / 2)',
              top: '50%',
              transform: 'translateY(-50%)',
              position: 'absolute',
              height: 'max-content',
            }}
          >
            <IconBefore size={iconSize} />
          </Box>
        ) : (
          IconBefore || null
        )}
        <TextInputFactory
          ref={mergeRefs(ref, forwardedRef)}
          {...props}
          {...validityProps}
          {...(inputProps as Omit<InputHTMLAttributes<HTMLInputElement>, 'size'>)}
          disabled={props.disabled}
          onChange={e => props.onChange?.(e.currentTarget.value, e)}
          iconBefore={!!props.iconBefore}
          iconAfter={!!props.loading}
        />

        {props.loading ? (
          <Box
            css={{
              display: 'flex',
              alignItems: 'center',
              pointerEvents: 'none',
              color: '$placeholderText',
              right: 'calc($5 / 2)',
              top: '50%',
              transform: 'translateY(-50%)',
              position: 'absolute',
              height: 'max-content',
            }}
          >
            <Spinner size={iconSize} />
          </Box>
        ) : null}
      </Box>

      <FieldErrorAndHelpText
        errorMessage={errorMessage}
        errorMessageProps={errorMessageProps}
        invalid={props.invalid}
        helpText={props.helpText}
        helpTextProps={descriptionProps}
      />
    </FieldWrapper>
  )
})

TextInput.displayName = TextInputFactory.displayName

TextInput.toString = TextInputFactory.toString

TextInput.defaultProps = {
  type: 'text',
  spellCheck: true,
  disabled: false,
  size: 'regular',
}
