import { isNaN, isPlainObject } from 'lodash-es'
import { ColumnInstance, Hooks } from 'react-table'

export const useFormatters = (hooks: Hooks): void => {
  hooks.accessValue.push(accessValue)
}

function isEmptyValue(value: any, emptyValue: any): boolean {
  if (typeof emptyValue !== 'undefined' && value === emptyValue) return true

  if (typeof value === 'undefined' || value === null || isNaN(value)) return true

  if (Array.isArray(value) && value.length <= 0) return true

  if (isPlainObject(value) && Object.getOwnPropertyNames(value).length <= 0) return true

  return false
}

function accessValue(value: any, { column }: { column: ColumnInstance }) {
  if (isEmptyValue(value, column.emptyValue) && typeof column?.defaultValue !== 'undefined') {
    return column?.defaultValue
  }

  if (!!column.cellFormatter) {
    return column.cellFormatter(value)
  }

  return value
}

useFormatters.pluginName = 'useFormatters'
