import { useRadio, useRadioGroup } from '@react-aria/radio'
import { useRadioGroupState } from '@react-stately/radio'
import { VariantProps } from '@stitches/react'
import { AnimateSharedLayout, motion, Transition } from 'framer-motion'
import { createContext, useContext, useMemo, useRef } from 'react'
import { mergeProps, useFocusRing, useId, VisuallyHidden } from 'react-aria'
import { FieldLabelAndDescription, FieldLabelAndDescriptionProps, FieldWrapper } from '../form'
import { CheckCircleIcon } from '../icon'
import { HStack } from '../layout'
import { styled } from '../theme/default'
import { Label } from '../typography/Label'
import { fontPxToNum } from '../typography/utils'

type SegmentedControlOption = {
  label: string
  value: string
  disabled?: boolean
}

type SegmentedControlProps = {
  /** Control options */
  options: SegmentedControlOption[]
  children?: never
  size?: VariantProps<typeof ControlOptionFactory>['controlSize']

  disabled?: Parameters<typeof useRadioGroup>[0]['isDisabled']
  readonly?: Parameters<typeof useRadioGroup>[0]['isReadOnly']
  required?: Parameters<typeof useRadioGroup>[0]['isRequired']
} & Omit<Parameters<typeof useRadioGroup>[0], 'isDisabled' | 'isRequired' | 'isReadonly'> &
  FieldLabelAndDescriptionProps &
  Parameters<typeof useRadioGroupState>[0]

const SegmentedControlContext = createContext<ReturnType<typeof useRadioGroupState>>(null)

const ControlOptionFactory = styled('label', {
  border: '1px solid $gray3',
  backgroundColor: '$background',
  paddingInline: '$7',
  borderRadius: '$controlRegular',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  userSelect: 'none',
  position: 'relative',
  cursor: 'pointer',

  '&, label, span, div': {
    fontWeight: '$medium',
    whiteSpace: 'nowrap',
  },

  '&:hover': {
    backgroundColor: '$gray5',
  },

  variants: {
    focusVisible: {
      true: {
        boxShadow: `0 0 0 3px hsl($colors$hsl_primary / 0.3)`,
      },
    },

    isDisabled: { true: { backgroundColor: '$gray5', color: '$disabled', cursor: 'not-allowed' } },

    controlSize: {
      small: {
        height: '$controlHeightSmall',
        paddingInline: '$3',
        svg: {
          display: 'none',
        },
      },
      regular: {
        height: '$controlHeightRegular',
        paddingInline: '$7',
        svg: {
          size: fontPxToNum(3),
        },
      },
      large: {
        height: '$controlHeightLarge',
        paddingInline: '$8',
        svg: {
          size: fontPxToNum(4),
        },
      },
    },

    isSelected: {
      true: {
        borderColor: '$primary',
        borderWidth: '2px',
        backgroundColor: '$primary',
        color: '$white',
        position: 'absolute',
        zIndex: '$focused',
        width: 'calc(100% + 2px)',
        height: 'calc(100% + 2px)',
        cursor: 'default',

        '&:hover': {
          backgroundColor: '$primary',
        },
      },
    },
  },

  defaultVariants: {
    controlSize: 'regular',
  },

  compoundVariants: [
    {
      isDisabled: true,
      isSelected: true,
      css: {
        borderColor: '$gray3',
        backgroundColor: '$gray3',
        color: '$disabled',
        cursor: 'not-allowed',
        pointerEvents: 'none',
      },
    },
  ],
})

const SegmentedControlGrid = styled('div', {
  $$optionsCount: '1',
  display: 'grid',
  gridTemplateColumns: 'repeat($$optionsCount, 1fr)',

  '.gc-control-parent:first-child': {
    borderTopRightRadius: 0,
    borderBottomRightRadius: 0,
  },

  '.gc-control-parent:not(:first-child):not(:last-child)': {
    borderRadius: 0,
    borderLeftWidth: 0,
  },

  '.gc-control-parent:last-child': {
    borderTopLeftRadius: 0,
    borderBottomLeftRadius: 0,
    borderLeftWidth: 0,
  },

  variants: {
    size: {
      small: {},
    },
  },
})

export function SegmentedControl(props: SegmentedControlProps) {
  const { label, labelProps, description, options } = props

  const mapBooleanProps = {
    isDisabled: props.disabled,
    isReadOnly: props.readonly,
    isRequired: props.required,
  }

  const state = useRadioGroupState({
    ...props,
    ...mapBooleanProps,
    defaultValue: options[0].value,
  })
  const { radioGroupProps, labelProps: ariaLabelProps } = useRadioGroup({ ...props, ...mapBooleanProps, label }, state)

  const layoutId = useId()

  const controlOptions = useMemo(
    () =>
      options.map((option, index) => (
        <ControlOption
          key={option.value}
          size={props.size}
          first={index === 0}
          last={index === options.length - 1}
          {...option}
        />
      )),
    [options],
  )

  return (
    <FieldWrapper as="fieldset" {...radioGroupProps}>
      <FieldLabelAndDescription
        label={label}
        labelProps={mergeProps(ariaLabelProps, labelProps)}
        description={description}
      />

      <SegmentedControlGrid css={{ $$optionsCount: options.length ?? 1 }}>
        <SegmentedControlContext.Provider value={state}>
          <AnimateSharedLayout key={layoutId}>{controlOptions}</AnimateSharedLayout>
        </SegmentedControlContext.Provider>
      </SegmentedControlGrid>
    </FieldWrapper>
  )
}

SegmentedControl.defaultProps = {
  size: 'regular',
}

const controlOptionTransition: Transition = {
  type: 'spring',
  bounce: 0.15,
  duration: 0.3,
}

function ControlOption(
  props: SegmentedControlOption & {
    size?: VariantProps<typeof ControlOptionFactory>['controlSize']
    first?: boolean
    last?: boolean
  },
) {
  const { label, size } = props
  const state = useContext(SegmentedControlContext)
  const ref = useRef(null)
  const { inputProps } = useRadio({ ...props, 'aria-label': label }, state, ref)
  const { isFocusVisible, focusProps } = useFocusRing()

  const isSelected = state.selectedValue === props.value

  const borderRadiusAccessor = props?.first ? 'first' : props?.last ? 'last' : 'default'
  const animateStyles = {
    first: {
      borderTopLeftRadius: 5,
      borderBottomLeftRadius: 5,
      borderTopRightRadius: 0,
      borderBottomRightRadius: 0,
    },
    last: {
      borderTopLeftRadius: 0,
      borderBottomLeftRadius: 0,
      borderTopRightRadius: 5,
      borderBottomRightRadius: 5,
    },
    default: {
      borderRadius: 0,
    },
  }[borderRadiusAccessor]

  return (
    <ControlOptionFactory
      isDisabled={state.isDisabled}
      className="gc-control-parent"
      key={props.value}
      controlSize={size}
    >
      <VisuallyHidden>
        <input {...inputProps} {...focusProps} />
      </VisuallyHidden>

      <HStack align="center" space="small">
        <Label as="span" css={{ pointerEvents: 'none' }} size={size as any}>
          {label}
        </Label>
      </HStack>

      {isSelected && (
        <ControlOptionFactory
          controlSize={size}
          isSelected
          isDisabled={state.isDisabled}
          focusVisible={isFocusVisible}
          className="gc-control-selector"
          as={motion.div}
          initial={false}
          animate={animateStyles}
          transition={controlOptionTransition}
          layoutId="gc-control-selector"
        >
          <HStack align="center" space="small">
            <CheckCircleIcon size="$3" />
            <Label as="span" css={{ pointerEvents: 'none' }} size={size as any}>
              {label}
            </Label>
          </HStack>
        </ControlOptionFactory>
      )}
    </ControlOptionFactory>
  )
}
