import { AnimatePresence, motion } from 'framer-motion'
import { Spring, Tween } from 'framer-motion/types/types'
import { useContext } from 'react'
import { useHistory } from 'react-router-dom'
import { Button } from '../button'
import { ArrowLeftIcon } from '../icon'
import { styled } from '../theme/default'
import { Heading } from '../typography'
import { HeightContext, stackRouterTransition } from './heightContext'
import type { StackRouteProps } from './StackPage'

const StackRouterHeader = styled(motion.div, {
  backgroundColor: '$gray5',
  padding: '0 $4',
  height: '$$headerHeight',
  zIndex: '$default',
  position: 'relative',
  isolation: 'isolate',
  width: '100%',
  display: 'flex',
  gridTemplateColumns: '1fr auto 1fr',
  alignItems: 'center',
})

const StackRouterHeading = styled(motion.span, Heading, {
  gridColumn: '2',
  position: 'absolute',
  width: '100%',
  textAlign: 'center',
  left: 0,
  fontWeight: '$medium',
  userSelect: 'none',
  pointerEvents: 'none',
  fontSize: '$4',
})

const headingTransition: Spring = { type: 'spring', bounce: 0.2, duration: stackRouterTransition.duration, delay: 0.05 }
const headingContainerTransition: Tween = { type: 'tween', duration: stackRouterTransition.duration }

export function StackHeader({ title, hasHeader = true, hasBackButton }: Partial<StackRouteProps>) {
  const history = useHistory()
  const paneHeight = useContext(HeightContext)

  const showBackButton = history.length > 1 && hasBackButton

  const headingVariants = {
    initial() {
      const isPush = history.action === 'PUSH'
      return {
        x: isPush ? '100%' : '-25%',
        opacity: 0,
        transition: headingTransition,
      }
    },
    enter() {
      return {
        x: 0,
        opacity: 1,
        transition: headingTransition,
      }
    },
    exit({ action = history.action } = {}) {
      const isPop = action === 'POP'

      return {
        x: isPop ? '100%' : '-10%',
        opacity: 0,
        transition: headingTransition,
      }
    },
  }

  const headerContainerVariants = {
    normal: {
      height: 80,
      transition: headingContainerTransition,
    },
    collapsed: {
      height: 0,
      transition: headingContainerTransition,
    },
  }

  return (
    <StackRouterHeader initial={false} animate={hasHeader ? 'normal' : 'collapsed'} variants={headerContainerVariants}>
      <AnimatePresence initial={false} exitBeforeEnter={false} custom={{ action: history.action }}>
        {hasHeader && showBackButton && (
          <Button
            as={motion.button}
            onPress={history.goBack}
            icon={ArrowLeftIcon}
            appearance="minimal"
            round
            disabled={paneHeight.isAnimating()}
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            transition={{ type: 'tween', duration: 0.1 }}
            css={{ color: '$label', gridColumn: '1' }}
          />
        )}
        {hasHeader && (
          <StackRouterHeading key={title} initial="initial" animate="enter" exit="exit" variants={headingVariants}>
            {title}
          </StackRouterHeading>
        )}
      </AnimatePresence>
    </StackRouterHeader>
  )
}
