import cardValidator from 'card-validator'
import { useFormikContext } from 'formik'
import { useEffect } from 'react'
import { mergeProps } from 'react-aria'
import { useFormikHelper } from '../formik'
import { TextInput } from '../textinput'
import { CreditCardChildFieldProps, CreditCardField } from './types'

const DEFAULT_PLACEHOLDER = 'Name on card'
const DEFAULT_NAME = CreditCardField.NAME
const DEFAULT_ARIA_LABEL = 'Name on card'

export function FormikCreditCardNameField({
  name = DEFAULT_NAME,
  placeholder = DEFAULT_PLACEHOLDER,
  ...props
}: CreditCardChildFieldProps) {
  const { registerField, unregisterField } = useFormikContext()

  const [field, meta, helpers] = useFormikHelper({ name, ...props })

  useEffect(() => {
    registerField(name, {
      validate: value => (cardValidator.cardholderName(value).isValid ? undefined : `Must provide a name`),
    })

    return () => unregisterField(name)
  }, [registerField, unregisterField, name])

  return (
    <BaseCreditCardNameField
      aria-label={DEFAULT_ARIA_LABEL}
      {...mergeProps(field, props, { name, placeholder })}
      onChange={value => {
        props?.onChange?.(value)
        helpers.setValue(value)
      }}
      invalid={(meta.touched || meta.submitCount > 0) && !!meta.error}
      errorMessage={(meta.touched || meta.submitCount > 0) && meta.error}
    />
  )
}

export function BaseCreditCardNameField(props: CreditCardChildFieldProps) {
  return <TextInput {...props} autoComplete="ccname" autoCorrect="off" spellCheck="false" id="billingName" />
}
