import { isObject } from 'lodash-es'

function listValue(value: any, title?: string) {
  return isObject(value) ? value : { value: value, title: title !== undefined ? title : value }
}

export function mapListValues(listValues: [] | {}, func: Function) {
  let ret = []
  if (Array.isArray(listValues)) {
    for (let v of listValues) {
      ret.push(func(listValue(v)))
    }
  } else {
    for (let value in listValues) {
      ret.push(func(listValue(value, listValues[value])))
    }
  }
  return ret
}
