import { ComponentProps, createContext } from 'react'
import { useRadioGroupState } from 'react-stately'
import { RadioFactory } from './RadioFactory'

export type RadioContextType = ReturnType<typeof useRadioGroupState> & {
  size: ComponentProps<typeof RadioFactory>['size']
  orientation: 'vertical' | 'horizontal'
}

export const RadioContext = createContext<RadioContextType>(null)
