import { styled } from '../theme/default'
import { fontPxToNum, lineHeightPxToNum } from './utils'

/**
 * `Label` is used for input elements and composes `Text`
 *
 * @note When using `Label` be sure to respect aria guidelines
 * by using `react-aria`'s `useLabel` or by nesting the input in `Label`
 */
export const Label = styled('label', {
  fontWeight: '$medium',
  margin: 0,
  fontSize: '$root',
  fontFamily: '$sans',

  capsize: {
    metrics: 'Modern Era',
    lineGap: 0,
  },

  variants: {
    /** @default "regular" */
    size: {
      /** @deprecated use small, regular, large instead */
      extraSmall: {
        capsize: {
          fontSize: fontPxToNum(1),
          lineGap: lineHeightPxToNum(1),
        },
      },
      /** @deprecated use small, regular, large instead */
      footnote: {
        capsize: {
          fontSize: fontPxToNum(2),
          lineGap: lineHeightPxToNum(1),
        },
      },
      /** @deprecated use small, regular, large instead */
      caption2: {
        capsize: {
          fontSize: fontPxToNum(2),
          lineGap: lineHeightPxToNum(1),
        },
      },
      /** @deprecated use small, regular, large instead */
      caption1: {
        capsize: {
          fontSize: fontPxToNum(3),
          lineGap: lineHeightPxToNum(1),
        },
      },
      /** @deprecated use small, regular, large instead */
      body: {
        capsize: {
          fontSize: fontPxToNum(4),
          lineGap: lineHeightPxToNum(1),
        },
      },

      // Label specific sizes
      small: {
        capsize: {
          fontSize: 12.5,
          lineGap: lineHeightPxToNum(1),
        },
      },

      regular: {
        capsize: {
          fontSize: fontPxToNum(3),
          lineGap: lineHeightPxToNum(1),
        },
      },

      large: {
        capsize: {
          fontSize: fontPxToNum(4),
          lineGap: lineHeightPxToNum(1),
        },
      },
    },

    /**
     * @summary These appearances are used for input elements
     * @default "default"
     */
    appearance: {
      default: {},

      /** Use this appearance when the label represents a group of fields or a fieldset */
      group: {
        color: '$gray1',
        fontWeight: '$regular',
        letterSpacing: '0.6px',
        textTransform: 'uppercase',

        capsize: {
          fontSize: fontPxToNum(1),
          lineGap: lineHeightPxToNum(1),
        },
      },
    },

    /**
     * @default false
     * @type boolean
     */
    disabled: {
      true: {
        color: '$disabled',
      },
    },
  },

  defaultVariants: {
    size: 'regular',
    appearance: 'default',
    disabled: false,
  },

  compoundVariants: [
    { size: 'small', appearance: 'group', css: { fontSize: 11, lineGap: lineHeightPxToNum(1) } },
    { size: 'regular', appearance: 'group', css: { fontSize: 11, lineGap: lineHeightPxToNum(1) } },
    { size: 'large', appearance: 'group', css: { fontSize: fontPxToNum(2), lineGap: lineHeightPxToNum(1) } },
  ],
})
