import { RadioGroupState, useRadioGroupState } from '@react-stately/radio'
import { ComponentProps, createContext, ReactNode, useContext, useEffect, useMemo, useRef, useState } from 'react'
import { mergeProps, useFocusRing, useRadio, useRadioGroup, VisuallyHidden } from 'react-aria'
import { useIntl } from 'react-intl'
import { Overwrite } from 'utility-types'
import { FieldErrorAndHelpText, FieldLabelAndDescription, FieldLabelAndDescriptionProps } from '../form'
import { ValidationMessageProps } from '../form/helpers/types'
import { Box, HStack, VStack } from '../layout'
import { NumberInput } from '../numberinput'
import { styled } from '../theme/default'
import { Text } from '../typography'
import { fontPxToNum, lineHeightPxToNum } from '../typography/utils'
import { calculateColumns } from './utils/calculateColumns'

const AmountContext = createContext<AmountSelectorState>(null)

const AmountGrid = styled('div', {
  $$columns: '2',

  display: 'grid',
  width: '100%',
  gridTemplateColumns: 'repeat($$columns, auto)',
  gap: '$2',
})

const AmountChip = styled('label', {
  $$shadowColor: '$colors$gray3',
  $$borderWidth: '1px',

  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  border: 'none',
  boxShadow: '0 0 0 $$borderWidth $$shadowColor',
  borderRadius: '$1',
  color: '$label',
  backgroundColor: '$background',
  width: '100%',
  minWidth: '50px',
  padding: '0 $2',
  cursor: 'pointer',
  userSelect: 'none',
  fontWeight: '$medium',
  height: 'calc($controlHeightRegular - 1px)',

  span: {
    capsize: {
      fontSize: fontPxToNum(3),
      lineGap: lineHeightPxToNum(1),
    },
  },

  '&:hover': {
    backgroundColor: '$gray5',
  },

  '&:active, &:checked, &:focus': {
    $$shadowColor: '$colors$primary',
  },

  variants: {
    isFocused: {
      true: {
        color: '$primary',
        $$shadowColor: '$colors$primary',
        $$borderWidth: '2px',
      },
    },

    isSelected: {
      true: {
        color: '$primary',
        $$shadowColor: '$colors$primary',
        $$borderWidth: '2px',
      },
    },
  },
})

const CurrencyLocale = styled(Text, {
  backgroundColor: '$background',
  display: 'flex',
  alignItems: 'center',
  position: 'absolute',
  height: '80%',
  top: '12%',
  right: '2px',
  paddingRight: '$2',
  paddingLeft: '$1',
  fontWeight: '$medium',
  userSelect: 'none',

  variants: {
    disabled: {
      true: {
        backgroundColor: '$disabled',
      },
    },
  },
})

type OverideProps = {
  value?: number
  defaultValue?: number
  onChange?: (value: number) => void
}

type RadioGroupStateProps = Overwrite<Parameters<typeof useRadioGroupState>[0], OverideProps>
type RadioGroupProps = Overwrite<Parameters<typeof useRadioGroup>[0], OverideProps>

type AriaProps = Overwrite<RadioGroupStateProps, RadioGroupProps>

type AmountOtherInputProps = Pick<
  ComponentProps<typeof NumberInput>,
  'minValue' | 'maxValue' | 'onBlur' | 'name' | 'autoFocus' | 'invalid'
>

type AmountSelectorProps = {
  options?: {
    label?: string
    value: number
  }[]

  /** Shows the other amount text field
   * @default false
   */
  otherField?: ReactNode | boolean

  disabled?: Parameters<typeof useRadioGroup>[0]['isDisabled']
  required?: Parameters<typeof useRadioGroup>[0]['isRequired']

  currency?: string

  /** @private only used internally */
  onBlur?: Parameters<typeof Number>[0]['onBlur']
} & AriaProps &
  ValidationMessageProps &
  FieldLabelAndDescriptionProps &
  AmountOtherInputProps

type AmountSelectorState = RadioGroupState & {
  otherValue?: number
  setOtherValue?: (value: number) => void
  currency?: string
  optionsCount?: number
}

export function AmountSelector({
  currency = 'CAD',
  helpText,
  invalid,
  errorMessage,
  minValue,
  maxValue,
  labelProps,
  ...props
}: AmountSelectorProps) {
  const { options = [], label, description, otherField: OtherField = null } = props
  const apiProps: Partial<Parameters<typeof useRadioGroup>[0]> = {
    isDisabled: props.disabled,
    isRequired: props.required,
    orientation: 'horizontal',
  }

  const state: AmountSelectorState = useRadioGroupState({
    ...(props as unknown as Parameters<typeof useRadioGroupState>[0]),
  })
  const { radioGroupProps, labelProps: radioLabelProps } = useRadioGroup(
    {
      ...(props as unknown as Parameters<typeof useRadioGroup>[0]),
      ...apiProps,
    },
    state,
  )

  const [otherValue, setOtherValue] = useState<number>()

  state.setOtherValue = setOtherValue
  state.otherValue = otherValue
  state.currency = currency
  state.optionsCount = options?.length ?? 0

  useEffect(() => {
    if (state.optionsCount === 0 && props.value) {
      state.setOtherValue(props.value)
    } else if (Object.values(options).some(option => option.value === props.value)) {
      state.setOtherValue(undefined)
    }
  }, [state.optionsCount])

  useEffect(() => {
    const value = Number(state.otherValue || state.selectedValue)

    if (isNaN(value)) {
      props?.onChange?.(undefined)
    } else {
      props.onChange?.(value)
    }
  }, [state.selectedValue, state.otherValue])

  useEffect(() => {
    if (!!props.otherField && props.value && !props.options?.find(option => option.value === props.value)) {
      state.setOtherValue(props.value)
    }
  }, [])

  return (
    <VStack as="fieldset" {...radioGroupProps} space="small">
      <FieldLabelAndDescription
        label={label}
        labelProps={mergeProps(labelProps, radioLabelProps)}
        description={description}
      />

      <AmountGrid css={{ $$columns: calculateColumns(state.optionsCount) }}>
        <AmountContext.Provider value={state}>
          {options?.map(option => (
            <AmountOption key={option.value} {...option} />
          ))}
          {typeof OtherField === 'boolean' && (
            <AmountOtherField
              inputProps={{
                minValue,
                maxValue,
                onBlur: props.onBlur,
                name: props.name,
                autoFocus: !!state.optionsCount,
              }}
            />
          )}
          {typeof OtherField === 'function' && <OtherField />}
        </AmountContext.Provider>
      </AmountGrid>

      <FieldErrorAndHelpText helpText={helpText} errorMessage={errorMessage} invalid={invalid} />
    </VStack>
  )
}

function AmountOtherInput({
  currencyCode,
  currencySymbol,
  inputProps,
}: {
  currencyCode: string
  currencySymbol: string
  inputProps?: AmountOtherInputProps
}) {
  const state = useContext(AmountContext)

  return (
    <Box css={{ position: 'relative', gridColumn: '-1 / 1' }}>
      <NumberInput
        {...inputProps}
        placeholder={`${currencySymbol} Other`}
        data-testid="otherselector"
        formatOptions={{ style: 'currency', currency: state.currency, currencyDisplay: 'narrowSymbol' }}
        onChange={value => {
          state.setOtherValue(value)
          state.setSelectedValue('')
        }}
        value={state.otherValue}
        focused={!!state.otherValue || state.lastFocusedValue === 'other'}
      />
      <CurrencyLocale size="caption1">{currencyCode}</CurrencyLocale>
    </Box>
  )
}

function AmountOtherField({ inputProps }: { inputProps?: AmountOtherInputProps }) {
  const state = useContext(AmountContext)
  const currency = state.currency

  const { formatNumberToParts } = useIntl()

  const currencyCode = useMemo(
    () =>
      formatNumberToParts(10, { style: 'currency', currency, currencyDisplay: 'code' }).find(
        part => part.type === 'currency',
      )?.value,
    [currency],
  )

  const currencySymbol = useMemo(
    () =>
      formatNumberToParts(10, { style: 'currency', currency, currencyDisplay: 'narrowSymbol' }).find(
        part => part.type === 'currency',
      )?.value,
    [currency],
  )

  const OtherFieldButton = () => (
    <Box
      css={{ gridColumn: '-1 / 1', userSelect: 'none' }}
      onClick={() => {
        state.setSelectedValue('')
        state.setLastFocusedValue('other')
      }}
      data-testid="otherbutton"
    >
      <AmountChip>
        <HStack justify="between" css={{ width: '100%', paddingInline: 'calc($1 - 3px)' }}>
          <HStack space="extraSmall">
            <span>{currencySymbol}</span>
            <span>Other</span>
          </HStack>
          <span>{currencyCode}</span>
        </HStack>
      </AmountChip>
    </Box>
  )

  return state.otherValue || state.lastFocusedValue === 'other' || state.optionsCount === 0 ? (
    <AmountOtherInput currencyCode={currencyCode} currencySymbol={currencySymbol} inputProps={inputProps} />
  ) : (
    <OtherFieldButton />
  )
}

function AmountOption(props: AmountSelectorProps['options'][0] & Pick<AmountSelectorProps, 'currency'>) {
  const state = useContext(AmountContext)
  const ref = useRef(null)
  const { inputProps } = useRadio(
    { ...props, children: props.label, 'aria-label': props.value.toString(), value: props.value.toString() },
    state,
    ref,
  )
  const { isFocusVisible, focusProps, isFocused } = useFocusRing()

  const isSelected = !state.otherValue && Number(state.selectedValue) === props.value

  return (
    <AmountChip isFocused={isFocused || isFocusVisible} isSelected={isSelected}>
      <VisuallyHidden>
        <input {...inputProps} {...focusProps} onClick={() => state.setOtherValue(NaN)} ref={ref} />
      </VisuallyHidden>
      <span>
        {props.label ??
          new Intl.NumberFormat(navigator.language, {
            style: 'currency',
            currency: state.currency,
            currencyDisplay: 'narrowSymbol',
            maximumFractionDigits: props.value % 1 === 0 ? 0 : undefined,
          }).format(props.value)}
      </span>
    </AmountChip>
  )
}
