import React, { useMemo } from 'react'
import {
  Accessor,
  actions,
  ActionType,
  Column,
  ColumnInstance,
  functionalUpdate,
  Hooks,
  TableInstance,
  TableOptions,
  TableState,
} from 'react-table'
import { UseColumnSummaryState } from '../../typings/react-table-config'
import * as aggregations from './aggregations'

function getFirstDefined(...args: unknown[]) {
  for (let i = 0; i < args.length; i += 1) {
    if (typeof args[i] !== 'undefined') {
      return args[i]
    }
  }
}

// Actions
actions.resetColumnSummary = 'resetColumnSummary'
actions.setColumnSummary = 'setColumnSummary'

export const useColumnSummary = (hooks: Hooks) => {
  hooks.useOptions.push(useOptions)
  hooks.stateReducers.push(reducer)
  hooks.useInstance.push(useInstance)
}

useColumnSummary.pluginName = 'useColumnSummary'

const defaultUserColumnSummaryFunctions = {}
// const defaultColumnSummaryFunction = 'count'

function DefaultColumnSummaryComponent({ column: { columnSummary } }) {
  return columnSummary.value
}

function useOptions(options: TableOptions<object>) {
  const { columns = [] } = options
  const columnsWithDefaultSummary = useMemo(
    () =>
      columns.map((column: Column) => {
        if (!column.ColumnSummary && !!column.columnSummaryFunction) {
          return {
            ...column,
            ColumnSummary: DefaultColumnSummaryComponent,
          }
        }

        return column
      }),
    [columns],
  )

  return {
    ...options,
    columns: columnsWithDefaultSummary,
  }
}

function reducer(state: TableState, action: ActionType, _: TableState, instance: TableInstance) {
  if (action.type === actions.init) {
    return {
      columnSummary: {},
      ...state,
    }
  }

  if (action.type === actions.resetColumnSummary) {
    return {
      ...state,
      columnSummary: instance.initialState.columnSummary || {},
    }
  }

  if (action.type === actions.setColumnSummary) {
    return {
      ...state,
      columnSummary: functionalUpdate(action.columnSummary, state.columnSummary),
    }
  }
}

function useInstance(instance: TableInstance) {
  const {
    state: { columnSummary },
    allColumns,
    rows,
    columnSummaryFunctions: userColumnSummaryFunctions = defaultUserColumnSummaryFunctions,
    dispatch,
    disableColumnSummary,
  } = instance

  const setColumnSummary = React.useCallback(
    columnSummary => {
      return dispatch({ type: actions.setColumnSummary, columnSummary })
    },
    [dispatch],
  )

  React.useMemo(() => {
    allColumns.forEach(column => {
      const { id, accessor, columnSummaryFunction } = column as ColumnInstance<Record<string, any>> & {
        accessor: Accessor<any>
      }

      // Determine if a column has summary
      column.hasColumnSummary = accessor
        ? !!getFirstDefined(
            column.disableColumnSummary === true ? false : undefined,
            disableColumnSummary === true ? false : undefined,
            !!column.columnSummaryFunction,
            true,
          )
        : false

      const columnSummaryType = columnSummary[id] || columnSummaryFunction

      const summaryFunction =
        typeof columnSummaryType === 'function'
          ? columnSummaryType
          : userColumnSummaryFunctions[columnSummaryType] || aggregations[columnSummaryType]

      let columnSummaryValue = null

      if (summaryFunction) {
        columnSummaryValue = summaryFunction(rows.map(row => row.original[column.id]))
      } else if (columnSummaryType) {
        console.info({ column })
        throw new Error(`React Table: Invalid columnSummary function provided for column listed above`)
      }

      // TODO: Add support for summary formatting
      // if (columnSummaryValue && !!column.columnSummaryFormatter) {
      //   columnSummaryValue = column.columnSummaryFormatter(columnSummaryValue)
      // }

      column.columnSummary = {
        type: columnSummaryType,
        value: columnSummaryValue,
      }

      column.ColumnSummary = column.ColumnSummary || DefaultColumnSummaryComponent

      column.setColumnSummary = (data: UseColumnSummaryState<Record<string, unknown>>['columnSummary']) => {
        setColumnSummary({ ...columnSummary, [id]: data })
      }
    })
  }, [allColumns, setColumnSummary, columnSummary, userColumnSummaryFunctions, rows, disableColumnSummary])

  Object.assign(instance, {
    setColumnSummary,
  })
}
