import { motion, useReducedMotion } from 'framer-motion'
import { ComponentProps, forwardRef, useContext, useRef } from 'react'
import { useFocusRing, useRadio } from 'react-aria'
import { mergeRefs } from 'react-laag'
import { Box } from '../layout/Box'
import { styled } from '../theme/default'
import { Label } from '../typography'
import { RadioContext } from './RadioContext'
import { RadioFactory } from './RadioFactory'

const RadioLabel = styled('label', {
  display: 'inline-flex',
  alignItems: 'center',
  verticalAlign: 'middle',
  cursor: 'pointer',
  position: 'relative',
  userSelect: 'none',

  variants: {
    disabled: {
      true: {
        cursor: 'not-allowed',
      },
    },
  },
})

const RadioCircle = styled(motion.rect, {
  fill: 'currentColor',
  size: 17,
})

const RadioWrapper = styled(motion.svg, {
  width: 25,
  height: 25,
})

const radioCircleVariants = {
  hidden: {
    scale: 0.5,
    originX: '50%',
    originY: '50%',
  },
  visible: {
    scale: 1,
    originX: '50%',
    originY: '50%',
  },
}

type AriaRadioProps = Parameters<typeof useRadio>[0]
export type RadioProps = AriaRadioProps &
  Pick<ComponentProps<typeof RadioFactory>, 'css' | 'size'> & {
    /**
     * When true, the aria-invalid attribute is true.
     * Used for accessibility.
     */
    invalid?: string | boolean

    disabled?: AriaRadioProps['isDisabled']

    value: string | number
  }

interface RadioPropsWithLabel extends Omit<RadioProps, 'children' | 'label'> {
  label: string
  children?: never
}
interface RadioPropsWithChildren extends Omit<RadioProps, 'children' | 'label'> {
  children: string
  label?: never
}

/**
 * Radios must be used in a `RadioGroup` to provide a group of radio buttons.
 * @since 1.12.0
 * @author Alyssa Gutierrez
 * @author Evan Hennessy
 *
 * ```tsx
 * // Always pass a child as a label
 * <Radio>Option 1</Radio>
 *
 * // Or use the label prop
 * <Radio label="Option 1">
 * ```
 */
export const Radio = forwardRef<HTMLInputElement, RadioPropsWithLabel | RadioPropsWithChildren>(
  ({ css, disabled, ...props }, forwardedRef) => {
    const { children, label } = props
    const propsAliases = { isDisabled: disabled }
    const { size, ...state } = useContext(RadioContext)
    const ref = useRef<HTMLInputElement>(null)
    const { inputProps } = useRadio({ ...props, ...propsAliases }, state, ref)
    const { focusProps } = useFocusRing()
    const shouldReduceMotion = useReducedMotion()

    return (
      <RadioLabel disabled={disabled}>
        <RadioFactory
          size={size as any}
          disabled={disabled}
          {...inputProps}
          {...focusProps}
          ref={mergeRefs(ref, forwardedRef)}
        />

        <Box
          css={{
            boxSizing: 'border-box',
            display: 'flex',
            flex: 'none',
            alignItems: 'center',
            justifyContent: 'center',
            size: '$2',
            pointerEvents: 'none',
          }}
          aria-hidden="true"
        >
          <RadioWrapper>
            <RadioCircle
              rx={100}
              x={4}
              y={4}
              variants={radioCircleVariants}
              animate={state.selectedValue === props.value ? 'visible' : 'hidden'}
              initial={{ transformOrigin: '50% 50%' }}
              transition={{ duration: shouldReduceMotion ? 0.01 : 0.2 }}
            />
          </RadioWrapper>
        </Box>
        <Label as="span" disabled={disabled} size={size as any}>
          {label || children}
        </Label>
      </RadioLabel>
    )
  },
)
