import { ComponentProps } from 'react'
import { mergeProps, useLabel } from 'react-aria'
import { FieldErrorAndHelpText, FieldLabelAndDescription, FieldLabelAndDescriptionProps, FieldWrapper } from '../form'
import type { ValidationMessageProps } from '../form/helpers/types'
import { TextInputProps } from '../textinput'
import { Select } from './Select'

type SelectFieldProps = TextInputProps &
  ComponentProps<typeof Select> &
  FieldLabelAndDescriptionProps &
  ValidationMessageProps & {}

export function SelectField(props: SelectFieldProps) {
  const { labelProps: ariaLabelProps, fieldProps } = useLabel(props)
  const { label, description, labelProps, invalid = null, validationMessage, helpText } = props

  return (
    <FieldWrapper css={props.css}>
      <FieldLabelAndDescription
        label={label}
        labelProps={mergeProps(ariaLabelProps, labelProps)}
        description={description}
      />
      <Select {...mergeProps(fieldProps, props)} />
      <FieldErrorAndHelpText helpText={helpText} errorMessage={validationMessage} invalid={invalid} />
    </FieldWrapper>
  )
}

SelectField.defaultProps = {
  disabled: false,
  invalid: false,
  size: 'regular',
}
