import { ComponentProps } from 'react'
import { mergeProps } from 'react-aria'
import { SegmentedControl } from '../segmentedControl'
import { useFormikHelper } from './useFormikHelper'

/**
 * A helper for using `SegmentedControl` with formik forms.
 * Automatically sets errors and assigns `onChange` and `value` based on the field name
 *
 * @throws if not used in a Formik Context
 */

export function FormikSegementedControl(props: ComponentProps<typeof SegmentedControl>) {
  const [field, _, helpers] = useFormikHelper(props)

  return (
    <SegmentedControl
      {...mergeProps(field, props)}
      onChange={value => {
        props?.onChange?.(value)
        helpers.setValue(value)
      }}
    />
  )
}
