import { useFormikContext } from 'formik'
import { motion, useAnimation, Variants } from 'framer-motion'
import { ComponentProps, ReactNode, useEffect, useState } from 'react'
import { Button } from '../button'
import { CheckCircleBoldIcon } from '../icon'
import { Intent } from '../theme/default'
import { useFocusContext } from './focusManager'

const buttonAnimationVariants: Variants = {
  shake: {
    x: [-10, 10],
    transition: {
      repeat: 2,
      duration: 0.15,
      type: 'tween',
    },
  },
  idle: {
    x: 0,
  },
}

/**
 * A helper for submitting with formik forms.
 * @author Evan Hennessy
 * @since 1.39.0
 */
export function FormikSubmitButton(props: ComponentProps<typeof Button>) {
  let { isSubmitting, errors, submitForm } = useFormikContext()
  const { focus, handleBlur, handleFocus } = useFocusContext() || {}
  const [state, setState] = useState<'idle' | 'loading' | 'success' | 'error'>('idle')
  const controls = useAnimation()

  useEffect(() => {
    let timer: NodeJS.Timeout
    const hasErrors = Object.keys(errors).length > 0

    if (state === 'idle' && isSubmitting) {
      setState('loading')
    } else if (state === 'loading' && !isSubmitting && !hasErrors) {
      setState('success')
      timer = setTimeout(() => setState('idle'), 5000)
    } else if (state === 'loading' && !isSubmitting && hasErrors) {
      setState('error')
      controls.start('shake').then(() => {
        controls.stop()
        controls.start('idle')
      })
      timer = setTimeout(() => setState('idle'), 1500)
    }

    return () => {
      clearTimeout(timer)
    }
  }, [isSubmitting, errors])

  const buttonDictionary: Record<typeof state, { label?: string | boolean; intent?: Intent; icon?: ReactNode }> = {
    idle: { label: (props?.children as string) || 'Submit', icon: props?.iconBefore },
    loading: { label: props?.loadingLabel || false },
    success: { label: 'Success', intent: 'success', icon: CheckCircleBoldIcon },
    error: { label: (props?.children as string) || 'Submit', intent: 'danger' },
  }

  return (
    <Button
      as={motion.button}
      variants={buttonAnimationVariants}
      animate={controls}
      children={buttonDictionary[state].label}
      intent={buttonDictionary[state].intent}
      iconBefore={buttonDictionary[state].icon}
      type="button"
      name="submit"
      loading={state === 'loading'}
      loadingLabel={props.loadingLabel || 'Submit'}
      onClick={submitForm}
      {...{ focused: focus === props?.name, onBlur: handleBlur, onFocus: handleFocus }}
      {...(props as any)}
    />
  )
}
