import { VariantProps } from '@stitches/react'
import { ComponentProps, forwardRef, ReactNode } from 'react'
import { styled } from '../theme/default'
import { fontPxToNum, lineHeightPxToNum } from './utils'

/**
 * Use this for UI Text
 * @description It has no margins, line-height, leading or trailing space around the text.
 * If you don't want that then use `Paragraph` instead
 *
 * @note Don't override the `fontSize` in css. Instead use the `size` prop
 * @example ```jsx
 * <Text size="caption2">My Text</Text>
 * ```
 *
 * @author Evan Hennessy
 */
export const TextFactory = styled('span', {
  margin: 0,
  fontSize: '$root',
  fontFamily: '$sans',
  fontWeight: '$regular',

  capsize: {
    metrics: 'Modern Era',
    lineGap: 0,
  },

  variants: {
    size: {
      extraSmall: {
        capsize: {
          fontSize: fontPxToNum(1),
          lineGap: lineHeightPxToNum(1),
        },
      },
      footnote: {
        capsize: {
          fontSize: fontPxToNum(2),
          lineGap: lineHeightPxToNum(1),
        },
      },
      caption2: {
        capsize: {
          fontSize: fontPxToNum(2),
          lineGap: lineHeightPxToNum(1),
        },
      },
      caption1: {
        capsize: {
          fontSize: fontPxToNum(3),
          lineGap: lineHeightPxToNum(1),
        },
      },
      body: {
        capsize: {
          fontSize: fontPxToNum(4),
          lineGap: lineHeightPxToNum(1),
        },
      },
      large: {
        capsize: {
          fontSize: fontPxToNum(5),
          lineGap: lineHeightPxToNum(1),
        },
      },
    },

    lineClamp: {
      true: {
        '.truncate': {
          display: 'block',
          overflow: 'hidden',
          textOverflow: 'ellipsis',
          whiteSpace: 'nowrap',
        },
      },
    },
  },

  defaultVariants: {
    size: 'body',
  },
})

export const Text = forwardRef<HTMLSpanElement, ComponentProps<typeof TextFactory>>(
  ({ title, ...props }, forwardedRef) => {
    const children: ReactNode = props?.lineClamp ? <span className="truncate">{props.children}</span> : props.children

    if (props.lineClamp) {
      title ||= props.children as string
    }

    return (
      <TextFactory ref={forwardedRef} title={title} {...props}>
        {children}
      </TextFactory>
    )
  },
) as typeof TextFactory

Text.displayName = 'Text'
export type TextSize = VariantProps<typeof TextFactory>['size']

Text.toString = TextFactory.toString
